import React, { useEffect, Fragment, useState } from 'react';
import { bindActionCreators, Dispatch } from 'redux';
import { connect, RootStateOrAny } from 'react-redux';
import { Link, useHistory } from 'react-router-dom';
import moment from 'moment';

import { Button, Checkbox, Input, Select, Card, TimePicker, Form, Typography, DatePicker, Upload } from 'antd';
import { InboxOutlined, SearchOutlined } from '@ant-design/icons';
import notifier from 'utils/notifier';

import { initTickets, editTickets, postTickets, getTicketsNo } from 'store/memberTickets/actions';
import { getListIpas } from 'store/ipas/actions';
import { getListCallerCodes } from 'store/callerCodes/actions';
import { getListQueues } from 'store/queues/actions';
import { getListCategories } from 'store/categories/actions';
import { getListHPCodes } from 'store/hpCodes/actions';
import { getMySelf } from 'store/auth/actions';

import api from 'constants/api';
import { TYPE_LIST, CALLER_TYPE_LIST } from 'constants/mockData/tickets';

import FloatLabel from 'components/Common/FloatLabel';
import ProviderSearch from 'components/Provider/ProviderSearch';
import MemberSearch from 'components/Member/MemberSearch';
import RichTextEditor from '../Create/RichTextEditor';

import './index.scss';

const { Option } = Select;
const { Title } = Typography;
const { Dragger } = Upload;

function getBase64(file: any) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => resolve(reader.result);
    reader.onerror = error => reject(error);
  });
}

const dummyRequest = ({ file, onSuccess }: any) => {
  setTimeout(() => {
    onSuccess('ok');
  }, 0);
};

interface TicketsCreateProps2 {
  edited: boolean;
  data: any;
  editTickets: Function;
  postTickets: Function;
  initTickets: Function;
  getListIpas: Function;
  ipaItems: any;
  getListCallerCodes: Function;
  callerCodeItems: any;
  getTicketsNo: Function;
  ticket_ref_no: any;
  getListQueues: Function;
  queueItems: any;
  getListCategories: Function;
  categoryItems: any;
  getListHPCodes: Function;
  hpCodeItems: any;
  is_admin: any;
  is_manager: any;
  isFetchingMe: boolean;
  user: any;
  getMySelf: Function;
}

const infoPopHandle = React.createRef<HTMLDivElement>();
const memberPopHandle = React.createRef<HTMLDivElement>();

const TicketsCreatePage2 = (
  {
    edited, data, editTickets, postTickets, initTickets,
    getListIpas, ipaItems, getListCallerCodes, callerCodeItems, getTicketsNo, ticket_ref_no, getListQueues, queueItems,
    getListCategories, categoryItems, getListHPCodes, hpCodeItems,
    is_admin, is_manager,
    isFetchingMe, user, getMySelf,
  }: TicketsCreateProps2,
) => {
  const [isProviderSearchOpen, setIsProviderSearchOpen] = useState(false);
  const [isMemberSearchOpen, setIsMemberSearchOpen] = useState(false);
  const history = useHistory();
  const [filteredCategories, setFilteredCategories] = useState([]);
  const [infoPop, setInfoPop] = useState(false);
  const [memberPop, setMemberPop] = useState(false);
  const [attachFile, setAttachFile] = useState<any>('');

  useEffect(() => {
    getMySelf();
  }, [getMySelf]);

  useEffect(() => {
    if (!isFetchingMe && user) {
      editTickets({
        USER: user,
      });
    }
  }, [isFetchingMe, user]);

  useEffect(() => {
    initTickets();
    getListIpas();
    getListCallerCodes();
    getTicketsNo()
      .then((res: any) => {
        editTickets({ TICKET_REF_NO: res.ticket_ref_no });
      });
    getListQueues();
    getListCategories()
      .then((res: any) => {
        setFilteredCategories(res);
      });
    getListHPCodes();
  }, [initTickets, getListIpas, getListCallerCodes, getTicketsNo, getListQueues, getListCategories, getListHPCodes, setFilteredCategories, editTickets]);

  const onClickInfoPopHandler = (e: any) => {
    if (infoPop && infoPopHandle.current && !infoPopHandle.current.contains(e.target)) {
      setInfoPop(false);
    }
  };

  const onClickMemberPopHandler = (e: any) => {
    if (memberPop && memberPopHandle.current && !memberPopHandle.current.contains(e.target)) {
      setMemberPop(false);
    }
  };

  useEffect(() => {
    window.addEventListener('click', onClickInfoPopHandler);
    window.addEventListener('click', onClickMemberPopHandler);
    return () => {
      window.removeEventListener('click', onClickInfoPopHandler);
      window.removeEventListener('click', onClickMemberPopHandler);
    };
  });

  const handleChangeTicketsCreate = (key: string, val: any) => {
    editTickets({ [key]: val });
  };

  const handleChangeTicketsCreateBulk = (data: any) => {
    editTickets(data);
  };

  const handleSelectProvider = (data: any) => {
    // Get details of provider, set to form
    if (data?.id) {
      api.providers.get(data.id)
        .then(res => {
          handleChangeTicketsCreateBulk({
            PROVIDER_ID: res.providerRefId || '',
            PROVIDER_PHONE: res.workPhone || '',
            PROVIDER: `${res.firstName || ''} ${res.mi || ''} ${res.lastName || ''}`,
            PROVIDER_FAX: res.fax || '',

            providerFirstName: res.firstName || '',
            providerLastName: res.lastName || '',

            providerVendorId: res.vendorId || '',
            providerAccount: res.subAccount || '',
          });
        })
        .catch(err => {
          notifier.error('Can not get details of provider!');
        });
    }

    setIsProviderSearchOpen(false);
  };

  const handleSelectMember = (data: any) => {
    // Get details of member, set to form
    if (data?.id) {
      api.members.get(data.id)
        .then(res => {
          handleChangeTicketsCreateBulk({
            MBR_ID: res?.memberRefId || '',
            MBR_FIRSTNAME: res.firstName || '',
            MBR_LASTNAME: res.lastName || '',
            MBR_PHONE: res.phone || '',
            MBR_EXT: res.extension || '',
            MBR_DOB: res.birth || '',
            MBR_MI: res.middleInitial || '',
            MBR_IPA: data.companyId,
            MBR_HPCODE: res.hpCode || '',
          });
        })
        .catch(err => {
          notifier.error('Can not get details of member!');
        });
    }

    setIsMemberSearchOpen(false);
  };

  const onRoutedQueue = (val: any) => {
    handleChangeTicketsCreate('routeTo', val);

    let temp = categoryItems.filter((c: any) => c.QUEUES.includes(val));
    setFilteredCategories(temp);

    if (!temp.map((c: any) => c.CATEGORY_ID).includes(data.CATEGORY.CATEGORY_ID)) {
      let initCategory = {
        CATEGORY_ID: null,
        TAT: null,
      };

      handleChangeTicketsCreate('CATEGORY', initCategory);
    }
  };

  const onAttach = async (info: any) => {
    if (info?.file) {
      setAttachFile(info.file);
    }
  };

  const onSubmit = () => {
    let item: any = {
      itemid: null,
      itemcontent: data.ITEM_CONTENT || '',
      ipa: data.IPA || null,
      itemtype: data.ITEM_TYPE || null,
      userid: data.USER?.USER_ID || null,
      status: data.STATUS?.STATUS_ID || null,
      ticketid: data.TICKET_REF_NO || null,
      itemtitle: data.ITEM_TITLE || null,
      mbrfirstname: data.MBR_FIRSTNAME || null,
      mbrlastname: data.MBR_LASTNAME || null,
      mbrid: data.MBR_ID || null,
      mbrphone: data.MBR_PHONE || null,
      mbrext: data.MBR_EXT || null,
      mbrdob: data.MBR_DOB || null,
      mbrmi: data.MBR_MI || null,
      mbrhpcode: data.MBR_HPCODE || null,
      mbripa: data.MBR_IPA || null,
      provider: data.PROVIDER || null,
      providerphone: data.PROVIDER_PHONE || null,
      providerext: data.PROVIDER_EXT || null,
      providerfax: data.PROVIDER_FAX || null,
      providerid: data.PROVIDER_ID || null,
      authnbr: data.AUTH_NBR || null,
      notes: data.NOTES || null,
      priority: data.PRIORITY.PRIORITY_ID || null,
      category: data.CATEGORY || null,
      urgent: data.URGENT || null,
      callercode: data?.CALLER_CODE?.CC_ID || null,
      healthplan: data.HEALTH_PLAN || null,
      hpphone: data.HP_PHONE || null,
      hpext: data.HP_EXT || null,
      hpfax: data.HP_FAX || null,
      bucketid: data.BUCKET_ID || null,
      calltime: data.CALL_TIME || null,
      callername: data.CALLER_NAME || null,
      callbacknbr: data.CALLBACK_NBR || null,
      callbackext: data.CALLBACK_EXT || null,
      callerfax: data.CALLER_FAX || null,
      userassigned: data.USER?.USER_ID || null,
    };

    const itemJson = JSON.stringify(item);

    const params = new FormData();
    if (attachFile) {
      params.append('attach_file', attachFile, attachFile.name);
    }

    params.append('item', itemJson);
    params.append('queue', data.routeTo);
    params.append('parent', data.parent);

    postTickets(params)
      .then((res: any) => {
        if (res?.item) {
          history.push(`/member-tickets/${res.item}`);
        } else {
          notifier.error("Creating failed!");
        }
      });
  };

  return (
    <div className="page ticket-create2-page">
      <ProviderSearch
        isModal
        isModalVisible={isProviderSearchOpen}
        onCloseModal={() => setIsProviderSearchOpen(false)}
        onProviderSelect={handleSelectProvider}
      />

      <MemberSearch
        isModalVisible={isMemberSearchOpen}
        onCloseModal={() => {setIsMemberSearchOpen(false);}}
        onMemberSelect={handleSelectMember}
      />

      <div className="ticket-header row">
        <Title level={4}>Queue Item</Title>
      </div>

      <div className="ticket-content">
        <div className="row">
          <div className="content-layer py-2">
            <div className="content-grid">
              <div className="px-3">
                <div className="ticket-item">
                  <span className="ticket-label">TICKET REF NO:</span>
                  <span>{data.TICKET_REF_NO}</span>

                  <span className="ticket-label">Subject:</span>
                  <Input value={data.ITEM_TITLE} onChange={e => handleChangeTicketsCreate('ITEM_TITLE', e.target.value)} />

                  <span className="ticket-label">IPA:</span>
                  <Select
                    value={data.IPA}
                    onChange={(value) => handleChangeTicketsCreate('IPA', value)}
                    className="w-100"
                  >
                    {ipaItems?.map((ipa: any) => (
                      <Option key={ipa.code} value={ipa.code}>{ipa.code + ' | ' + ipa.description}</Option>
                    ))}
                  </Select>

                  <span className="ticket-label">TYPE:</span>
                  <Select
                    value={data.ITEM_TYPE}
                    onChange={(value) => handleChangeTicketsCreate('ITEM_TYPE', value)}
                    className="w-100"
                  >
                    {TYPE_LIST.map(i => (
                      <Option key={i.key} value={i.key}>{i.label}</Option>
                    ))}
                  </Select>

                  <span className="ticket-label">CALLER CODE:</span>
                  <Select
                    value={data.CALLER_CODE?.CC_ID}
                    onChange={(v) => {
                      const selected = callerCodeItems.find((i: any) => i.CC_ID === v);
                      handleChangeTicketsCreate('CALLER_CODE', selected);
                    }}
                    className="w-100"
                  >
                    {callerCodeItems.map((code: any) => (
                      <Option key={code.CC_ID} value={code.CC_ID}>{code.CALLER_CODE}</Option>
                    ))}
                  </Select>
                </div>
              </div>

              <div className="px-3">
                <div className="ticket-item">
                  <span className="ticket-label">CALL TIME:</span>
                  <TimePicker
                    use12Hours format="HH:mm:ss"
                    suffixIcon={<i className="far fa-clock" />} placeholder=""
                    value={data.CALL_TIME && moment(data.CALL_TIME, 'HH:mm:ss')}
                    onChange={(time, timeString) => handleChangeTicketsCreate('CALL_TIME', timeString)}
                  />

                  <span className="ticket-label">CALLER NAME:</span>
                  <span className="d-flex w-100">
                    <Input
                      value={data.CALLER_NAME}
                      onChange={e => handleChangeTicketsCreate('CALLER_NAME', e.target.value)}
                      style={{width: '45%'}}
                    />
                    <Select
                      value={data.CALLER_CLASSIFICATION}
                      onChange={(value) => handleChangeTicketsCreate('CALLER_CLASSIFICATION', value)}
                      style={{width: '50%', marginLeft: '5%'}}
                    >
                      {CALLER_TYPE_LIST.map(i => (
                        <Option key={i.key} value={i.key}>{i.label}</Option>
                      ))}
                    </Select>
                  </span>

                  <span className="ticket-label">CALLBACK NUMBER:</span>
                  <span className="d-flex w-100">
                    <Input
                      value={data.CALLBACK_NBR}
                      onChange={e => handleChangeTicketsCreate('CALLBACK_NBR', e.target.value)}
                      style={{width: '50%'}}
                    />
                    <Input
                      placeholder="ext"
                      value={data.CALLBACK_EXT}
                      onChange={e => handleChangeTicketsCreate('CALLBACK_EXT', e.target.value)}
                      style={{width: '45%', marginLeft: '5%'}}
                    />
                  </span>

                  <span className="ticket-label">CALLER FAX:</span>
                  <Input value={data.CALLER_FAX} onChange={e => handleChangeTicketsCreate('CALLER_FAX', e.target.value)} />

                  <span className="ticket-label">CALLER EMAIL:</span>
                  <Input value={data.CALLER_EMAIL} onChange={e => handleChangeTicketsCreate('CALLER_EMAIL', e.target.value)} />
                </div>
              </div>

              <div className="px-3">
                <div className="ticket-item">
                  <span className="ticket-label">ROUTE TO:</span>
                  <Select
                    value={data.routeTo}
                    onChange={(value) => onRoutedQueue(value)}
                    className="w-100"
                  >
                    {queueItems.map((queue: any) => (
                      <Option key={queue.QUEUE_ID} value={queue.QUEUE_ID}>{queue.DEPT_NAME + ' | ' + queue.NAME}</Option>
                    ))}
                  </Select>

                  <span className="ticket-label">CATEGORY:</span>
                  <Select
                    value={typeof data.CATEGORY !== 'object' ? data.CATEGORY : ''}
                    onChange={(value) => handleChangeTicketsCreate('CATEGORY', value)}
                    className="w-100"
                  >
                    {filteredCategories.map((category: any) => (
                      <Option key={category.CATEGORY_ID} value={category.CATEGORY_ID}>{category.NAME}</Option>
                    ))}
                  </Select>

                  <span className="ticket-label">TICKET REFERENCE:</span>
                  <Input value={data.MatchedItemInput} onChange={e => handleChangeTicketsCreate('MatchedItemInput', e.target.value)} />
                </div>
              </div>
            </div>
          </div>
        </div>

        <div className="center-layer-wrapper">
          <div className="content-layer center-layer">
            <Card
              size="small" title="Member Info"
              extra={
                <Fragment>
                  <Link to="#" onClick={() => setIsMemberSearchOpen(true)}><SearchOutlined/>SEARCH</Link>
                </Fragment>
              }
              className="ticket-card w-100"
            >
              <div className="ticket-item">
                <span className="ticket-label">Member ID:</span>
                <Input value={data.MBR_ID} disabled />

                <span className="ticket-label">First Name:</span>
                <Input value={data.MBR_FIRSTNAME} disabled/>

                <span className="ticket-label">Last Name:</span>
                <Input value={data.MBR_LASTNAME} disabled/>

                <span className="ticket-label">STATUS:</span>
                <Checkbox checked={data.memberStatus} disabled>
                  {data.memberStatus ? 'Active' : 'In Active'}
                </Checkbox>
              </div>

              <div className="add-fields">
                <div className="add-fields-content">
                  <div className="add-fields-header">
                    <Title level={5}>Additional Fields</Title>
                    <Link to="#" onClick={()=> setInfoPop(true)}>Why are some fields blocked?</Link>
                    {infoPop &&
                    <div className="pop-up" onClick={()=> setInfoPop(false)} ref={infoPopHandle}>
                      Blocked fields are not search criteria, and are therefore read-only.
                      <br/>
                      If you are requesting a change to member/provider record, please submit the request in the body of this ticket.
                    </div>
                    }
                  </div>

                  <div className="add-fields-body">
                    <div className="ticket-item grid-1-3">
                      <span className="ticket-label">PHONE:</span>
                      <span className="d-flex w-100">
                        <Input
                          value={data.MBR_PHONE}
                          onChange={e => handleChangeTicketsCreate('MBR_PHONE', e.target.value)}
                          style={{width: '50%'}}
                        />
                        <Input
                          placeholder="ext"
                          value={data.MBR_EXT}
                          onChange={e => handleChangeTicketsCreate('MBR_EXT', e.target.value)}
                          style={{width: '45%', marginLeft: '5%'}}
                        />
                      </span>

                      <span className="ticket-label">DATE OF BIRTH:</span>
                      <DatePicker
                        format="MM/DD/YYYY" suffixIcon={<i className="far fa-calendar-alt" />} placeholder=""
                        value={data.MBR_DOB && moment(data.MBR_DOB)}
                        onChange={(date, dateString) => handleChangeTicketsCreate('MBR_DOB', dateString)}
                      />

                      <span className="ticket-label">IPA:</span>
                      <Input value={data.IPA} disabled />

                      <span className="ticket-label">HPCODE:</span>
                      <Select
                        value={data.MBR_HPCODE}
                        onChange={(value) => handleChangeTicketsCreate('MBR_HPCODE', value)}
                        className="w-100"
                      >
                        {hpCodeItems?.map((hp: any) => (
                          <Option key={hp.code} value={hp.code}>{hp.code + ' | ' + hp.description}</Option>
                        ))}
                      </Select>

                      <span className="ticket-label">OPT:</span>
                      <Input value={data.opt} onChange={e => handleChangeTicketsCreate('opt', e.target.value)} />
                    </div>
                  </div>
                </div>
              </div>
            </Card>
          </div>
        </div>

        <div className="center-layer-wrapper">
          <div className="content-layer center-layer">
            <Card
              size="small" title="MemberShip"
              extra={
                <Fragment>
                  <Link to="#" onClick={() => setIsProviderSearchOpen(true)}><SearchOutlined/>SEARCH</Link>
                </Fragment>
              }
              className="ticket-card w-100"
            >
              <div className="row">
                <div className="col-md-24">
                  <FloatLabel label="AUTH NUMBER" value={data.AUTH_NBR}>
                    <Input value={data.AUTH_NBR} onChange={e => handleChangeTicketsCreate('AUTH_NBR', e.target.value)} />
                  </FloatLabel>
                </div>
              </div>

              <div className="member-ship-provider">
                <label className="provider-label">Provider Info</label>

                <div className="ticket-item">
                  <span className="ticket-label">PROVIDER ID:</span>
                  <Input value={data.PROVIDER_ID} disabled />

                  <span className="ticket-label">FIRST NAME:</span>
                  <Input value={data.providerFirstName} disabled/>

                  <span className="ticket-label">LAST NAME:</span>
                  <Input value={data.providerLastName} disabled/>

                  <span className="ticket-label">STATUS:</span>
                  <Checkbox checked={data.providerStatus} disabled>
                    {data.providerStatus ? 'Active' : 'In Active'}
                  </Checkbox>
                </div>

                <div className="add-fields">
                  <div className="add-fields-content">
                    <div className="add-fields-header">
                      <Title level={5}>Additional Fields</Title>
                      <Link to="#" onClick={()=> setMemberPop(true)}>Why are some fields blocked?</Link>
                      {memberPop &&
                      <div className="pop-up" onClick={()=> setMemberPop(false)} ref={memberPopHandle}>
                        Blocked fields are not search criteria, and are therefore read-only.
                        <br/>
                        If you are requesting a change to member/provider record, please submit the request in the body of this ticket.
                      </div>
                      }
                    </div>

                    <div className="add-fields-body">
                      <div className="ticket-item grid-1-3">
                        <span className="ticket-label">PHONE:</span>
                        <Input value={data.PROVIDER_PHONE} onChange={e => handleChangeTicketsCreate('PROVIDER_PHONE', e.target.value)} />

                        <span className="ticket-label">(add) ALT PHONE:</span>
                        <Input value={data.providerAddPhone} onChange={e => handleChangeTicketsCreate('providerAddPhone', e.target.value)} />

                        <span className="ticket-label">FAX:</span>
                        <Input value={data.PROVIDER_FAX} onChange={e => handleChangeTicketsCreate('PROVIDER_FAX', e.target.value)} />

                        <span className="ticket-label">VENDOR ID:</span>
                        <Input value={data.providerVendorId} onChange={e => handleChangeTicketsCreate('providerVendorId', e.target.value)} />

                        <span className="ticket-label">ACCOUNT:</span>
                        <Input value={data.providerAccount} onChange={e => handleChangeTicketsCreate('providerAccount', e.target.value)} />
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </Card>
          </div>
        </div>

        <div className="row">
          <div className="content-layer">
            <Card size="small" title="MESSAGE" className="ticket-card w-100">
              <RichTextEditor
                content={data.ITEM_CONTENT || ''}
                handleContentChange={(content: any) => handleChangeTicketsCreate('ITEM_CONTENT', content)}
                placeholder="insert text here..."
              />
            </Card>
          </div>
        </div>

        <div className="row">
          <div className="content-layer">
            <Card size="small" title="NOTES" className="ticket-card w-100">
              <RichTextEditor
                content={data.NOTES || ''}
                handleContentChange={(content: any) => handleChangeTicketsCreate('NOTES', content)}
                placeholder="insert text here..."
              />
            </Card>
          </div>
        </div>

        <div className="row">
          <div className="content-layer">
            <Card size="small" title="ATTACHMENT" className="ticket-card w-100">
              {attachFile
                ? <div className="d-flex flex-column">
                  <span>{attachFile?.name}</span>
                  <Button onClick={() => setAttachFile(null)}>Upload Another File</Button>
                </div>
                : <Dragger onChange={(info) => onAttach(info)} multiple={false} customRequest={dummyRequest}>
                  <p className="ant-upload-drag-icon">
                    <InboxOutlined />
                  </p>
                  <p className="ant-upload-text">Click or drag file to this area to upload</p>
                </Dragger>
              }
            </Card>
          </div>
        </div>
      </div>

      <div className="row ticket-footer">
        <div className="col-24 d-flex justify-content-center">
          <Button type="primary" className="ml-3 waves-effect waves-light" onClick={onSubmit}>
            SUBMIT
          </Button>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = (store: RootStateOrAny) => ({
  edited: store.rootReducer.memberTickets.edited,
  data: store.rootReducer.memberTickets.data,
  ticket_ref_no: store.rootReducer.memberTickets.ticket_ref_no,
  ipaItems: store.rootReducer.ipas.items,
  callerCodeItems: store.rootReducer.callerCodes.items,
  queueItems: store.rootReducer.queues.items,
  categoryItems: store.rootReducer.categories.items,
  hpCodeItems: store.rootReducer.hpCodes.items,
  is_admin: store.rootReducer.auth.is_admin,
  is_manager: store.rootReducer.auth.is_manager,
  user: store.rootReducer.auth.user,
  isFetchingMe: store.rootReducer.auth.isFetchingMe,
});

const mapDispatchToProps = (dispatch: Dispatch) => bindActionCreators(
  {
    initTickets,
    editTickets,
    postTickets,
    getListIpas,
    getListCallerCodes,
    getTicketsNo,
    getListQueues,
    getListCategories,
    getListHPCodes,
    getMySelf,
  },
  dispatch,
);

export default connect(mapStateToProps, mapDispatchToProps)(TicketsCreatePage2);
