import types from './types';
import { ThunkDispatch } from 'redux-thunk';
import api from 'constants/api';
import _get from 'lodash/get';

export const initPgoDataManagements = () => {
  return {
    type: types.PGO_DATA_MANAGEMENT_INIT,
  };
};

export const editPgoDataManagements = (data: any) => {
  return {
    type: types.PGO_DATA_MANAGEMENT_EDIT,
    payload: data,
  };
};

export const getListPgoDataManagements = () => (dispatch: ThunkDispatch<any, any, any>, getState: any) => {
  if (getState().rootReducer.pgoDataManagements.isFetchingListDataManagements) { return Promise.reject(); }

  dispatch({ type: types.GET_LIST_PGO_DATA_MANAGEMENT_REQUEST });

  return api.pgoDetails.dataManagements.getList()
    .then((res: any) => {

      dispatch({
        type: types.GET_LIST_PGO_DATA_MANAGEMENT_SUCCESS,
        payload: res,
      });

      return res;
    })
    .catch((err: Error) => {
      const msg = _get(err, 'response.data.msg', 'Get List failed!');
      const errors = _get(err, 'response.data.errors', {});
      const payload = { ...err, msg, errors };

      dispatch({
        type: types.GET_LIST_PGO_DATA_MANAGEMENT_FAILURE,
        payload,
      });

      return Promise.reject(payload);
    });
};

