import React, { useEffect, useState } from 'react';
import { bindActionCreators, Dispatch } from 'redux';
import { connect, RootStateOrAny } from 'react-redux';
import { useHistory } from "react-router-dom";

import { Typography, Collapse, Button, DatePicker, Input, Select, Upload } from 'antd';
import moment from 'moment';
import { SearchOutlined, InboxOutlined } from '@ant-design/icons';

import './index.scss';
import FloatLabel from 'components/Common/FloatLabel';
import { UMHS_PATIENT_REQUEST_SOURCE, UMHS_SERVICE_TYPE } from 'constants/mockData/umhs';
import { editAuthorization, initAuthorization, postAuthorization } from 'store/authorization/actions';

const { Title } = Typography;
const { Option } = Select;
const { Panel } = Collapse;
const { Dragger } = Upload;

interface AuthCreatePageProps {
  edited: boolean;
  data: any;
  initAuthorization: Function;
  editAuthorization: Function;
  postAuthorization: Function;
}

const leftSideTree = ['1', '3', '4', '5'];
const rightSideTree = ['2', '6'];

const AuthCreatePage = (
  {
    edited, data, initAuthorization, editAuthorization, postAuthorization
  }: AuthCreatePageProps) => {
  const history = useHistory();
  const [currentStep, setCurrentStep] = useState<Array<string>>([]);
  const [scrollStatus, setScrollStatus] = useState<boolean>(false);
  let intervalId: any;

  useEffect(() => {
    initAuthorization();
  }, [initAuthorization]);

  const handleChange = (key: string, val: any) => {
    editAuthorization({ [key]: val });
  };

  const addProviderNote = () => {
    let length = data.provider_notes.length;
    if (length > 0) {
      let notes = [...data.provider_notes, {
        id: data.provider_notes[length - 1].id + 1, note: ''
      }];
      editAuthorization({ provider_notes: notes });
    } else {
      let notes = [{ id: 0, note: ''}];
      editAuthorization({ provider_notes: notes });
    }
  };

  const handleChangeProvider = (id: bigint, val: any) => {
    let notes = data.provider_notes;
    notes.find((item: any) => (item.id === id)).note = val;

    editAuthorization({ provider_notes: notes });
  };

  const removeProvider = (id: bigint) => {
    let notes = data.provider_notes;
    editAuthorization({ provider_notes: notes.filter((item: any) => (item.id !== id)) });
  };

  const addCareManagement = () => {
    let length = data.careManagements.length;
    if (length > 0) {
      let managements = [...data.careManagements, {
        id: data.careManagements[length - 1].id + 1,
        care_subject: '',
        care_user: '',
        care_date: '',
        care_body: '',
      }];
      editAuthorization({ careManagements: managements });
    } else {
      let managements = [{
        id: 0,
        care_subject: '',
        care_user: '',
        care_date: '',
        care_body: '',
      }];
      editAuthorization({ careManagements: managements });
    }
  };

  const handleChangeCare = (id: bigint, key: string, val: any) => {
    let managements = data.careManagements;
    managements.find((item: any) => (item.id === id))[key] = val;

    editAuthorization({ careManagements: managements });
  };

  const removeCare = (id: bigint) => {
    let managements = data.careManagements;
    editAuthorization({ careManagements: managements.filter((item: any) => (item.id !== id)) });
  };

  const toLeft = () => {
    if (!scrollStatus) {
      setScrollStatus(true);
      intervalId = setInterval(LeftHandle, 5);
    }
  };

  const LeftHandle = () => {
    const smoothScroll: HTMLElement = document.getElementById('smooth_scroll') || new HTMLElement();
    smoothScroll.scrollLeft = smoothScroll.scrollLeft -20;

    if (smoothScroll.scrollLeft < 20) {
      smoothScroll.scrollLeft = 0;
      clearInterval(intervalId);
      setScrollStatus(false);
    }
  };

  const toRight = () => {
    if (!scrollStatus) {
      setScrollStatus(true);
      intervalId = setInterval(RightHandle, 5);
    }
  };

  const RightHandle = () => {
    const smoothScroll: HTMLElement = document.getElementById('smooth_scroll') || new HTMLElement();
    let scrollValue: number = smoothScroll.scrollLeft + 20;
    smoothScroll.scrollLeft = scrollValue;

    if (smoothScroll.scrollLeft < scrollValue-1) {
      clearInterval(intervalId);
      setScrollStatus(false);
    }
  };

  const handleGoToNextStep = (next: string) => {
    if (leftSideTree.find((step) => (step === next))) {
      toLeft();
    }

    if (rightSideTree.find((step) => (step === next))) {
      toRight();
    }

    if (!currentStep.find((step) => (step === next))) {
      setCurrentStep([...currentStep, next]);
    }
  };

  const onSubmit = () => {
    postAuthorization(data);

    history.push('/authorization/new');
  };

  const onSaveDraft = () => {
    let id = moment().format('YYYYMMDDhhmmss');
    let draft = {
      id: id,
      data,
    };

    let authDrafts = JSON.parse(localStorage.getItem('authDrafts') || '[]');

    authDrafts = [...authDrafts, draft];

    localStorage.setItem('authDrafts', JSON.stringify(authDrafts));

    history.push('/authorization/new');
  };

  return (
    <div className="page auth-create-page">
      <Title level={4}>Authorization Create Page</Title>

      <div className="content">
        {/*<div className="row">*/}
        {/*  <div className="col-lg-24 col-md-24">*/}
        {/*  <span className="auth-progress mb-3">*/}
        {/*    <div className="auth-progress-bar" style={{ width: `${currentStep.length * 100 / 9}%` }} />*/}
        {/*    <span className="auth-progress-amount"><b>{Math.floor(currentStep.length * 100 / 9)}%</b></span>*/}
        {/*  </span>*/}
        {/*  </div>*/}
        {/*</div>*/}

        <div className="row">
          <div className="col-24">
            <div className="tree-container " id="smooth_scroll">
              <div className="mt-5 left-tree">
                <Collapse activeKey={currentStep} onChange={e => {
                  toLeft();
                  let current: string[] = typeof e === 'string' ? [] : e;
                  setCurrentStep(current);
                }}>

                  <Panel key="1" header="APC - AUTH #20210331RARARA2005" className="node-patient">
                    <div className="row">
                      <div className="col-md-8">
                        <FloatLabel label="Health Plan Auth" value={data.patient_healthPlanAuth}>
                          <Input value={data.patient_healthPlanAuth} onChange={e => handleChange('patient_healthPlanAuth', e.target.value)} />
                        </FloatLabel>
                      </div>

                      <div className="col-md-8">
                        <FloatLabel label="Request Date/time" value={data.patient_requestDate}>
                          <DatePicker
                            format="MM/DD/YYYY" suffixIcon={<i className="far fa-calendar-alt" />} placeholder=""
                            value={data.patient_requestDate && moment(data.patient_requestDate)}
                            onChange={(date, dateString) => handleChange('patient_requestDate', dateString)}
                          />
                        </FloatLabel>
                      </div>

                      <div className="col-md-8">
                        <FloatLabel label="Auth Action Date/time" value={data.patient_authActionDate}>
                          <DatePicker
                            format="MM/DD/YYYY" suffixIcon={<i className="far fa-calendar-alt" />} placeholder=""
                            value={data.patient_authActionDate && moment(data.patient_authActionDate)}
                            onChange={(date, dateString) => handleChange('patient_authActionDate', dateString)}
                          />
                        </FloatLabel>
                      </div>

                      <div className="col-md-8">
                        <FloatLabel label="Expiry Date/time" value={data.patient_expiryDate}>
                          <DatePicker
                            format="MM/DD/YYYY" suffixIcon={<i className="far fa-calendar-alt" />} placeholder=""
                            value={data.patient_expiryDate && moment(data.patient_expiryDate)}
                            onChange={(date, dateString) => handleChange('patient_expiryDate', dateString)}
                          />
                        </FloatLabel>
                      </div>

                      <div className="col-md-16" />

                      <div className="col-md-8 mt-4">
                        <FloatLabel label="Member ID" value={data.patient_memberId} hasIcon>
                          <Input prefix={<SearchOutlined />} value={data.patient_memberId}
                                 onChange={e => handleChange('patient_memberId', e.target.value)} />
                        </FloatLabel>
                      </div>

                      <div className="col-md-8 mt-4">
                        <FloatLabel label="Name" value={data.patient_name} hasIcon>
                          <Input prefix={<SearchOutlined />} value={data.patient_name}
                                 onChange={e => handleChange('patient_name', e.target.value)} />
                        </FloatLabel>
                      </div>

                      <div className="col-md-8 mt-4">
                        <FloatLabel label="DOB" value={data.patient_dob}>
                          <Input value={data.patient_dob} onChange={e => handleChange('patient_dob', e.target.value)} />
                        </FloatLabel>
                      </div>

                      <div className="col-md-8">
                        <FloatLabel label="Gender" value={data.patient_gender}>
                          <Input value={data.patient_gender} onChange={e => handleChange('patient_gender', e.target.value)} />
                        </FloatLabel>
                      </div>

                      <div className="col-md-8">
                        <FloatLabel label="Health Plan" value={data.patient_healthPlan}>
                          <Input value={data.patient_healthPlan} onChange={e => handleChange('patient_healthPlan', e.target.value)} />
                        </FloatLabel>
                      </div>

                      <div className="col-md-8">
                        <FloatLabel label="Request Source" value={data.patient_requestSource}>
                          <Select
                            value={data.patient_requestSource}
                            onChange={(value) => handleChange('patient_requestSource', value)}
                            className="w-100"
                          >
                            {UMHS_PATIENT_REQUEST_SOURCE.map(i => (<Option key={i.key} value={i.key}>{i.label}</Option>))}
                          </Select>
                        </FloatLabel>
                      </div>

                      <div className="col-md-8">
                        <FloatLabel label="ACM Case" value={data.patient_acmCase} hasIcon>
                          <Input prefix={<SearchOutlined />} value={data.patient_acmCase}
                                 onChange={e => handleChange('patient_acmCase', e.target.value)} />
                        </FloatLabel>
                      </div>

                      <div className="col-md-8">
                        <FloatLabel label="Health Plan Case" value={data.patient_healthPlanCase}>
                          <Input value={data.patient_healthPlanCase} onChange={e => handleChange('patient_healthPlanCase', e.target.value)} />
                        </FloatLabel>
                      </div>

                      <div className="col-md-8">
                        <FloatLabel label="CCS/GHDP Case" value={data.patient_ccs_ghdpCase}>
                          <Input value={data.patient_ccs_ghdpCase} onChange={e => handleChange('patient_ccs_ghdpCase', e.target.value)} />
                        </FloatLabel>
                      </div>

                      <div className="col-md-8">
                        <FloatLabel label="Primary language" value={data.patient_primaryLanguage}>
                          <Input value={data.patient_primaryLanguage} onChange={e => handleChange('patient_primaryLanguage', e.target.value)} />
                        </FloatLabel>
                      </div>
                    </div>

                    <div className="row">
                      <div className="col d-flex">
                        <Button type="primary" className="ml-auto waves-effect waves-light" onClick={() => handleGoToNextStep('2')}>
                          Next
                        </Button>
                      </div>
                    </div>
                  </Panel>

                  <Panel key="3" header="Referring Provider" className="node-referring">
                    <div className="row">
                      <div className="col-md-8">
                        <FloatLabel label="Referring Provider Name" value={data.referring_providerName} hasIcon>
                          <Input prefix={<SearchOutlined />} value={data.referring_providerName}
                                 onChange={e => handleChange('referring_providerName', e.target.value)} />
                        </FloatLabel>
                      </div>

                      <div className="col-md-8">
                        <FloatLabel label="Referring Provider ID" value={data.referring_providerId}>
                          <Input value={data.referring_providerId} onChange={e => handleChange('referring_providerId', e.target.value)} />
                        </FloatLabel>
                      </div>

                      <div className="col-md-8">
                        <FloatLabel label="Referring Provider Speciality" value={data.referring_providerSpeciality}>
                          <Input value={data.referring_providerSpeciality} onChange={e => handleChange('referring_providerSpeciality', e.target.value)} />
                        </FloatLabel>
                      </div>

                      <div className="col-md-8">
                        <FloatLabel label="Phone" value={data.referring_phone}>
                          <Input value={data.referring_phone} onChange={e => handleChange('referring_phone', e.target.value)} />
                        </FloatLabel>
                      </div>

                      <div className="col-md-8">
                        <FloatLabel label="Fax" value={data.referring_fax}>
                          <Input value={data.referring_fax} onChange={e => handleChange('referring_fax', e.target.value)} />
                        </FloatLabel>
                      </div>

                      <div className="col-md-8">
                        <FloatLabel label="Address" value={data.referring_address}>
                          <Input value={data.referring_address} onChange={e => handleChange('referring_address', e.target.value)} />
                        </FloatLabel>
                      </div>

                      {data.provider_notes.map((note: any) => (
                        <div className="col-md-24 mb-3" key={note.id}>
                          <FloatLabel label="Notes" value={note.note}>
                            <Input.TextArea value={note.note} rows={5} onChange={e => handleChangeProvider(note.id, e.target.value)} />
                          </FloatLabel>
                          <Button type="primary" className="ml-3 waves-effect waves-light" onClick={() => removeProvider(note.id)}>
                            Remove
                          </Button>
                        </div>
                      ))}
                    </div>

                    <div className="row">
                      <div className="col d-flex">
                        <Button type="primary" className="ml-auto waves-effect waves-light" onClick={addProviderNote}>
                          Add Note
                        </Button>
                        <Button className="ml-5 waves-effect waves-light" onClick={() => handleGoToNextStep('3')}>
                          Previous
                        </Button>
                        <Button type="primary" className="ml-3 waves-effect waves-light" onClick={() => handleGoToNextStep('5')}>
                          Next
                        </Button>
                      </div>
                    </div>
                  </Panel>

                  <Panel key="4" header="Requesting services" className="node-service">
                    <div className="row">
                      <div className="col-md-8">
                        <FloatLabel label="Rendering Provider Name" value={data.rendering_providerName} hasIcon>
                          <Input prefix={<SearchOutlined />} value={data.rendering_providerName}
                                 onChange={e => handleChange('rendering_providerName', e.target.value)} />
                        </FloatLabel>
                      </div>

                      <div className="col-md-8">
                        <FloatLabel label="Rendering Provider ID" value={data.rendering_providerId}>
                          <Input value={data.rendering_providerId} onChange={e => handleChange('rendering_providerId', e.target.value)} />
                        </FloatLabel>
                      </div>

                      <div className="col-md-8">
                        <FloatLabel label="Rendering Provider Speciality" value={data.rendering_providerSpeciality}>
                          <Input value={data.rendering_providerSpeciality} onChange={e => handleChange('rendering_providerSpeciality', e.target.value)} />
                        </FloatLabel>
                      </div>

                      <div className="col-md-16">
                        <FloatLabel label="Facility Name" value={data.rendering_facilityName} hasIcon>
                          <Input prefix={<SearchOutlined />} value={data.rendering_facilityName} onChange={e => handleChange('rendering_facilityName', e.target.value)} />
                        </FloatLabel>
                      </div>

                      <div className="col-md-8">
                        <FloatLabel label="Distance" value={data.rendering_distance}>
                          <Input value={data.rendering_distance} onChange={e => handleChange('rendering_distance', e.target.value)} />
                        </FloatLabel>
                      </div>

                      <div className="col-md-8">
                        <FloatLabel label="Contract Status" value={data.rendering_contractStatus}>
                          <Input value={data.rendering_contractStatus} onChange={e => handleChange('rendering_contractStatus', e.target.value)} />
                        </FloatLabel>
                      </div>

                      <div className="col-md-8">
                        <FloatLabel label="Provider Class" value={data.rendering_providerClass}>
                          <Input value={data.rendering_providerClass} onChange={e => handleChange('rendering_providerClass', e.target.value)} />
                        </FloatLabel>
                      </div>

                      <div className="col-md-8" />

                      <div className="col-md-8 mt-3">
                        <FloatLabel label="Service - P/H" value={data.service_PH}>
                          <Input value={data.service_PH}
                                 onChange={e => handleChange('service_PH', e.target.value)} />
                        </FloatLabel>
                      </div>

                      <div className="col-md-8 mt-3">
                        <FloatLabel label="Place of Service" value={data.service_place}>
                          <Input value={data.service_place} onChange={e => handleChange('service_place', e.target.value)} />
                        </FloatLabel>
                      </div>

                      <div className="col-md-8 mt-3" />

                      <div className="col-md-8">
                        <FloatLabel label="Quantity Unit" value={data.service_quantityUnit}>
                          <Input value={data.service_quantityUnit} onChange={e => handleChange('service_quantityUnit', e.target.value)} />
                        </FloatLabel>
                      </div>

                      <div className="col-md-8">
                        <FloatLabel label="Procedure Code - Service Description" value={data.service_procedureCode}>
                          <Input value={data.service_procedureCode} onChange={e => handleChange('service_procedureCode', e.target.value)} />
                        </FloatLabel>
                      </div>

                      <div className="col-md-8">
                        <FloatLabel label="Adj code - Description" value={data.service_adjCode}>
                          <Input value={data.service_adjCode} onChange={e => handleChange('service_adjCode', e.target.value)} />
                        </FloatLabel>
                      </div>

                      <div className="col-md-16 mt-3">
                        <FloatLabel label="DX Code - Description" value={data.service_diagnosis}>
                          <Input value={data.service_diagnosis} onChange={e => handleChange('service_diagnosis', e.target.value)} />
                        </FloatLabel>
                      </div>

                      <div className="col-md-8"/>

                      <div className="col-md-8">
                        <FloatLabel label="Admit Date" value={data.service_admitDate}>
                          <DatePicker
                            format="MM/DD/YYYY" suffixIcon={<i className="far fa-calendar-alt" />} placeholder=""
                            value={data.service_admitDate && moment(data.service_admitDate)}
                            onChange={(date, dateString) => handleChange('service_admitDate', dateString)}
                          />
                        </FloatLabel>
                      </div>

                      <div className="col-md-8">
                        <FloatLabel label="Discharge Date" value={data.service_dischargeDate}>
                          <DatePicker
                            format="MM/DD/YYYY" suffixIcon={<i className="far fa-calendar-alt" />} placeholder=""
                            value={data.service_dischargeDate && moment(data.service_dischargeDate)}
                            onChange={(date, dateString) => handleChange('service_dischargeDate', dateString)}
                          />
                        </FloatLabel>
                      </div>
                    </div>

                    <div className="row">
                      <div className="col d-flex">
                        <Button className="ml-auto waves-effect waves-light" onClick={() => handleGoToNextStep('4')}>
                          Previous
                        </Button>
                        <Button type="primary" className="ml-3 waves-effect waves-light" onClick={() => handleGoToNextStep('6')}>
                          Next
                        </Button>
                      </div>
                    </div>
                  </Panel>

                  <Panel key="5" header="Audit trail" className="node-audit">
                    <div className="row">
                      <div className="col-md-6">
                        <FloatLabel label="Date/Time" value={data.audit_date}>
                          <Input value={data.audit_date} readOnly
                                 onChange={e => handleChange('audit_date', e.target.value)} />
                        </FloatLabel>
                      </div>

                      <div className="col-md-6">
                        <FloatLabel label="User" value={data.audit_user}>
                          <Input value={data.audit_user} readOnly
                                 onChange={e => handleChange('audit_user', e.target.value)} />
                        </FloatLabel>
                      </div>

                      <div className="col-md-6">
                        <FloatLabel label="Orig Value" value={data.audit_origValue}>
                          <Input value={data.audit_origValue} readOnly
                                 onChange={e => handleChange('audit_origValue', e.target.value)} />
                        </FloatLabel>
                      </div>

                      <div className="col-md-6">
                        <FloatLabel label="New Value" value={data.audit_newValue}>
                          <Input value={data.audit_newValue} readOnly
                                 onChange={e => handleChange('audit_newValue', e.target.value)} />
                        </FloatLabel>
                      </div>
                    </div>

                    <div className="row">
                      <div className="col d-flex">
                        <Button className="ml-auto waves-effect waves-light" onClick={() => handleGoToNextStep('6')}>
                          Previous
                        </Button>
                        <Button type="primary" className="ml-3 waves-effect waves-light" onClick={() => handleGoToNextStep('8')}>
                          Next
                        </Button>
                      </div>
                    </div>
                  </Panel>

                </Collapse>
              </div>

              <div className="tree-trunk"/>

              <div className="mt-5 right-tree">
                <Collapse activeKey={currentStep} onChange={e => {
                  toRight();
                  let current: string[] = typeof e === 'string' ? [] : e;
                  setCurrentStep(current);
                }}>
                  <Panel key="2" header="Attachments" className="node-attachment">
                    <Dragger>
                      <p className="ant-upload-drag-icon">
                        <InboxOutlined />
                      </p>
                      <p className="ant-upload-text">Click or drag file to this area to upload</p>
                    </Dragger>

                    <div className="row">
                      <div className="col d-flex">
                        <Button className="ml-auto waves-effect waves-light" onClick={() => handleGoToNextStep('1')}>
                          Previous
                        </Button>
                        <Button type="primary" className="ml-3 waves-effect waves-light" onClick={() => handleGoToNextStep('3')}>
                          Next
                        </Button>
                      </div>
                    </div>
                  </Panel>

                  <Panel key="6" header="Care management notes" className="node-care">
                    {data.careManagements.map((care: any) => (
                      <div className="row mb-3" key={care.id}>
                        <div className="col-md-8">
                          <FloatLabel label="Subject" value={care.care_subject}>
                            <Input value={care.care_subject}
                                   onChange={e => handleChangeCare(care.id,'care_subject', e.target.value)} />
                          </FloatLabel>
                        </div>

                        <div className="col-md-8">
                          <FloatLabel label="User" value={care.care_user}>
                            <Input value={care.care_user}
                                   onChange={e => handleChangeCare(care.id,'care_user', e.target.value)} />
                          </FloatLabel>
                        </div>

                        <div className="col-md-8">
                          <FloatLabel label="Date/Time" value={care.care_date}>
                            <DatePicker
                              format="MM/DD/YYYY" suffixIcon={<i className="far fa-calendar-alt" />} placeholder=""
                              value={care.care_date && moment(care.care_date)}
                              onChange={(date, dateString) => handleChangeCare(care.id,'care_date', dateString)}
                            />
                          </FloatLabel>
                        </div>

                        <div className="col-md-24">
                          <FloatLabel label="Notes" value={care.care_body}>
                            <Input.TextArea value={care.care_body} rows={3}
                                            onChange={e => handleChangeCare(care.id,'care_body', e.target.value)} />
                          </FloatLabel>
                        </div>

                        <Button type="primary" className="ml-4 waves-effect waves-light" onClick={() => removeCare(care.id)}>
                          Remove
                        </Button>
                      </div>
                    ))}

                    <div className="row">
                      <div className="col d-flex">
                        <Button type="primary" className="ml-auto waves-effect waves-light" onClick={addCareManagement}>
                          Add
                        </Button>
                        <Button className="ml-5 waves-effect waves-light" onClick={() => handleGoToNextStep('7')}>
                          Previous
                        </Button>
                        <Button type="primary" className="ml-3 waves-effect waves-light">Finish</Button>
                      </div>
                    </div>
                  </Panel>
                </Collapse>
              </div>
            </div>
          </div>

          <div className="col-24">
            <div className="row mt-3">
              <div className="col d-flex">
                <Button type="primary" className="ml-auto waves-effect waves-light" onClick={onSaveDraft}>
                  Save Draft
                </Button>
                <Button className="ml-3 waves-effect waves-light" onClick={() => history.push('/authorization/new')}>
                  Cancel
                </Button>
                <Button type="primary" className="ml-3 waves-effect waves-light" disabled={!edited} onClick={onSubmit}>
                  Save
                </Button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = (store: RootStateOrAny) => ({
  edited: store.rootReducer.authorization.edited,
  data: store.rootReducer.authorization.data,
});

const mapDispatchToProps = (dispatch: Dispatch) => bindActionCreators({
  editAuthorization,
  initAuthorization,
  postAuthorization
},dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(AuthCreatePage);
