import types from './types';

const INITIAL_DATA = {
  "companyId": null,
  "type": "",
  "code": "",
  "description": "",
  "editable": false,
  "selectable": true,
  "id": "",
};

const INITIAL_STATE = {
  isFetchingListCallCategories: false,

  data: {...INITIAL_DATA},
  items: [],
};

const callCategoriesReducer = (state = INITIAL_STATE, action: any) => {
  switch (action.type) {
    // Get list
    case types.GET_LIST_CALL_CATEGORY_REQUEST:
      return {
        ...state,
        isFetchingListCallCategories: true,
      };
    case types.GET_LIST_CALL_CATEGORY_SUCCESS:
      return {
        ...state,
        isFetchingListCallCategories: false,
        items: action.payload,
      };
    case types.GET_LIST_CALL_CATEGORY_FAILURE:
      return {
        ...state,
        isFetchingListCallCategories: false,
        items: [],
      };

    default:
      return state;
  }
};

export default callCategoriesReducer;
