import types from './types';

const INITIAL_TERMING_PROVIDER_GROUP_DATA = {
  id: '',
  deleted: '',
  createdDateUtc: '',
  modifiedDateUtc: '',
  pgoMasterId: '',
  npi: '',
  providerName: '',
  providerId: '',
  vendorEffectiveDate: '',
  vendorName: '',
  vendorId: '',
  licenseNumber: '',
  specialty: '',
  providerClass: '',
  details: ''
};

const INITIAL_STATE = {
  isFetchingListTermingProviderGroups: false,
  isFetchingTermingProviderGroups: false,
  isPostingTermingProviderGroups: false,
  isPuttingTermingProviderGroups: false,

  data: {...INITIAL_TERMING_PROVIDER_GROUP_DATA},
  items: [{...INITIAL_TERMING_PROVIDER_GROUP_DATA}],

  edited: false,
};

const pgoTermingProviderGroupsReducer = (state = INITIAL_STATE, action: any) => {
  switch (action.type) {
    // Init
    case types.PGO_TERMING_PROVIDER_GROUP_INIT:
      return {
        ...state,
        data: { ...INITIAL_TERMING_PROVIDER_GROUP_DATA },
        edited: false,
      };

    // Edit
    case types.PGO_TERMING_PROVIDER_GROUP_EDIT:
      return {
        ...state,
        data: { ...state.data, ...action.payload },
        edited: true,
      };

    // Get list
    case types.GET_LIST_PGO_TERMING_PROVIDER_GROUP_REQUEST:
      return {
        ...state,
        isFetchingListTermingProviderGroups: true,
      };
    case types.GET_LIST_PGO_TERMING_PROVIDER_GROUP_SUCCESS:
      return {
        ...state,
        isFetchingListTermingProviderGroups: false,
        items: action.payload,
      };
    case types.GET_LIST_PGO_TERMING_PROVIDER_GROUP_FAILURE:
      return {
        ...state,
        isFetchingListTermingProviderGroups: false,
        items: [],
      };

    // Get
    case types.GET_PGO_TERMING_PROVIDER_GROUP_REQUEST:
      return {
        ...state,
        isFetchingTermingProviderGroups: true,
      };
    case types.GET_PGO_TERMING_PROVIDER_GROUP_SUCCESS:
      return {
        ...state,
        isFetchingTermingProviderGroups: false,
        data: action.payload,
        edited: false,
      };
    case types.GET_PGO_TERMING_PROVIDER_GROUP_FAILURE:
      return {
        ...state,
        isFetchingTermingProviderGroups: false,
        data: { ...INITIAL_TERMING_PROVIDER_GROUP_DATA },
      };

    // Post
    case types.POST_PGO_TERMING_PROVIDER_GROUP_REQUEST:
      return {
        ...state,
        isPostingTermingProviderGroups: true,
      };
    case types.POST_PGO_TERMING_PROVIDER_GROUP_SUCCESS:
      return {
        ...state,
        isPostingTermingProviderGroups: false,
        edited: false,
      };
    case types.POST_PGO_TERMING_PROVIDER_GROUP_FAILURE:
      return {
        ...state,
        isPostingTermingProviderGroups: false,
      };

    // Put
    case types.PUT_PGO_TERMING_PROVIDER_GROUP_REQUEST:
      return {
        ...state,
        isPuttingTermingProviderGroups: true,
      };
    case types.PUT_PGO_TERMING_PROVIDER_GROUP_SUCCESS:
      return {
        ...state,
        isPuttingTermingProviderGroups: false,
        edited: false,
      };
    case types.PUT_PGO_TERMING_PROVIDER_GROUP_FAILURE:
      return {
        ...state,
        isPuttingTermingProviderGroups: false,
      };

    default:
      return state;
  }
};

export default pgoTermingProviderGroupsReducer;
