import React, { useEffect } from 'react';
import { connect, RootStateOrAny } from 'react-redux';

import './style.scss';

interface ErrorPageProps {
  isFetchingMe: boolean,
}

const ErrorPage = ({ isFetchingMe }: ErrorPageProps) => {
  useEffect(() => {
    setTimeout(() => {
      window.location.href = '/';
    }, 5000);
  }, []);

  return (
    <div className="page page-error-404">
      <h1>{isFetchingMe ? '...' : 'Error - 404, Redirecting to homepage after 5 seconds...'}</h1>
    </div>
  );
};

const mapStateToProps = (store: RootStateOrAny) => ({
  isFetchingMe: store.rootReducer.auth.isFetchingMe,
});

export default connect(mapStateToProps)(ErrorPage);
