import types from './types';

const INITIAL_GRID_OPERATION_DATA = {
  id: '',
  deleted: '',
  createdDateUtc: '',
  modifiedDateUtc: '',
  pgoMasterId: '',
  subject: '',
  user: '',
  body: ''
};

const INITIAL_STATE = {
  isFetchingListGridOperations: false,
  isFetchingGridOperations: false,
  isPostingGridOperations: false,
  isPuttingGridOperations: false,

  data: {...INITIAL_GRID_OPERATION_DATA},
  items: [],

  edited: false,
};

const pgoGridOperationsReducer = (state = INITIAL_STATE, action: any) => {
  switch (action.type) {
    // Init
    case types.PGO_GRID_OPERATION_INIT:
      return {
        ...state,
        data: { ...INITIAL_GRID_OPERATION_DATA },
        edited: false,
      };

    // Edit
    case types.PGO_GRID_OPERATION_EDIT:
      return {
        ...state,
        data: { ...state.data, ...action.payload },
        edited: true,
      };

    // Get list
    case types.GET_LIST_PGO_GRID_OPERATION_REQUEST:
      return {
        ...state,
        isFetchingListGridOperations: true,
      };
    case types.GET_LIST_PGO_GRID_OPERATION_SUCCESS:
      return {
        ...state,
        isFetchingListGridOperations: false,
        items: action.payload,
      };
    case types.GET_LIST_PGO_GRID_OPERATION_FAILURE:
      return {
        ...state,
        isFetchingListGridOperations: false,
        items: [],
      };

    // Get
    case types.GET_PGO_GRID_OPERATION_REQUEST:
      return {
        ...state,
        isFetchingGridOperations: true,
      };
    case types.GET_PGO_GRID_OPERATION_SUCCESS:
      return {
        ...state,
        isFetchingGridOperations: false,
        data: action.payload,
        edited: false,
      };
    case types.GET_PGO_GRID_OPERATION_FAILURE:
      return {
        ...state,
        isFetchingGridOperations: false,
        data: { ...INITIAL_GRID_OPERATION_DATA },
      };

    // Post
    case types.POST_PGO_GRID_OPERATION_REQUEST:
      return {
        ...state,
        isPostingGridOperations: true,
      };
    case types.POST_PGO_GRID_OPERATION_SUCCESS:
      return {
        ...state,
        isPostingGridOperations: false,
        edited: false,
      };
    case types.POST_PGO_GRID_OPERATION_FAILURE:
      return {
        ...state,
        isPostingGridOperations: false,
      };

    // Put
    case types.PUT_PGO_GRID_OPERATION_REQUEST:
      return {
        ...state,
        isPuttingGridOperations: true,
      };
    case types.PUT_PGO_GRID_OPERATION_SUCCESS:
      return {
        ...state,
        isPuttingGridOperations: false,
        edited: false,
      };
    case types.PUT_PGO_GRID_OPERATION_FAILURE:
      return {
        ...state,
        isPuttingGridOperations: false,
      };

    default:
      return state;
  }
};

export default pgoGridOperationsReducer;
