import types from './types';

const INITIAL_DATA = {
  "BUCKETS": [
    [
      0,
      ""
    ]
  ],
  "DEPT_ID": null,
  "DEPT_NAME": null,
  "IS_ACTIVE": true,
  "NAME": "",
  "QUEUE_ID": 0
};

const INITIAL_STATE = {
  isFetchingListQueues: false,

  data: {...INITIAL_DATA},
  items: [],
};

const queuesReducer = (state = INITIAL_STATE, action: any) => {
  switch (action.type) {
    // Get list
    case types.GET_LIST_QUEUE_REQUEST:
      return {
        ...state,
        isFetchingListQueues: true,
      };
    case types.GET_LIST_QUEUE_SUCCESS:
      return {
        ...state,
        isFetchingListQueues: false,
        items: action.payload.Queues,
      };
    case types.GET_LIST_QUEUE_FAILURE:
      return {
        ...state,
        isFetchingListQueues: false,
        items: [],
      };

    default:
      return state;
  }
};

export default queuesReducer;
