import types from './types';
import api from 'constants/api';
import _get from 'lodash/get';
import moment from 'moment';

import { ThunkDispatch } from 'redux-thunk';

export const getMembers = (data: any) => (dispatch: ThunkDispatch<any, any, any>, getState: any) => {
    if (getState().rootReducer.members.isFetchingMembers) { return Promise.reject(); }

    dispatch({ type: types.GET_MEMBERS_REQUEST, payload: data });

    return api.members.findMembers(data)
        .then((res: any) => {

            dispatch({
                type: types.GET_MEMBERS_SUCCESS,
                payload: res,
            });

            return res;
        })
        .catch((err: Error) => {
            const msg = _get(err, 'response.data.msg', 'Get failed!');
            const errors = _get(err, 'response.data.errors', {});
            const payload = { ...err, msg, errors };

            dispatch({
                type: types.GET_MEMBERS_FAILURE,
                payload,
            });

            return Promise.reject(payload);
        });
};