import React, { PureComponent } from 'react';
import { connect, RootStateOrAny } from 'react-redux';
import {
  Redirect,
  Route,
  Switch,
} from 'react-router-dom';
import { RouteComponentProps } from 'react-router';
import { TransitionGroup, CSSTransition } from 'react-transition-group';

import GeneralLayout from 'containers/Templates/Layout/GeneralLayout';
import GuestLayout from 'containers/Templates/Layout/GuestLayout';

import LandingPage from 'containers/Pages/Common/LandingPage';
import LogoutPage from 'containers/Pages/Common/LogoutPage';
import LoginPage from 'containers/Pages/Common/LoginPage';
import ErrorPage from 'containers/Pages/Common/ErrorPage';

import Dashboard from 'containers/Pages/User/Dashboard';

import PgoNewPage from 'containers/Pages/User/PgoDetails/New';
import PgoCreatePage from 'containers/Pages/User/PgoDetails/Create';
import PgoUpdateDraftPage from 'containers/Pages/User/PgoDetails/EditDraft';

import PgoSearchPage from 'containers/Pages/User/PgoDetails/Search';
import PgoUpdatePage from 'containers/Pages/User/PgoDetails/Update';

// import AuthNewPage from 'containers/Pages/User/Authorization/New';
// import AuthCreatePage from 'containers/Pages/User/Authorization/Create';
// import AuthUpdateDraftPage from 'containers/Pages/User/Authorization/EditDraft';
//
// import AuthSearchPage from 'containers/Pages/User/Authorization/Search';
// import AuthUpdatePage from 'containers/Pages/User/Authorization/Update';

import AuthNewPage from 'containers/Pages/User/Authorization2/New';
import AuthCreatePage from 'containers/Pages/User/Authorization2/Create';
import AuthUpdateDraftPage from 'containers/Pages/User/Authorization2/EditDraft';

import AuthSearchPage from 'containers/Pages/User/Authorization2/Search';
import AuthUpdatePage from 'containers/Pages/User/Authorization2/Update';

import ProfilePage from 'containers/Pages/User/Profile';

import ProviderPage from 'containers/Pages/User/Provider';

import TicketsCreatePage from 'containers/Pages/User/MemberTickets/Create';
import TicketsCreatePage2 from 'containers/Pages/User/MemberTickets/Create-2';
import TicketsDetailPage from 'containers/Pages/User/MemberTickets/Review';
import TicketsSearchPage from 'containers/Pages/User/MemberTickets/Search';

import CCSTicketsCreatePage from 'containers/Pages/User/CCSTickets/Create';
//import from 'containers/Pages/User/CCSTickets/Review';
import MembersPage from 'containers/Pages/User/Members';

import AttachmentViewerTest from 'containers/Pages/User/AttachmentViewerTest';

type RoutesProps = RouteComponentProps<any> & {
  isAuthenticated: boolean;
  user_role: string;
  user: any;
  loggedIn: boolean;
}

class Routes extends PureComponent<RoutesProps> {

  CommonRoutes = () => {
    return [
      <Route key="/" exact path="/" component={LandingPage} />,
      <Route key="/logout" exact path="/logout" component={LogoutPage} />,
    ];
  };

  GuestRoutes = (location: any) => {
    return (
      <Switch location={location}>
        <Route exact path="/login" component={LoginPage} />

        {this.CommonRoutes()}

        <Route exact component={ErrorPage} />
      </Switch>
    );
  };

  UserRoutes = (user_role: string | null, location: any) => {
    return (
      <Switch location={location}>
        <Redirect exact from="/login" to="/dashboard" />
        <Redirect exact from="/" to="/dashboard" />

        <Route path="/dashboard" component={Dashboard} />

        <Route path="/member-tickets/create" component={TicketsCreatePage} />
        <Route path="/member-tickets/create-2" component={TicketsCreatePage2} />
        <Route path="/member-tickets/search" component={TicketsSearchPage} />
        <Route path="/member-tickets/:id" component={TicketsDetailPage} />

        <Route path="/ccstickets/create" component={CCSTicketsCreatePage} />

        <Route path="/pgo-details/new" component={PgoNewPage} />
        <Route path="/pgo-details/create" component={PgoCreatePage} />
        <Route path="/pgo-details/drafts/:id/" component={PgoUpdateDraftPage} />

        <Route path="/pgo-details/search" component={PgoSearchPage} />
        <Route path="/pgo-details/:id/edit" component={PgoUpdatePage} />

        <Route path="/authorization/new" component={AuthNewPage} />
        <Route path="/authorization/create" component={AuthCreatePage} />
        <Route path="/authorization/drafts/:id/" component={AuthUpdateDraftPage} />

        <Route path="/authorization/search" component={AuthSearchPage} />
        <Route path="/authorization/:id/edit" component={AuthUpdatePage} />

        <Route path="/profile" component={ProfilePage} />
        <Route path="/provider" component={ProviderPage} />

        <Route path="/member" component={MembersPage} />

        {/*<Route path="/attachmentviewertest" component={AttachmentViewerTest} />*/}

        {this.CommonRoutes()}
        <Route exact component={ErrorPage} />
      </Switch>
    );
  };

  render() {
    const { location, history, isAuthenticated, loggedIn, user_role, user, ...props } = this.props;

    return (
      <TransitionGroup className="transition-group">
        <CSSTransition
          key={location.pathname}
          timeout={{ enter: 300, exit: 300 }}
          classNames="fade1"
        >
          <section className="html-v1 route-section">
            {loggedIn
              ? (
                <GeneralLayout {...props} location={location} user={user} isAuthenticated={loggedIn}
                               user_role={user_role} history={history}>
                  {this.UserRoutes(user_role, location)}
                </GeneralLayout>
              )
              : (
                <GuestLayout location={location} history={history} >
                  {this.GuestRoutes(location)}
                </GuestLayout>
              )
            }
          </section>
        </CSSTransition>
      </TransitionGroup>
    );

  }
}

const mapStateToProps = (state: RootStateOrAny) => ({
  isAuthenticated: !!(state.rootReducer.auth?.user && state.rootReducer.auth?.access_token),
  user_role: (state.rootReducer.auth?.user?.user_role) || '',
  user: state.rootReducer.auth?.user,
  loggedIn: state.rootReducer.auth?.loggedIn,
});

export default connect(mapStateToProps)(Routes);
