import types from './types';

const INITIAL_STATE = {
  isFetchingList: false,

  data: [],
};

const departmentsReducer = (state = INITIAL_STATE, action: any) => {
  switch (action.type) {
    // Login
    case types.GET_LIST_DEPARTMENT_REQUEST:
      return {
        ...state,
        isFetchingList: true,
        data: [],
      };
    case types.GET_LIST_DEPARTMENT_SUCCESS:
      return {
        ...state,
        isFetchingList: false,
        data: action.payload,
      };
    case types.GET_LIST_DEPARTMENT_FAILURE:
      return {
        ...state,
        isFetchingList: false,
        data: [],
      };

    // Initial state
    default:
      return state;
  }
};

export default departmentsReducer;
