import React, { useState, useEffect } from 'react';

import api from 'constants/api';
import notifier from '../../../../utils/notifier';

export const useGetVendorsOfProviders = (selectedProviderGuid: any) => {
  const [isFetchingVendors, setIsFetchingVendors] = useState<boolean>(false);
  const [vendorIds, setVendorIds] = useState<Array<any>>([]);
  const [vendorDetails, setVendorDetails] = useState<Array<any>>([]);

  useEffect(() => {
    if (selectedProviderGuid) {
      setIsFetchingVendors(true);

      api.providers.getVendors(selectedProviderGuid)
        .then(data => {
          setVendorIds(data);

          const getVendorDetailsPromises = data.map(
            (vendorId: any) => api.vendors.get(vendorId?.id)
              .then(res => res)
              .catch(err => Promise.resolve(false)),
          );

          return Promise.all(getVendorDetailsPromises)
            .then(vendors => {
              const newVendorDetails = vendors.reduce((acc: Array<any>, cur) => {
                if (cur) { acc.push(cur); }
                return acc;
              }, []);

              setVendorDetails(newVendorDetails);
            })
            .catch(err => {
              setVendorDetails([]);

              return Promise.reject(err);
            });
        })
        .catch(err => {
          notifier.error('Can not get vendors of selected provider!', err?.msg || '');
        })
        .finally(() => {
          setIsFetchingVendors(false);
        });
    }
  }, [selectedProviderGuid, setVendorIds]);

  return {isFetchingVendors, vendorDetails};
};
