import types from './types';

const INITIAL_DATA = {

    AUTH_NBR: '',
    CALLBACK_NBR: '',
    CALLBACK_EXT: '',
    CALLER_CODE: {
      CC_ID: '',
      CALLER_CODE: '',
    },
    CALLER_EMAIL: '',
    CALLER_FAX: '',
    CALLER_NAME: '',
    CALL_TIME: '',
    CALLER_CLASSIFICATION: '',
    CATEGORY: {
      CATEGORY_ID: null,
      TAT: null,
    },
    IPA: null,
  ITEM_ATTACHMENT: '',
    ITEM_TITLE: '',
    ITEM_TYPE: '',

    MBR_ID: '',
    MBR_FIRSTNAME: '',
    MBR_LASTNAME: '',
    MBR_PHONE: '',
    MBR_EXT: '',
    MBR_DOB: '',
    MBR_MI: '',
    MBR_IPA: '',
    MBR_HPCODE: '',

    PROVIDER_ID: '',
    PROVIDER: '',
    PROVIDER_PHONE: '',
    PROVIDER_EXT: null,
    PROVIDER_FAX: '',

    TICKET_REF_NO: '',
    ITEM_CONTENT: '',
    NOTES: '',

  routeTo: '',
  MatchedItemInput: '',
  opt: '',
  providerFirstName: '',
  providerLastName: '',
  memberStatus: false,
  providerStatus: false,
  providerAddPhone: '',
  providerVendorId: '',
  providerAccount: '',

  ITEM_ID: '',
  TAGS: null,
  URGENT: null,
  USER: {
    'DEPT_ID': '',
    'FIRST_NAME': '',
    'IS_ACTIVE': false,
    'LAST_NAME': '',
    'USERNAME': '',
    'USER_ID': null,
  },
  BUCKET_ID: '',
  BUCKET_NAME: '',
  CLAIM_CS: {},
  DATE_COMPLETED: null,
  DATE_CREATED: '',
  DATE_MODIFIED: '',
  DEPARTMENT: '',
  DUE_DATE: null,
  HEALTH_PLAN: null,
  HP_EXT: null,
  HP_FAX: null,
  HP_PHONE: null,
  ORIGIN_QUEUE: 112,
  PRIORITY: {
    PRIORITY: '',
    PRIORITY_ID: 2,
  },
  parent: null,
};

const INITIAL_STATE = {
  isFetchingListTickets: false,
  isFetchingTickets: false,
  isPostingTickets: false,
  isPuttingTickets: false,
  isFetchingTicketsNo: false,

  data: { ...INITIAL_DATA },
  items: [],

  ticket_ref_no: '',

  edited: false,
};

const ticketsReducer = (state = INITIAL_STATE, action: any) => {
  switch (action.type) {
    // Edit
    case types.TICKET_EDIT:
      return {
        ...state,
        data: { ...state.data, ...action.payload },
        edited: true,
      };

    // Init
    case types.TICKET_INIT:
      return {
        ...state,
        data: { ...INITIAL_DATA },
        edited: true,
      };

    // Get list
    case types.GET_LIST_TICKET_REQUEST:
      return {
        ...state,
        isFetchingListTickets: true,
      };
    case types.GET_LIST_TICKET_SUCCESS:
      return {
        ...state,
        isFetchingListTickets: false,
        items: action.payload,
      };
    case types.GET_LIST_TICKET_FAILURE:
      return {
        ...state,
        isFetchingListTickets: false,
        items: [],
      };

    // Get
    case types.GET_TICKET_REQUEST:
      return {
        ...state,
        isFetchingTickets: true,
      };
    case types.GET_TICKET_SUCCESS:
      return {
        ...state,
        isFetchingTickets: false,
        data: action.payload.item,
        edited: false,
      };
    case types.GET_TICKET_FAILURE:
      return {
        ...state,
        isFetchingTickets: false,
        data: { ...INITIAL_DATA },
      };

    // Get ticket No
    case types.GET_TICKET_NO_REQUEST:
      return {
        ...state,
        isFetchingTicketsNo: true,
        ticket_ref_no: '',
      };
    case types.GET_TICKET_NO_SUCCESS:
      return {
        ...state,
        isFetchingTicketsNo: false,
        ticket_ref_no: action.payload.ticket_ref_no,
      };
    case types.GET_TICKET_NO_FAILURE:
      return {
        ...state,
        isFetchingTicketsNo: false,
        ticket_ref_no: '',
      };

    // Post
    case types.POST_TICKET_REQUEST:
      return {
        ...state,
        isPostingTickets: true,
      };
    case types.POST_TICKET_SUCCESS:
      return {
        ...state,
        isPostingTickets: false,
        edited: false,
      };
    case types.POST_TICKET_FAILURE:
      return {
        ...state,
        isPostingTickets: false,
      };

    // Put
    case types.PUT_TICKET_REQUEST:
      return {
        ...state,
        isPuttingTickets: true,
      };
    case types.PUT_TICKET_SUCCESS:
      return {
        ...state,
        isPuttingTickets: false,
        edited: false,
      };
    case types.PUT_TICKET_FAILURE:
      return {
        ...state,
        isPuttingTickets: false,
      };

    default:
      return state;
  }
};

export default ticketsReducer;
