import types from './types';
import api from 'constants/api';
import _get from 'lodash/get';
import moment from 'moment';

import { ThunkDispatch } from 'redux-thunk';

export const getListProviders = (data: any = {}) => (dispatch: ThunkDispatch<any, any, any>, getState: any) => {
  if (getState().rootReducer.providers.isFetchingList) { return Promise.reject(); }

  const params = new URLSearchParams();

  params.append('PageIndex', data?.SortByExpression || '1');
  params.append('PageSize', '10');
  params.append('SortByExpression', data?.SortByExpression || '');
  params.append('SortDirection', data?.SortDirection || 'asc');
  params.append('ProviderId', data?.ProviderId || '');
  params.append('LastName', data?.LastName || '');
  params.append('FirstName', data?.FirstName || '');
  params.append('CompanyId', data?.CompanyId || '');
  params.append('Speciality', data?.Speciality || '');
  params.append('City', data?.City || '');
  params.append('ProviderVendor', data?.ProviderVendor || '');
  params.append('HpCode', data?.HpCode || '');
  params.append('PcpLocation', data?.PcpLocation || '');
  params.append('Phone', data?.Phone || '');

  dispatch({ type: types.GET_LIST_PROVIDER_REQUEST, payload: data });

  return api.providers.getList(params)
    .then((res: any) => {
      let data: any = [...res];
      data.forEach((arr: any, index: number) => {
        let fromDate = moment(arr.RmiFromDate).format('MM/DD/YY');
        data[index] = {...arr, key: arr.Id, RmiFromDate: fromDate};
      });

      dispatch({
        type: types.GET_LIST_PROVIDER_SUCCESS,
        payload: data,
      });

      return res;
    })
    .catch((err: Error) => {
      const msg = _get(err, 'response.data.msg', 'Get List failed!');
      const errors = _get(err, 'response.data.errors', {});
      const payload = { ...err, msg, errors };

      dispatch({
        type: types.GET_LIST_PROVIDER_FAILURE,
        payload,
      });

      return Promise.reject(payload);
    });
};

export const getProviders = (data: any) => (dispatch: ThunkDispatch<any, any, any>, getState: any) => {
  if (getState().rootReducer.providers.isFetchingProviders) { return Promise.reject(); }

  dispatch({ type: types.GET_PROVIDERS_REQUEST, payload: data });

  return api.providers.findProviders(data)
    .then((res: any) => {

      dispatch({
        type: types.GET_PROVIDERS_SUCCESS,
        payload: res,
      });

      return res;
    })
    .catch((err: Error) => {
      const msg = _get(err, 'response.data.msg', 'Get failed!');
      const errors = _get(err, 'response.data.errors', {});
      const payload = { ...err, msg, errors };

      dispatch({
        type: types.GET_PROVIDERS_FAILURE,
        payload,
      });

      return Promise.reject(payload);
    });
};

export const editProviders = (data: any) => {
  return {
    type: types.PROVIDERS_EDIT,
    payload: data,
  };
};
