import React, { Fragment } from 'react';
import { bindActionCreators, Dispatch } from 'redux';
import { connect, RootStateOrAny } from 'react-redux';

import { Button, DatePicker, Input, Select } from 'antd';

import { editPgoMasters } from 'store/pgoDetails/sections/masters/actions';
import FloatLabel from 'components/Common/FloatLabel';
import {
  PGO_IPA_OPTIONS,
  PGO_PROVIDER_RELATION_HEALTH_PLAN, PGO_PROVIDER_RELATION_LINE_OF_BUSINESS,
  PGO_PROVIDER_RELATION_PRIORITY_STATUS, PGO_PROVIDER_RELATION_PROVIDER_OR_GROUP,
  PGO_PROVIDER_RELATION_REQUESTED_ACTION
} from 'constants/mockData/pgoDetails';
import moment from "moment";
import notifier from 'utils/notifier';

const { Option } = Select;

interface PgoDetailsProps {
  data: any;
  editPgoMasters: Function;
  handleGoToNextStep: Function;
}

const MastersPage = ({ data, editPgoMasters, handleGoToNextStep }: PgoDetailsProps) => {

  const handleChangeMasters = (key: string, val: any) => {
    editPgoMasters({ [key]: val });
  };

  const onNext = () => {
    if (data.relationApprovalHealthPlan !== '' && data.relationApprovalIpa !== '' && data.relationApprovalLineOfBusiness !== '' && data.relationApprovalNotes !== '' && data.relationApprovalPriorityStatus !== '' && data.relationApprovalProviderGroup !== '' && data.relationApprovalRequestedAction !== '' && data.relationApprovalRequestorName !== '') {
      handleGoToNextStep('3');
    } else {
      notifier.error('Please fill all input fields to continue !');
    }
  };

  return (
    <Fragment>
      <div className="row">
        <div className="col-md-8 select-red-label">
          <FloatLabel label="Requested Action" value={data.relationApprovalRequestedAction}>
            <Select value={data.relationApprovalRequestedAction}
                    onChange={(value) => handleChangeMasters('relationApprovalRequestedAction', value)}
                    className="w-100"
            >
              {PGO_PROVIDER_RELATION_REQUESTED_ACTION.map(i => (
                <Option key={i.key} value={i.key}>{i.label}</Option>
              ))}
            </Select>
          </FloatLabel>
        </div>

        <div className="col-md-8">
          <FloatLabel label="Priority Status" value={data.relationApprovalPriorityStatus}>
            <Select value={data.relationApprovalPriorityStatus}
                    onChange={(value) => handleChangeMasters('relationApprovalPriorityStatus', value)}
                    className="w-100"
            >
              {PGO_PROVIDER_RELATION_PRIORITY_STATUS.map(i => (
                <Option key={i.key} value={i.key}>{i.label}</Option>
              ))}
            </Select>
          </FloatLabel>
        </div>

        <div className="col-md-8">
          <FloatLabel label="Requestor Name" value={data.relationApprovalRequestorName}>
            <Input value={data.relationApprovalRequestorName} onChange={e => handleChangeMasters('relationApprovalRequestorName', e.target.value)} />
          </FloatLabel>
        </div>

        <div className="col-md-8">
          <FloatLabel label="Provider or Group" value={data.relationApprovalProviderGroup}>
            <Select value={data.relationApprovalProviderGroup}
                    onChange={(value) => handleChangeMasters('relationApprovalProviderGroup', value)}
                    className="w-100"
            >
              {PGO_PROVIDER_RELATION_PROVIDER_OR_GROUP.map(i => (
                <Option key={i.key} value={i.key}>{i.label}</Option>
              ))}
            </Select>
          </FloatLabel>
        </div>

        <div className="col-md-8">
          <FloatLabel label="Effect Date" value={data.relationApprovalEffectiveDate}>
            <DatePicker
              format="MM/DD/YYYY" suffixIcon={<i className="far fa-calendar-alt" />} placeholder=""
              value={data.relationApprovalEffectiveDate && moment(data.relationApprovalEffectiveDate)}
              onChange={(date, dateString) => handleChangeMasters('relationApprovalEffectiveDate', dateString)}
            />
          </FloatLabel>
        </div>

        <div className="col-md-8">
          <FloatLabel label="Acknowledgement Date" value={data.relationApprovalAcknowledgementDate}>
            <DatePicker
              format="MM/DD/YYYY" suffixIcon={<i className="far fa-calendar-alt" />} placeholder=""
              value={data.relationApprovalAcknowledgementDate && moment(data.relationApprovalAcknowledgementDate)}
              onChange={(date, dateString) => handleChangeMasters('relationApprovalAcknowledgementDate', dateString)}
            />
          </FloatLabel>
        </div>

        <div className="col-md-8">
          <FloatLabel label="Health Plan" value={data.relationApprovalHealthPlan}>
            <Select value={data.relationApprovalHealthPlan}
                    onChange={(value) => handleChangeMasters('relationApprovalHealthPlan', value)} className="w-100"
            >
              {PGO_PROVIDER_RELATION_HEALTH_PLAN.map(i => (
                <Option key={i.key} value={i.key}>{i.label}</Option>
              ))}
            </Select>
          </FloatLabel>
        </div>

        <div className="col-md-8">
          <FloatLabel label="Line of Business" value={data.relationApprovalLineOfBusiness}>
            <Select value={data.relationApprovalLineOfBusiness}
                    onChange={(value) => handleChangeMasters('relationApprovalLineOfBusiness', value)}
                    className="w-100"
            >
              {PGO_PROVIDER_RELATION_LINE_OF_BUSINESS.map(i => (
                <Option key={i.key} value={i.key}>{i.label}</Option>
              ))}
            </Select>
          </FloatLabel>
        </div>

        <div className="col-md-8">
          <FloatLabel label="IPA" value={data.relationApprovalIpa}>
            <Select value={data.relationApprovalIpa}
                    onChange={(value) => handleChangeMasters('relationApprovalIpa', value)} className="w-100"
            >
              {PGO_IPA_OPTIONS.map(i => (<Option key={i.key} value={i.key}>{i.label}</Option>))}
            </Select>
          </FloatLabel>
        </div>

        <div className="col-md-8">
          <FloatLabel label="Age Restriction" value={data.relationApprovalAgeRestriction}>
            <Input value={data.relationApprovalAgeRestriction} type="number"
                   onChange={e => handleChangeMasters('relationApprovalAgeRestriction', e.target.value)} />
          </FloatLabel>
        </div>

        <div className="col-md-8">
          <FloatLabel label="Membership" value={data.relationApprovalMemberShip}>
            <Input value={data.relationApprovalMemberShip}
                   onChange={e => handleChangeMasters('relationApprovalMemberShip', e.target.value)} />
          </FloatLabel>
        </div>

        <div className="col-md-8">
          <FloatLabel label="Open Auth" value={data.relationApprovalOpenAuth}>
            <Input value={data.relationApprovalOpenAuth}
                   onChange={e => handleChangeMasters('relationApprovalOpenAuth', e.target.value)} />
          </FloatLabel>
        </div>

        <div className="col-md-24">
          <FloatLabel label="Notes" value={data.relationApprovalNotes}>
            <Input.TextArea value={data.relationApprovalNotes} rows={5}
                            onChange={e => handleChangeMasters('relationApprovalNotes', e.target.value)} />
          </FloatLabel>
        </div>
      </div>

      <div className="row">
        <div className="col d-flex">
          <Button className="ml-auto waves-effect waves-light" onClick={() => handleGoToNextStep('1')}>Previous</Button>
          <Button type="primary" className="ml-3 waves-effect waves-light" onClick={onNext}>
            Next
          </Button>
        </div>
      </div>
    </Fragment>
  );
};

const mapStateToProps = (store: RootStateOrAny) => ({
  data: store.rootReducer.pgoMasters.data,
});

const mapDispatchToProps = (dispatch: Dispatch) => bindActionCreators(
  {
    editPgoMasters,
  },
  dispatch,
);

export default connect(mapStateToProps, mapDispatchToProps)(MastersPage);
