import React from 'react';
import { connect } from 'react-redux';
import Routes from 'containers/Routes';
import LoadingIndicator from 'components/Common/LoadingIndicator';

const App = ({ isFetchingMe, isPostingLogin, isStoredTokenVerified, ...rest }: any) => {
  if (isPostingLogin || !isStoredTokenVerified) {
    return (
      <div className="app">
        <LoadingIndicator isLoading={true} fullScreen />
      </div>
    );
  }

  return (
    <div className="app">
      <Routes {...rest} />
    </div>
  );
};

const mapStateToProps = (store: any) => ({
  isFetchingMe: store.rootReducer.auth.isFetchingMe,
  isPostingLogin: store.rootReducer.auth.isPostingLogin,
  isStoredTokenVerified: store.rootReducer.auth.isStoredTokenVerified,
});

export default connect(mapStateToProps)(App);
