import React, { useEffect, useState } from 'react';
import { bindActionCreators, Dispatch } from 'redux';
import { connect, RootStateOrAny } from 'react-redux';
import { useHistory } from "react-router-dom";

import { Typography, Collapse, Button } from 'antd';
import moment from 'moment';

import PgoAttachments from './sections/PgoAttachments';
import PgoCareManagements from './sections/PgoCareManagements';
import PgoDataManagements from './sections/PgoDataManagements';
import PgoEligibility from './sections/PgoEligibility';
import PgoGridOperations from './sections/PgoGridOperations';
import PgoMasters from './sections/PgoMasters';
import PgoReceivingProviderGroups from './sections/PgoReceivingProviderGroups';
import PgoRoutings from './sections/PgoRoutings';
import PgoTermingProviderGroups from './sections/PgoTermingProviderGroups';

import { initPgoDetails } from 'store/pgoDetails/actions';
import { postPgoMasters, initPgoMasters } from 'store/pgoDetails/sections/masters/actions';
import { postPgoCareManagements, initPgoCareManagements } from 'store/pgoDetails/sections/careManagements/actions';
import { postPgoAttachments, initPgoAttachments } from 'store/pgoDetails/sections/attachments/actions';
import { postPgoDataManagements, initPgoDataManagements } from 'store/pgoDetails/sections/dataManagements/actions';
import { postPgoEligibility, initPgoEligibility } from 'store/pgoDetails/sections/eligibility/actions';
import { postPgoGridOperations, initPgoGridOperations } from 'store/pgoDetails/sections/gridOperations/actions';
import { postPgoTermingProviderGroups, initPgoTermingProviderGroups} from 'store/pgoDetails/sections/termingProviderGroups/actions';
import { postPgoReceivingProviderGroups, initPgoReceivingProviderGroups } from 'store/pgoDetails/sections/receivingProviderGroups/actions';

import './index.scss';

const { Title } = Typography;
const { Panel } = Collapse;

interface PgoCreatePageProps {
  mastersData: any;
  attachmentsData: any;
  careManagementsData: any;
  dataManagementsData: any;
  eligibilityData: any;
  gridOperationsData: any;
  termingProviderData: any;
  receivingProviderData: any;
  routingData: any;
  postPgoCareManagements: Function;
  postPgoMasters: Function;
  postPgoAttachments: Function;
  postPgoDataManagements: Function;
  postPgoEligibility: Function;
  postPgoGridOperations: Function;
  postPgoTermingProviderGroups: Function;
  postPgoReceivingProviderGroups: Function;
  initPgoDetails: Function;
  initPgoCareManagements: Function;
  initPgoMasters: Function;
  initPgoAttachments: Function;
  initPgoDataManagements: Function;
  initPgoEligibility: Function;
  initPgoGridOperations: Function;
  initPgoTermingProviderGroups: Function;
  initPgoReceivingProviderGroups: Function;
}

const leftSideTree = ['1', '2', '3', '8', '9'];
const rightSideTree = ['4', '5', '6', '7'];

const PgoCreatePage = (
  {
    mastersData,
    attachmentsData,
    careManagementsData,
    dataManagementsData,
    eligibilityData,
    gridOperationsData,
    termingProviderData,
    receivingProviderData,
    routingData,
    postPgoCareManagements,
    postPgoMasters,
    postPgoAttachments,
    postPgoDataManagements,
    postPgoEligibility,
    postPgoGridOperations,
    postPgoTermingProviderGroups,
    postPgoReceivingProviderGroups,
    initPgoDetails,
    initPgoCareManagements,
    initPgoMasters,
    initPgoAttachments,
    initPgoDataManagements,
    initPgoEligibility,
    initPgoGridOperations,
    initPgoTermingProviderGroups,
    initPgoReceivingProviderGroups,
  }: PgoCreatePageProps) => {
  const history = useHistory();
  const [currentStep, setCurrentStep] = useState<Array<string>>([]);
  const [scrollStatus, setScrollStatus] = useState<boolean>(false);
  let intervalId: any;

  useEffect(() => {
    initPgoDetails();
    initPgoCareManagements();
    initPgoMasters();
    initPgoAttachments();
    initPgoDataManagements();
    initPgoEligibility();
    initPgoGridOperations();
    initPgoTermingProviderGroups();
    initPgoReceivingProviderGroups();
  }, []);

  const toLeft = () => {
    if (!scrollStatus) {
      setScrollStatus(true);
      intervalId = setInterval(LeftHandle, 5);
    }
  };

  const LeftHandle = () => {
    const smoothScroll: HTMLElement = document.getElementById('smooth_scroll') || new HTMLElement();
    smoothScroll.scrollLeft = smoothScroll.scrollLeft -20;

    if (smoothScroll.scrollLeft < 20) {
      smoothScroll.scrollLeft = 0;
      clearInterval(intervalId);
      setScrollStatus(false);
    }
  };

  const toRight = () => {
    if (!scrollStatus) {
      setScrollStatus(true);
      intervalId = setInterval(RightHandle, 5);
    }
  };

  const RightHandle = () => {
    const smoothScroll: HTMLElement = document.getElementById('smooth_scroll') || new HTMLElement();
    let scrollValue: number = smoothScroll.scrollLeft + 20;
    smoothScroll.scrollLeft = scrollValue;

    if (smoothScroll.scrollLeft < scrollValue-1) {
      clearInterval(intervalId);
      setScrollStatus(false);
    }
  };

  const handleGoToNextStep = (next: string) => {
    if (leftSideTree.find((step) => (step === next))) {
      toLeft();
    }

    if (rightSideTree.find((step) => (step === next))) {
      toRight();
    }

    if (!currentStep.find((step) => (step === next))) {
      setCurrentStep([...currentStep, next]);
    }
  };

  const onSubmit = () => {
    postPgoMasters(mastersData);
    postPgoCareManagements(careManagementsData);
    postPgoAttachments(attachmentsData);
    postPgoDataManagements(dataManagementsData);
    postPgoEligibility(eligibilityData);
    postPgoGridOperations(gridOperationsData);
    postPgoTermingProviderGroups(termingProviderData);
    postPgoReceivingProviderGroups(receivingProviderData);

    history.push('/pgo-details/new');
  };

  const onSaveDraft = () => {
    let id = moment().format('YYYYMMDDhhmmss');
    let draft = {
      id: id,
      mastersData,
      attachmentsData,
      careManagementsData,
      dataManagementsData,
      eligibilityData,
      gridOperationsData,
      termingProviderData,
      receivingProviderData,
      routingData
    };

    let pgoDrafts = JSON.parse(localStorage.getItem('pgoDrafts') || '[]');

    pgoDrafts = [...pgoDrafts, draft];

    localStorage.setItem('pgoDrafts', JSON.stringify(pgoDrafts));

    history.push('/pgo-details/new');
  };

  return (
    <div className="page pgo-create-page">
      <Title level={4}>Provider Grid Operations</Title>

      <div className="content">
        <div className="row">
          <div className="col-lg-24 col-md-24">
          <span className="pgo-progress mb-3">
            <div className="pgo-progress-bar" style={{ width: `${currentStep.length * 100 / 9}%` }} />
            <span className="pgo-progress-amount"><b>{Math.floor(currentStep.length * 100 / 9)}%</b></span>
          </span>
          </div>
        </div>

        <div className="row">
          <div className="col-24">
            <div className={"tree-container " + (currentStep.length === 0 ? 'justify-content-center' : '')} id="smooth_scroll">
              <div className={"mt-5 left-tree" + (currentStep.filter(st => leftSideTree.includes(st)).length !== 0 ? ' active' : '')}>
                <Collapse activeKey={currentStep} onChange={e => {
                  toLeft();
                  let current: string[] = typeof e === 'string' ? [] : e;
                  setCurrentStep(current);
                }}>

                  <Panel key="1" header="Terming Provider/Group" className="node-terming">
                    <PgoTermingProviderGroups handleGoToNextStep={handleGoToNextStep} />
                  </Panel>

                  <Panel key="2" header="Provider Relation Approval" className="node-approval">
                    <PgoMasters handleGoToNextStep={handleGoToNextStep} />
                  </Panel>

                  <Panel key="3" header="Receiving Provider/Group" className="node-receiving">
                    <PgoReceivingProviderGroups handleGoToNextStep={handleGoToNextStep} />
                  </Panel>

                  <Panel key="8" header="Attachments" className="node-attachment">
                    <PgoAttachments handleGoToNextStep={handleGoToNextStep} />
                  </Panel>

                  <Panel key="9" header="Routing" className="node-routing">
                    <PgoRoutings handleGoToNextStep={handleGoToNextStep} />
                  </Panel>

                </Collapse>
              </div>

              <div className="tree-trunk"/>

              <div className={"mt-5 right-tree" + (currentStep.filter(st => rightSideTree.includes(st)).length !== 0 ? ' active' : '')}>
                <Collapse activeKey={currentStep} onChange={e => {
                  toRight();
                  let current: string[] = typeof e === 'string' ? [] : e;
                  setCurrentStep(current);
                }}>

                  <Panel key="4" header="Eligibility" className="node-eligibility">
                    <PgoEligibility handleGoToNextStep={handleGoToNextStep} />
                  </Panel>

                  <Panel key="5" header="Care Management" className="node-care">
                    <PgoCareManagements handleGoToNextStep={handleGoToNextStep} />
                  </Panel>

                  <Panel key="6" header="Provider Data Management" className="node-data">
                    <PgoDataManagements handleGoToNextStep={handleGoToNextStep} />
                  </Panel>

                  <Panel key="7" header="Provider Grid Operation" className="node-grid">
                    <PgoGridOperations handleGoToNextStep={handleGoToNextStep} />
                  </Panel>

                </Collapse>
              </div>
            </div>
          </div>

          <div className="col-24">
            <div className="row mt-3">
              <div className="col d-flex">
                <Button type="primary" className="ml-auto waves-effect waves-light" onClick={onSaveDraft}>
                  Save Draft
                </Button>
                <Button className="ml-3 waves-effect waves-light" onClick={() => history.push('/pgo-details/new')}>
                  Cancel
                </Button>
                <Button type="primary" className="ml-3 waves-effect waves-light" onClick={onSubmit}>
                  Save
                </Button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = (store: RootStateOrAny) => ({
  edited: store.rootReducer.pgoMasters.edited,
  mastersData: store.rootReducer.pgoMasters.data,
  attachmentsData: store.rootReducer.pgoAttachments.data,
  careManagementsData: store.rootReducer.pgoCareManagements.data,
  dataManagementsData: store.rootReducer.pgoDataManagements.data,
  eligibilityData: store.rootReducer.pgoEligibility.data,
  gridOperationsData: store.rootReducer.pgoGridOperations.data,
  termingProviderData: store.rootReducer.pgoTermingProviderGroups.data,
  receivingProviderData: store.rootReducer.pgoReceivingProviderGroups.data,
  routingData: store.rootReducer.pgoDetails.data,
});

const mapDispatchToProps = (dispatch: Dispatch) => bindActionCreators({
  postPgoCareManagements,
  postPgoMasters,
  postPgoAttachments,
  postPgoDataManagements,
  postPgoEligibility,
  postPgoGridOperations,
  postPgoTermingProviderGroups,
  postPgoReceivingProviderGroups,
  initPgoDetails,
  initPgoCareManagements,
  initPgoMasters,
  initPgoAttachments,
  initPgoDataManagements,
  initPgoEligibility,
  initPgoGridOperations,
  initPgoTermingProviderGroups,
  initPgoReceivingProviderGroups,
},dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(PgoCreatePage);
