import types from './types';

export const INITIAL_PROVIDER_TEMPLATE = {
  npi: '',
  providerName: '',
  providerId: '',
  vendorEffectDate: '',
  vendorName: '',
  vendorId: '',
  licenseNo: '',
  specialty: '',
  providerClass: '',
  details: '',
};

const INITIAL_DATA = {
  routing_routeTo: '',
  routing_followUp: '',
  receivingProviders: [],
};

const INITIAL_STATE = {
  isPostingSave: false,
  data: { ...INITIAL_DATA },

  edited: false,
};

const pgoDetailsReducer = (state = INITIAL_STATE, action: any) => {
  switch (action.type) {
    case types.PGO_INIT_FORM:
      return {
        ...state,
        data: { ...INITIAL_DATA },
        edited: false,
      };

    case types.PGO_EDIT:
      return {
        ...state,
        data: { ...state.data, ...action.payload },
        edited: true,
      };

    default:
      return state;
  }
};

export default pgoDetailsReducer;
