import types from './types';

const INITIAL_MASTER_DATA = {
  id: '',
  deleted: '',
  createdDateUtc: '',
  modifiedDateUtc: '',
  relationApprovalRequestedAction: '',
  relationApprovalPriorityStatus: '',
  relationApprovalRequestorName: '',
  relationApprovalProviderGroup: '',
  relationApprovalEffectiveDate: '',
  relationApprovalAcknowledgementDate: '',
  relationApprovalHealthPlan: '',
  relationApprovalLineOfBusiness: '',
  relationApprovalIpa: '',
  relationApprovalAgeRestriction: '',
  relationApprovalMemberShip: '',// I added this field
  relationApprovalOpenAuth: '',// I added this field
  relationApprovalNotes: '',
  termingProviderGroupId: '',
};

const INITIAL_STATE = {
  isFetchingListMasters: false,
  isFetchingMasters: false,
  isPostingMasters: false,
  isPuttingMasters: false,

  data: {...INITIAL_MASTER_DATA},
  items: [],

  edited: false,
};

const pgoMastersReducer = (state = INITIAL_STATE, action: any) => {
  switch (action.type) {
    // Init
    case types.PGO_MASTER_INIT:
      return {
        ...state,
        data: { ...INITIAL_MASTER_DATA },
        edited: false,
      };

    // Edit
    case types.PGO_MASTER_EDIT:
      return {
        ...state,
        data: { ...state.data, ...action.payload },
        edited: true,
      };

    // Get list
    case types.GET_LIST_PGO_MASTER_REQUEST:
      return {
        ...state,
        isFetchingListMasters: true,
      };
    case types.GET_LIST_PGO_MASTER_SUCCESS:
      return {
        ...state,
        isFetchingListMasters: false,
        items: action.payload,
      };
    case types.GET_LIST_PGO_MASTER_FAILURE:
      return {
        ...state,
        isFetchingListMasters: false,
        items: [],
      };

    // Get
    case types.GET_PGO_MASTER_REQUEST:
      return {
        ...state,
        isFetchingMasters: true,
      };
    case types.GET_PGO_MASTER_SUCCESS:
      return {
        ...state,
        isFetchingMasters: false,
        data: action.payload,
        edited: false,
      };
    case types.GET_PGO_MASTER_FAILURE:
      return {
        ...state,
        isFetchingMasters: false,
        data: { ...INITIAL_MASTER_DATA },
      };

    // Post
    case types.POST_PGO_MASTER_REQUEST:
      return {
        ...state,
        isPostingMasters: true,
      };
    case types.POST_PGO_MASTER_SUCCESS:
      return {
        ...state,
        isPostingMasters: false,
        edited: false,
      };
    case types.POST_PGO_MASTER_FAILURE:
      return {
        ...state,
        isPostingMasters: false,
      };

    // Put
    case types.PUT_PGO_MASTER_REQUEST:
      return {
        ...state,
        isPuttingMasters: true,
      };
    case types.PUT_PGO_MASTER_SUCCESS:
      return {
        ...state,
        isPuttingMasters: false,
        edited: false,
      };
    case types.PUT_PGO_MASTER_FAILURE:
      return {
        ...state,
        isPuttingMasters: false,
      };

    default:
      return state;
  }
};

export default pgoMastersReducer;
