// API constants

//load config from server to make this environmental dependent.
//this is blocking
function loadConfigFromServer() {
  const xmlhttp = new XMLHttpRequest();

  xmlhttp.open('GET', window.location.origin + '/config.json', false);
  xmlhttp.send();

  if (xmlhttp.status === 200 && xmlhttp.readyState === 4) {
    return JSON.parse(xmlhttp.responseText);
  } else {
    // TODO Throw exception
    return null;
  }
}

const json = loadConfigFromServer();

let constants = {
  IS_PROD: json.IS_PROD,

  API_SERVER: json.API_SERVER,
  API_SERVER_NEW: json.API_SERVER_NEW,

  UMS_SERVER: json.UMS_SERVER,
  UMS_CLIENT_ID: json.UMS_CLIENT_ID,
  UMS_CLIENT_SECRET: json.UMS_CLIENT_SECRET,

  USE_FAKE_API: json.USE_FAKE_API,
  DEFAULT_PAGINATION_PER_PAGE: 50,
};

if (process.env.REACT_APP_USE_LOCAL_ENV === 'true') {
  constants = {
    IS_PROD: process.env.REACT_APP_IS_PROD || false,

    API_SERVER: process.env.REACT_APP_API_SERVER || 'http://qamsqueue-back.nmm.cc/api',
    API_SERVER_NEW: process.env.REACT_APP_API_SERVER_NEW || 'http://qainternalapi.nmm.cc',

    UMS_SERVER: process.env.REACT_APP_UMS_SERVER || 'https://qaums.nmm.cc',
    UMS_CLIENT_ID:  process.env.REACT_APP_UMS_CLIENT_ID || 'ng-client',
    UMS_CLIENT_SECRET:  process.env.REACT_APP_UMS_CLIENT_SECRET || 'ngngngng',

    USE_FAKE_API:  process.env.REACT_APP_USE_FAKE_API === 'true',
    DEFAULT_PAGINATION_PER_PAGE: 50,
  };
}

export default constants;
