const RESPONSE_DELAY = 1000;

const apiWrapper = (data: any) => new Promise((resolve, reject) => {
  setTimeout(() => {
    if ((data.status && data.status % 100 === 2) || data.status == null) {
      resolve(data);
    } else {
      reject(Error(
        JSON.stringify(
          {
            response: {
              status: data.status,
              data,
            },
          }),
      ));
    }
  }, RESPONSE_DELAY);
});

