import React from 'react';
import { debounce } from 'lodash';

import {
  Table,
  Modal,
  Button,
  Card, Input,
} from 'antd';

import api from 'constants/api';
import './index.scss';
import FloatLabel from '../../Common/FloatLabel';

interface ProviderSearchProps {
  onProviderSelect?: Function;
  isModal?: boolean;
  isModalVisible?: boolean;
  onCloseModal?: Function;
  children?: any;
}

const columns = [
  {
    title: 'Company Id',
    key: 'companyId',
    dataIndex: 'companyId',
  },
  {
    title: 'First Name',
    key: 'firstName',
    dataIndex: 'firstName',
  },
  {
    title: 'Last Name',
    key: 'lastName',
    dataIndex: 'lastName',
  },
  {
    title: 'Provider Id',
    key: 'lastName',
    dataIndex: 'lastName',
  },
];

const INITIAL_STATE = {
  showResult: false,
  searchResult: [],
  isSearchingNow: false,

  searchParams: {
    companyId: '',
    npi: '',
    providerName: '',
    providerRefId: '',
    vendorName: '',
    licenseNo: '',
  },

  pagination: {
    current: 0,
    pageSize: 5,
    total: 0,
  },
};

class ProviderSearch extends React.PureComponent<ProviderSearchProps> {
  state = {
    ...INITIAL_STATE,
    searchParams: { ...INITIAL_STATE.searchParams },
    pagination: { ...INITIAL_STATE.pagination },
  };

  componentDidUpdate(prevProps: Readonly<ProviderSearchProps>, prevState: Readonly<{}>, snapshot?: any) {
    // Opening modal, reset the state to initial state
    if (prevProps.isModalVisible !== this.props.isModalVisible && this.props.isModalVisible) {
      this.setState({
        ...INITIAL_STATE,
        searchParams: { ...INITIAL_STATE.searchParams },
        pagination: { ...INITIAL_STATE.pagination },
      });
    }
  }

  // Call API and display result
  doSearch = () => {
    this.setState({ isSearchingNow: true });

    api.providers.findProviders({
      ...this.state.searchParams,
      pageSize: this.state.pagination.pageSize,
      pageIndex: this.state.pagination.current,
    })
      .then(searchResult => {
        this.setState({
          searchResult,
          showResult: true,
          isSearchingNow: false,
        });
      })
      .catch(err => {
        console.log(err);

        this.setState({
          searchResult: [],
          isSearchingNow: false,
        });
      });
  };

  debouncedSearch = debounce(() => this.doSearch(), 400);

  // Update search parameters with user input
  updateSearchParams = (params: any) => {
    this.setState((prevState: any) => ({
      searchParams: {
        ...prevState.searchParams,
        ...params,
      },
      pagination: {
        ...INITIAL_STATE.pagination,
      },
    }), this.debouncedSearch);
  };

  // Change pagination
  handleChangeTable = (pagination: any) => {
    this.setState(prevState => ({
      pagination,
    }));
  };

  // Close modal
  handleCloseModal = () => {
    if (this.props.onCloseModal) {
      this.props.onCloseModal();
    }
  };

  gotoPrevPage = () => {
    this.setState((prevState: any) => ({
      pagination: {
        ...prevState.pagination,
        current: prevState.pagination.current - 1 || 0,
      },
    }));

    this.doSearch();
  };

  gotoNextPage = () => {
    this.setState((prevState: any) => ({
      pagination: {
        ...prevState.pagination,
        current: prevState.pagination.current + 1,
      },
    }));

    this.doSearch();
  };

  render() {
    const {
      children,
      onProviderSelect,
      isModal = false,
      isModalVisible = false,
    } = this.props;

    const {
      searchParams,
      showResult,
      searchResult,
      isSearchingNow,
      pagination,
    } = this.state;

    const childParams = {
      searchParams,
      updateSearchParams: this.updateSearchParams,
    };

    const renderPagination = (
      <div className="d-flex w-100 mt-2 mb-3">
        <span className="ml-auto">Page: {pagination.current} </span>
        <Button size="small" className="ml-3" disabled={isSearchingNow || pagination.current === 0}
                onClick={this.gotoPrevPage}>
          Previous
        </Button>
        <Button size="small" className="ml-1" disabled={isSearchingNow || searchResult.length < pagination.pageSize}
                onClick={this.gotoNextPage}>
          Next
        </Button>
      </div>
    );

    const renderTable = (
      <Table
        className="provider-search-result__table"
        loading={isSearchingNow}
        dataSource={searchResult}
        columns={columns}
        onChange={this.handleChangeTable}
        // pagination={pagination} - For now just use next and prev only
        pagination={false}
        onRow={(record, rowIndex) => ({
          onClick: event => {
            if (onProviderSelect) {
              onProviderSelect(record);
            }

            setTimeout(() => {
              this.setState({
                showResult: false,
              });
            }, 100);
          },
        })}
      />
    )

    return (
      <div className="provider-search-popup">
        {!!children && children(childParams)}

        {(isModal) &&
        <Modal
          visible={isModalVisible}
          title="Search Provider"
          onCancel={this.handleCloseModal}
          width={800}
          footer={[
            <Button key="back" onClick={this.handleCloseModal}>
              Return
            </Button>,
          ]}
        >
          <Card className="mb-3">
            <div className="row">
              <div className="col-md-8">
                <FloatLabel label="NPI" value={searchParams.npi}>
                  <Input value={searchParams.npi} onChange={e => this.updateSearchParams({ 'npi': e.target.value })} />
                </FloatLabel>
              </div>

              <div className="col-md-8">
                <FloatLabel label="Company Id" value={searchParams.companyId}>
                  <Input value={searchParams.companyId}
                         onChange={e => this.updateSearchParams({ 'companyId': e.target.value })} />
                </FloatLabel>
              </div>

              <div className="col-md-8">
                <FloatLabel label="Provider Name" value={searchParams.providerName}>
                  <Input value={searchParams.providerName}
                         onChange={e => this.updateSearchParams({ 'providerName': e.target.value })} />
                </FloatLabel>
              </div>

              <div className="col-md-8">
                <FloatLabel label="Provider Id" value={searchParams.providerRefId}>
                  <Input value={searchParams.providerRefId}
                         onChange={e => this.updateSearchParams({ 'providerRefId': e.target.value })} />
                </FloatLabel>
              </div>

              <div className="col-md-8">
                <FloatLabel label="Vendor Name" value={searchParams.vendorName}>
                  <Input value={searchParams.vendorName}
                         onChange={e => this.updateSearchParams({ 'vendorName': e.target.value })} />
                </FloatLabel>
              </div>

              <div className="col-md-8">
                <FloatLabel label="License No" value={searchParams.licenseNo}>
                  <Input value={searchParams.licenseNo}
                         onChange={e => this.updateSearchParams({ 'licenseNo': e.target.value })} />
                </FloatLabel>
              </div>
            </div>
          </Card>

          {renderTable}
          {renderPagination}
        </Modal>
        }

        {(!isModal && (showResult || isSearchingNow)) &&
        <>
          {renderTable}
          {renderPagination}
        </>
        }
      </div>
    );
  }

}

export default ProviderSearch;
