import types from './types';

const INITIAL_DATA = {
  patient_requestDate: '',
  patient_authorization: '',
  patient_authStatus: '',
  patient_authActionDate: '',
  patient_expiryDate: '',
  patient_healthPlanAuth: '',
  patient_memberId: '',
  patient_name: '',
  patient_dob: '',
  patient_gender: '',
  patient_healthPlan: '',
  patient_requestSource: '',
  patient_acmCase: '',
  patient_healthPlanCase: '',
  patient_ccs_ghdpCase: '',
  patient_primaryLanguage: '',

  referring_providerName: '',
  referring_providerId: '',
  referring_providerSpeciality: '',
  referring_phone: '',
  referring_fax: '',
  referring_address: '',

  provider_notes: [{id: 0, note: ''}],

  rendering_providerName: '',
  rendering_providerId: '',
  rendering_providerSpeciality: '',
  rendering_servicePlace: '',
  rendering_facilityName: '',
  rendering_contractStatus: '',
  rendering_providerClass: '',

  service_PH: '',
  service_place: '',
  service_quantityUnit: '',
  service_procedureCode: '',
  service_adjCode: '',
  service_diagnosis: '',
  service_admitDate: '',
  service_dischargeDate: '',

  careManagements: [
    {
      id: 0,
      care_subject: '',
      care_user: '',
      care_date: '',
      care_body: '',
    }
  ],

  audit_date: '',
  audit_user: '',
  audit_origValue: '',
  audit_newValue: '',

  attachments: [],
};

const INITIAL_SEARCH_DATA = {
  companyId: '',
  authNo: '',
  memberPcp: '',
  memberId: '',
  lastName: '',
  firstName: '',
  birth: '',
  referProvider: '',
  hpCode: '',
  authStatus: '',
  priorityStatus: '',
  speciality: '',
  referToProvider: '',
};

const INITIAL_ADVANCE_SEARCH_DATA = {
  fromDate: '',
  toDate: '',
  expiryFrom: '',
  expiryTo: '',
  servicePlace: '',
  diagCode: '',
  procedureCode: '',
  caseNo: '',
  hpAuthNo: '',
};

const INITIAL_STATE = {
  isPosting: false,
  isPutting: false,
  isFetching: false,
  isFetchingList: false,

  data: { ...INITIAL_DATA },

  searchData: { ...INITIAL_SEARCH_DATA },

  advanceSearchData: { ...INITIAL_ADVANCE_SEARCH_DATA },

  searchResult: [],

  edited: false,
};

const authorizationReducer = (state = INITIAL_STATE, action: any) => {
  switch (action.type) {
    case types.AUTHORIZATION_INIT_FORM:
      return {
        ...state,
        data: { ...INITIAL_DATA },
        edited: false,
      };

    case types.AUTHORIZATION_EDIT:
      return {
        ...state,
        data: { ...state.data, ...action.payload },
        edited: true,
      };

    case types.AUTHORIZATION_SEARCH_EDIT:
      return {
        ...state,
        searchData: { ...state.searchData, ...action.payload },
        edited: true,
      };

    case types.AUTHORIZATION_ADVANCE_SEARCH_EDIT:
      return {
        ...state,
        advanceSearchData: { ...state.advanceSearchData, ...action.payload },
        edited: true,
      };

    case types.AUTHORIZATION_SEARCH_INIT:
      return {
        ...state,
        searchData: { ...INITIAL_SEARCH_DATA },
        advanceSearchData: { ...INITIAL_ADVANCE_SEARCH_DATA },
        edited: false,
      };

    //Find
    case types.GET_AUTHORIZATION_LIST_REQUEST:
      return {
        ...state,
        searchResult: [],
        isFetchingList: true,
      };

    case types.GET_AUTHORIZATION_LIST_SUCCESS:
      return {
        ...state,
        searchResult: action.payload,
        isFetchingList: false,
      };

    case types.GET_AUTHORIZATION_LIST_FAILURE:
      return {
        ...state,
        searchResult: [],
        isFetchingList: false,
      };

    //Clear
    case types.GET_AUTHORIZATION_CLEAR_FAILURE:
      return {
        ...state,
        searchResult: [],
      };

    // Get
    case types.GET_AUTHORIZATION_REQUEST:
      return {
        ...state,
        isFetching: true,
      };
    case types.GET_AUTHORIZATION_SUCCESS:
      return {
        ...state,
        isFetching: false,
        data: action.payload,
        edited: false,
      };
    case types.GET_AUTHORIZATION_FAILURE:
      return {
        ...state,
        isFetching: false,
        data: { ...INITIAL_DATA },
      };

    // Post
    case types.POST_AUTHORIZATION_REQUEST:
      return {
        ...state,
        isPosting: true,
      };
    case types.POST_AUTHORIZATION_SUCCESS:
      return {
        ...state,
        isPosting: false,
        edited: false,
      };
    case types.POST_AUTHORIZATION_FAILURE:
      return {
        ...state,
        isPosting: false,
      };

    // Put
    case types.PUT_AUTHORIZATION_REQUEST:
      return {
        ...state,
        isPutting: true,
      };
    case types.PUT_AUTHORIZATION_SUCCESS:
      return {
        ...state,
        isPutting: false,
        edited: false,
      };
    case types.PUT_AUTHORIZATION_FAILURE:
      return {
        ...state,
        isPutting: false,
      };

    default:
      return state;
  }
};

export default authorizationReducer;
