import React, { useState } from 'react';
import { bindActionCreators, Dispatch } from 'redux';
import { connect, RootStateOrAny } from 'react-redux';
import { DatePicker, Input } from 'antd';
import moment from 'moment';

import './index.scss';

import FloatLabel from 'components/Common/FloatLabel';

import { getAuthorizationList, editAuthorizationSearch, editAuthorizationAdvanceSearch, initAuthorizationSearch } from 'store/authorization/actions'

interface AuthorizationSearchProps {
  getAuthorizationList: Function;
  editAuthorizationSearch: Function;
  editAuthorizationAdvanceSearch: Function;
  initAuthorizationSearch: Function;
  searchResult: any;
  searchData: any;
  advanceSearchData: any;
}

const AuthorizationSearchView = ({ getAuthorizationList, editAuthorizationSearch, editAuthorizationAdvanceSearch, initAuthorizationSearch, advanceSearchData, searchResult, searchData }: AuthorizationSearchProps) => {
  const [showAdvancedSearch, setShowAdvancedSearch] = useState(false);

  const handleChangeAuthorizations = (key: string, val: any) => {
    editAuthorizationSearch({ [key]: val });
  };

  const handleChangeAdvance = (key: string, val: any) => {
    editAuthorizationAdvanceSearch({ [key]: val });
  };

  const onSearch = () => {
    let pagination = {
      current: 0,
      pageSize: 5,
    };

    let data;

    if (showAdvancedSearch) {
      data = {...searchData, ...advanceSearchData};
    } else {
      data = {...searchData}
    }

    getAuthorizationList({
      ...data,
      pageSize: pagination.pageSize,
      pageIndex: pagination.current
    })
      .then(() => {
        console.log('search success!!!');
      })
      .catch((err : Error) => {
        console.log(err);
        searchResult = [];
      });
  };

  const onClear = () => {
    initAuthorizationSearch();
  };

  return (
    <div className="auth-search-view row flex-lg-wrap justify-content-start mb-3 pb-3 ml-0">
      <div className="col-lg-10 col-md-4">
        <FloatLabel label="Company Id" value={searchData.companyId}>
          <Input value={searchData.companyId} onChange={e => handleChangeAuthorizations('companyId', e.target.value)}/>
        </FloatLabel>
      </div>

      <div className="col-lg-10 col-md-4">
        <FloatLabel label="Auth No" value={searchData.authNo}>
          <Input value={searchData.authNo} onChange={e => handleChangeAuthorizations('authNo', e.target.value)} />
        </FloatLabel>
      </div>

      <div className="col-lg-4 col-md-4">
        <FloatLabel label="Member PCP: Search By Provider ID or Name" value={searchData.memberPcp}>
          <Input value={searchData.memberPcp} onChange={e => handleChangeAuthorizations('memberPcp', e.target.value)} />
        </FloatLabel>
      </div>

      <div className="col-lg-5 col-md-4">
        <FloatLabel label="Member ID" value={searchData.memberId}>
          <Input value={searchData.memberId} onChange={e => handleChangeAuthorizations('memberId', e.target.value)} />
        </FloatLabel>
      </div>

      <div className="col-lg-5 col-md-4">
        <FloatLabel label="Last Name" value={searchData.lastName}>
          <Input value={searchData.lastName} onChange={e => handleChangeAuthorizations('lastName', e.target.value)} />
        </FloatLabel>
      </div>

      <div className="col-lg-5 col-md-4">
        <FloatLabel label="First Name" value={searchData.firstName}>
          <Input value={searchData.firstName} onChange={e => handleChangeAuthorizations('firstName', e.target.value)} />
        </FloatLabel>
      </div>

      <div className="col-lg-5 col-md-4">
        <FloatLabel label="Date of Birth" value={searchData.birth}>
          <DatePicker
            format="MM/DD/YYYY" suffixIcon={<i className="far fa-calendar-alt" />} placeholder=""
            value={searchData.birth && moment(searchData.birth)}
            onChange={(date, dateString) => handleChangeAuthorizations('birth', dateString)}
          />
        </FloatLabel>
      </div>

      <div className="col-lg-4 col-md-4">
        <FloatLabel label="Refer by Provider" value={searchData.referProvider}>
          <Input value={searchData.referProvider} onChange={e => handleChangeAuthorizations('referProvider', e.target.value)} />
        </FloatLabel>
      </div>

      <div className="col-lg-5 col-md-4">
        <FloatLabel label="Hp Code" value={searchData.hpCode}>
          <Input value={searchData.hpCode} onChange={e => handleChangeAuthorizations('hpCode', e.target.value)} />
        </FloatLabel>
      </div>

      <div className="col-lg-5 col-md-4">
        <FloatLabel label="Auth Status" value={searchData.authStatus}>
          <Input value={searchData.authStatus} onChange={e => handleChangeAuthorizations('authStatus', e.target.value)} />
        </FloatLabel>
      </div>

      <div className="col-lg-5 col-md-4">
        <FloatLabel label="Priority Status" value={searchData.priorityStatus}>
          <Input value={searchData.priorityStatus} onChange={e => handleChangeAuthorizations('priorityStatus', e.target.value)} />
        </FloatLabel>
      </div>

      <div className="col-lg-5 col-md-4">
        <FloatLabel label="Speciality" value={searchData.speciality}>
          <Input value={searchData.speciality} onChange={e => handleChangeAuthorizations('speciality', e.target.value)} />
        </FloatLabel>
      </div>

      <div className="col-lg-4 col-md-4">
        <FloatLabel label="Refer to Provider" value={searchData.referToProvider}>
          <Input value={searchData.referToProvider} onChange={e => handleChangeAuthorizations('referToProvider', e.target.value)} />
        </FloatLabel>
      </div>

      <div className="col-lg-24 col-md-24 mb-3">
        {showAdvancedSearch ?
          <div className="btn-wrapper">
            <button className="authorization-search-btn" onClick={() => setShowAdvancedSearch(false)}>Hide Advanced Search Fields</button>
            <button className="authorization-search-btn" onClick={onClear}>Clear</button>
          </div> :
          <div className="btn-wrapper">
          <button className="authorization-search-btn" onClick={onSearch}>Search</button>
          <button className="authorization-search-btn" onClick={onClear}>Clear</button>
          <button className="authorization-search-btn" onClick={() => setShowAdvancedSearch(true)}>Show Advanced Search Fields</button>
          </div>
        }
      </div>

      {showAdvancedSearch &&
        <>
          <div className="col-lg-2 col-md-4">
            <b>Date Range:</b>
          </div>

          <div className="col-lg-5 col-md-10">
            <FloatLabel label="From" value={advanceSearchData.fromDate}>
              <DatePicker
                format="MM/DD/YYYY" suffixIcon={<i className="far fa-calendar-alt" />} placeholder=""
                value={advanceSearchData.fromDate && moment(advanceSearchData.fromDate)}
                onChange={(date, dateString) => handleChangeAdvance('fromDate', dateString)}
              />
            </FloatLabel>
          </div>

          <div className="col-lg-5 col-md-10">
            <FloatLabel label="To" value={advanceSearchData.toDate}>
              <DatePicker
                format="MM/DD/YYYY" suffixIcon={<i className="far fa-calendar-alt" />} placeholder=""
                value={advanceSearchData.toDate && moment(advanceSearchData.toDate)}
                onChange={(date, dateString) => handleChangeAdvance('toDate', dateString)}
              />
            </FloatLabel>
          </div>

          <div className="col-lg-2 col-md-4">
            <b>Expiration Date Range:</b>
          </div>

          <div className="col-lg-5 col-md-10">
            <FloatLabel label="From" value={advanceSearchData.expiryFrom}>
              <DatePicker
                format="MM/DD/YYYY" suffixIcon={<i className="far fa-calendar-alt" />} placeholder=""
                value={advanceSearchData.expiryFrom && moment(advanceSearchData.expiryFrom)}
                onChange={(date, dateString) => handleChangeAdvance('expiryFrom', dateString)}
              />
            </FloatLabel>
          </div>

          <div className="col-lg-5 col-md-10">
            <FloatLabel label="To" value={advanceSearchData.expiryTo}>
              <DatePicker
                format="MM/DD/YYYY" suffixIcon={<i className="far fa-calendar-alt" />} placeholder=""
                value={advanceSearchData.expiryTo && moment(advanceSearchData.expiryTo)}
                onChange={(date, dateString) => handleChangeAdvance('expiryTo', dateString)}
              />
            </FloatLabel>
          </div>

          <div className="col-lg-5 col-md-8">
            <FloatLabel label="Place of Service" value={advanceSearchData.servicePlace}>
              <Input value={advanceSearchData.servicePlace} onChange={e => handleChangeAdvance('servicePlace', e.target.value)} />
            </FloatLabel>
          </div>

          <div className="col-lg-5 col-md-8">
            <FloatLabel label="Diag Code" value={advanceSearchData.diagCode}>
              <Input value={advanceSearchData.diagCode} onChange={e => handleChangeAdvance('diagCode', e.target.value)} />
            </FloatLabel>
          </div>

          <div className="col-lg-5 col-md-8">
            <FloatLabel label="Procedure Code" value={advanceSearchData.procedureCode}>
              <Input value={advanceSearchData.procedureCode} onChange={e => handleChangeAdvance('procedureCode', e.target.value)} />
            </FloatLabel>
          </div>

          <div className="col-lg-5 col-md-8">
            <FloatLabel label="Case No" value={advanceSearchData.caseNo}>
              <Input value={advanceSearchData.caseNo} onChange={e => handleChangeAdvance('caseNo', e.target.value)} />
            </FloatLabel>
          </div>

          <div className="col-lg-4 col-md-8">
            <FloatLabel label="HP Auth No" value={advanceSearchData.hpAuthNo}>
              <Input value={advanceSearchData.hpAuthNo} onChange={e => handleChangeAdvance('hpAuthNo', e.target.value)} />
            </FloatLabel>
          </div>

          <div className="col-lg-24 col-md-24 mb-3">
            <div className="btn-wrapper">
              <button className="authorization-search-btn" onClick={onSearch}>Search</button>
            </div>
          </div>
        </>
      }
    </div>
  );
};

const mapStateToProps = (store: RootStateOrAny) => ({
  searchResult: store.rootReducer.authorization.searchResult,
  searchData: store.rootReducer.authorization.searchData,
  advanceSearchData: store.rootReducer.authorization.advanceSearchData
});

const mapDispatchToProps = (dispatch: Dispatch) => bindActionCreators(
  {
    getAuthorizationList,
    editAuthorizationSearch,
    editAuthorizationAdvanceSearch,
    initAuthorizationSearch
  },
  dispatch,
);

export default connect(mapStateToProps, mapDispatchToProps)(AuthorizationSearchView);
