import types from './types';
import { ThunkDispatch } from 'redux-thunk';
import api from 'constants/api';
import _get from 'lodash/get';

export const getListCategories = () => (dispatch: ThunkDispatch<any, any, any>, getState: any) => {
  if (getState().rootReducer.categories.isFetchingListCategories) { return Promise.reject(); }

  dispatch({ type: types.GET_LIST_CATEGORY_REQUEST });

  return api.categories.getList()
    .then((res: any) => {

      dispatch({
        type: types.GET_LIST_CATEGORY_SUCCESS,
        payload: res,
      });

      return res;
    })
    .catch((err: Error) => {
      const msg = _get(err, 'response.data.msg', 'Get List failed!');
      const errors = _get(err, 'response.data.errors', {});
      const payload = { ...err, msg, errors };

      dispatch({
        type: types.GET_LIST_CATEGORY_FAILURE,
        payload,
      });

      return Promise.reject(payload);
    });
};
