import React, { useEffect } from 'react';
import { bindActionCreators, Dispatch } from 'redux';
import { connect, RootStateOrAny } from 'react-redux';

import { Typography } from 'antd';

import { getListPgoMasters } from 'store/pgoDetails/sections/masters/actions';

import './index.scss';

const { Title } = Typography;

interface PgoSearchPageProps {
  edited: boolean;
  items: any;
  getListPgoMasters: Function;
}

const PgoSearchPage = ({ getListPgoMasters }: PgoSearchPageProps) => {

  useEffect(() => {
    getListPgoMasters();
  }, [getListPgoMasters]);

  return (
    <div className="page pgo-search-page">

      <div className="content">
        <Title level={4}>Pgo Search Page</Title>

      </div>
    </div>
  );
};

const mapStateToProps = (store: RootStateOrAny) => ({
  items: store.rootReducer.pgoMasters.items,
});

const mapDispatchToProps = (dispatch: Dispatch) => bindActionCreators(
  {
    getListPgoMasters
  },
  dispatch,
);

export default connect(mapStateToProps, mapDispatchToProps)(PgoSearchPage);
