import types from './types';
import { ThunkDispatch } from 'redux-thunk';
import api from 'constants/api';
import _get from 'lodash/get';

export const editCCSTickets = (data: any) => {
    return {
        type: types.CCS_TICKET_EDIT,
        payload: data,
    };
};

export const initCCSTickets = () => {
    return {
        type: types.CCS_TICKET_INIT
    };
};

export const getListCCSTickets = () => (dispatch:ThunkDispatch<any, any, any>, getState: any) => {
    if (getState().rootReducer.ccsTickets.isFetchingCCSListTickets) { return Promise.reject(); }

    dispatch({ type: types.GET_LIST_CCS_TICKET_REQUEST });

    return api.ccsTickets.getList()
        .then((res: any) => {

            dispatch({
                type: types.GET_LIST_CCS_TICKET_SUCCESS,
                payload: res,
            });

            return res;
        })
        .catch((err: Error) => {
            const msg = _get(err, 'response.data.msg', 'Get List failed!');
            const errors =  _get(err, 'response.data.errors', {});
            const payload = { ...err, msg, errors };

            dispatch({
                type: types.GET_LIST_CCS_TICKET_FAILURE,
                payload,
              });
        
            return Promise.reject(payload);
        });
};

export const getCCSTickets = (masterId: any) => (dispatch: ThunkDispatch<any, any, any>, getState: any) => {
    if (getState().rootReducer.ccsTickets.isFetchingCCSTickets) { return Promise.reject(); }

    dispatch({ type: types.GET_CCS_TICKET_REQUEST });
    
    return api.ccsTickets.get(masterId)
        .then((res: any) => {

            dispatch({
                type: types.GET_CCS_TICKET_SUCCESS,
                payload: res,
            });

            return res;
        })
        .catch((err: Error) => {
            const msg = _get(err, 'response.data.msg', 'Get failed!');
            const errors = _get(err, 'response.data.errors', {});
            const payload = { ...err, msg, errors };

            dispatch({
                type: types.GET_CCS_TICKET_FAILURE,
                payload,
            });

            return Promise.reject(payload);
        });
};

