export const getTicketsNo = () => (dispatch: ThunkDispatch<any, any, any>, getState: any) => {
    if (getState().rootReducer.ccsTickets.isFetchingCCSTicketsNo) { return Promise.reject(); }
  
    dispatch({ type: types.GET_TICKET_NO_REQUEST });
  
    return api.ccsTickets.getNo()
      .then((res: any) => {
  
        dispatch({
          type: types.GET_TICKET_NO_SUCCESS,
          payload: res,
        });
  
        return res;
      })
      .catch((err: Error) => {
        const msg = _get(err, 'response.data.msg', 'Get failed!');
        const errors = _get(err, 'response.data.errors', {});
        const payload = { ...err, msg, errors };
  
        dispatch({
          type: types.GET_TICKET_NO_FAILURE,
          payload,
        });
  
        return Promise.reject(payload);
      });
  };