import types from './types';
import { ThunkDispatch } from 'redux-thunk';
import api from 'constants/api';
import _get from 'lodash/get';

export const initPgoTermingProviderGroups = () => {
  return {
    type: types.PGO_TERMING_PROVIDER_GROUP_INIT,
  };
};

export const editPgoTermingProviderGroups = (data: any) => {
  return {
    type: types.PGO_TERMING_PROVIDER_GROUP_EDIT,
    payload: data,
  };
};

export const getListPgoTermingProviderGroups = () => (dispatch: ThunkDispatch<any, any, any>, getState: any) => {
  if (getState().rootReducer.pgoTermingProviderGroups.isFetchingListProviderGroups) { return Promise.reject(); }

  dispatch({ type: types.GET_LIST_PGO_TERMING_PROVIDER_GROUP_REQUEST });

  return api.pgoDetails.providerGroups.getList()
    .then((res: any) => {

      dispatch({
        type: types.GET_LIST_PGO_TERMING_PROVIDER_GROUP_SUCCESS,
        payload: res,
      });

      return res;
    })
    .catch((err: Error) => {
      const msg = _get(err, 'response.data.msg', 'Get List failed!');
      const errors = _get(err, 'response.data.errors', {});
      const payload = { ...err, msg, errors };

      dispatch({
        type: types.GET_LIST_PGO_TERMING_PROVIDER_GROUP_FAILURE,
        payload,
      });

      return Promise.reject(payload);
    });
};

