import * as React from 'react';
import { Button, Card, DatePicker, Input, Modal, Table } from 'antd';
import { debounce } from 'lodash'
import api from 'constants/api';
import FloatLabel from '../../Common/FloatLabel';
import moment from 'moment';


interface MemberSearchProps {
  onCloseModal?: Function;
  isModalVisible?: boolean;
  isModal?: boolean;
  onMemberSelect?: Function;
  children?: any;
}

const columns = [
  {
    title: 'Company Id',
    key: 'companyId',
    dataIndex: 'companyId',
  },
  {
    title: 'First Name',
    key: 'firstName',
    dataIndex: 'firstName',
  },
  {
    title: 'Last Name',
    key: 'lastName',
    dataIndex: 'lastName',
  },
  {
    title: 'Date of Birth',
    key: 'birth',
    dataIndex: 'birth',
  },
  {
    title: 'HP Code',
    key:'hpCode',
    dataIndex: 'hpCode',
  },
  {
    title: 'Member Id',
    key: 'memberRefId',
    dataIndex: 'memberRefId',
  },
];

const INITIAL_STATE = {
  showResult: false,
  searchResult: [],
  isSearchingNow: false,

  searchParams: {
    companyId: '',
    memberName: '',
    memberRefId: '',
    birth: null,
    hpCode: '',
  },

  pagination: {
    current: 0,
    pageSize: 5,
    total: 0,
  },
};

class MemberSearch extends React.PureComponent<MemberSearchProps> {
  state = {
    ...INITIAL_STATE,
    searchParams: { ...INITIAL_STATE.searchParams },
    pagination: { ...INITIAL_STATE.pagination },
  };

  componentDidUpdate(prevProps: Readonly<MemberSearchProps>, prevState: Readonly<{}>, snapshot?: any) {
    //Open modal, reset the state to initial state
    if (prevProps.isModalVisible !== this.props.isModalVisible && this.props.isModalVisible) {
      this.setState({
        ...INITIAL_STATE,
        searchParams: { ...INITIAL_STATE.searchParams },
        pagination: { ...INITIAL_STATE.pagination },
      });
    }
  }

  //Call API and display result
  doSearch = () => {
    this.setState({ isSearchingNow: true });

    api.members.findMembers({
      ...this.state.searchParams,
      pageSize: this.state.pagination.pageSize,
      pageIndex: this.state.pagination.current,
    })
      .then(searchResult => {
        this.setState({
          searchResult,
          showResult: true,
          isSearchingNow: false,
        });
      })
      .catch(err => {
        console.log(err);

        this.setState({
          searchResult: [],
          isSearchingNow: false,
        });
      });
  };

  debouncedSearch = debounce(() => this.doSearch(), 400);

  // Update search parameters with user input
  updateSearchParams = (params: any) => {
    this.setState((prevState: any) => ({
      searchParams: {
        ...prevState.searchParams,
        ...params,
      },
      pagination: {
        ...INITIAL_STATE.pagination,
      },
    }), this.debouncedSearch);
  };

  // Change pagination
  handleChangeTable = (pagination: any) => {
    this.setState(prevState => ({
      pagination,
    }));
  }

  // Close modal
  handleCloseModal = () => {
    if (this.props.onCloseModal) {
      this.props.onCloseModal();
    }
  };


  gotoPrevPage = () => {
    this.setState((prevState: any) => ({
      pagination: {
        ...prevState.pagination,
        current: prevState.pagination.current - 1 || 0,
      },
    }));
    this.doSearch();
  };

  gotoNextPage = () => {
    this.setState((prevState: any) => ({
      pagination: {
        ...prevState.pagination,
        current: prevState.pagination.current + 1,
      },
    }));

    this.doSearch();
  };


  render() {
    const { 
      isModalVisible,
      children,
      onMemberSelect,
      isModal,
    } = this.props;

    const {
      searchParams,
      showResult,
      searchResult,
      isSearchingNow,
      pagination,
    } = this.state;

    const childParams = {
      searchParams,
      updateSearchParams: this.updateSearchParams,
    };

    const renderPagination = (
      <div className="d-flex w-100 mt-2 mb-3">
        <span className="ml-auto">Page: {pagination.current} </span>
        <Button size="small" className="ml-3" disabled={isSearchingNow || pagination.current === 0}
                onClick={this.gotoPrevPage}>
          Previous
        </Button>
        <Button size="small" className="ml-1" disabled={isSearchingNow || searchResult.length < pagination.pageSize}
                onClick={this.gotoNextPage}>
          Next
        </Button>
      </div>
    );

    return (
      <div className="member-search-popup">
        <Modal
          visible={isModalVisible}
          title="Search Member"
          onCancel={this.handleCloseModal}
          width={800}
          footer={[
            <Button key="back" onClick={this.handleCloseModal}>
              Return
            </Button>,
          ]}
        >
          <Card className="mb-3">
              <div className="row">
                <div className="col-md-8">
                  <FloatLabel label="Company Id" value={searchParams.companyId}>
                    <Input value={searchParams.companyId}
                          onChange={e => this.updateSearchParams({ 'companyId': e.target.value })} />
                  </FloatLabel>
                </div>

                <div className="col-md-8">
                  <FloatLabel label="Member Name" value={searchParams.memberName}>
                    <Input value={searchParams.memberName}
                          onChange={e => this.updateSearchParams({ 'memberName': e.target.value })} />
                  </FloatLabel>
                </div>

                <div className="col-md-8">
                  <FloatLabel label="Member Id" value={searchParams.memberRefId}>
                    <Input value={searchParams.memberRefId}
                          onChange={e => this.updateSearchParams({ 'memberRefId': e.target.value })} />
                  </FloatLabel>
                </div>

                <div className="col-md-8">
                  <FloatLabel label="Date of Birth" value={searchParams.birth}>
                    <DatePicker
                    format="YYYY-MM-DD" suffixIcon={<i className="far fa-calendar-alt" />} placeholder="YYYY-MM-DD"
                    value={searchParams.birth && moment(searchParams.birth)}
                    onChange={(date, dateString) => this.updateSearchParams({ 'birth': dateString })} />
                  </FloatLabel>
                </div>
                
                <div className="col-md-8">
                  <FloatLabel label="HP Code" value={searchParams.hpCode}>
                    <Input value={searchParams.hpCode}
                          onChange={e => this.updateSearchParams({ 'hpCode': e.target.value })} />
                  </FloatLabel>
                </div>
              </div>
            </Card>

            <Table
              className="member-search-result__table"
              loading={isSearchingNow}
              dataSource={searchResult}
              columns={columns}
              onChange={this.handleChangeTable}
              // pagination={pagination} - For now just use next and prev only
              pagination={false}
              onRow={(record, rowIndex) => ({
                onClick: event => {
                  if (onMemberSelect) {
                    onMemberSelect(record);
                  }

                  setTimeout(() => {
                    this.setState({
                      showResult: false,
                    });
                  }, 100);
                },
              })}
            />
            {renderPagination}
        </Modal>
      </div>
    );
  }
}

export default MemberSearch;
