import types from './types';
import { ThunkDispatch } from "redux-thunk";
import api from 'constants/api';
import _get from "lodash/get";

export const initAuthorization = () => {
  return {
    type: types.AUTHORIZATION_INIT_FORM,
  };
};

export const editAuthorization = (data: any) => {
  return {
    type: types.AUTHORIZATION_EDIT,
    payload: data,
  };
};

export const editAuthorizationSearch = (data: any) => {
  return {
    type: types.AUTHORIZATION_SEARCH_EDIT,
    payload: data,
  };
};

export const editAuthorizationAdvanceSearch = (data: any) => {
  return {
    type: types.AUTHORIZATION_ADVANCE_SEARCH_EDIT,
    payload: data,
  };
};

export const initAuthorizationSearch = () => {
  return {
    type: types.AUTHORIZATION_SEARCH_INIT,
  };
};

export const getAuthorizationList = (data: any) => (dispatch: ThunkDispatch<any, any, any>, getState: any) => {
  if (getState().rootReducer.authorization.isFetchingList) { return Promise.reject(); }

  dispatch({ type: types.GET_AUTHORIZATION_LIST_REQUEST, payload: data });

  return api.authorization.findList(data)
    .then((res: any) => {

      dispatch({
        type: types.GET_AUTHORIZATION_LIST_SUCCESS,
        payload: res,
      });

      return res;
    })
    .catch((err: Error) => {
      const msg = _get(err, 'response.data.msg', 'Get failed!');
      const errors = _get(err, 'response.data.errors', {});
      const payload = { ...err, msg, errors };

      dispatch({
        type: types.GET_AUTHORIZATION_LIST_FAILURE,
        payload,
      });

      return Promise.reject(payload);
    });
};

export const getAuthorization = (id: any) => (dispatch: ThunkDispatch<any, any, any>, getState: any) => {
  if (getState().rootReducer.authorization.isFetching) { return Promise.reject(); }

  dispatch({ type: types.GET_AUTHORIZATION_REQUEST });

  return api.authorization.get(id)
    .then((res: any) => {

      dispatch({
        type: types.GET_AUTHORIZATION_SUCCESS,
        payload: res,
      });

      return res;
    })
    .catch((err: Error) => {
      const msg = _get(err, 'response.data.msg', 'Get failed!');
      const errors = _get(err, 'response.data.errors', {});
      const payload = { ...err, msg, errors };

      dispatch({
        type: types.GET_AUTHORIZATION_FAILURE,
        payload,
      });

      return Promise.reject(payload);
    });
};

