import types from './types';

const INITIAL_DATA = {
  "companyId": null,
  "type": "",
  "code": "",
  "description": "",
  "editable": false,
  "selectable": true,
  "id": "",
};

const INITIAL_STATE = {
  isFetchingListHPCodes: false,

  data: {...INITIAL_DATA},
  items: [],
};

const hpCodesReducer = (state = INITIAL_STATE, action: any) => {
  switch (action.type) {
    // Get list
    case types.GET_LIST_HPCODE_REQUEST:
      return {
        ...state,
        isFetchingListHPCodes: true,
      };
    case types.GET_LIST_HPCODE_SUCCESS:
      return {
        ...state,
        isFetchingListHPCodes: false,
        items: action.payload,
      };
    case types.GET_LIST_HPCODE_FAILURE:
      return {
        ...state,
        isFetchingListHPCodes: false,
        items: [],
      };

    default:
      return state;
  }
};

export default hpCodesReducer;
