import types from './types';
import constants from 'constants/index';

const { DEFAULT_PAGINATION_PER_PAGE } = constants;

const INITIAL_STATE = {
  isFetchingList: false,

  data: [],

  searchResult: [],
  isFetchingSearchResult: false,

  filters: {
    PageIndex: 1,
    PageSize: DEFAULT_PAGINATION_PER_PAGE,
    SortByExpression: '',
    SortDirection: 'asc',
    ProviderId: '',
    LastName: '',
    FirstName: '',
    CompanyId: '',
    Speciality: '',
    City: '',
    ProviderVendor: '',
    HpCode: '',
    PcpLocation: '',
    Phone: '',
  },
};

const providersReducer = (state = INITIAL_STATE, action: any) => {
  switch (action.type) {
    // Login
    case types.GET_LIST_PROVIDER_REQUEST:
      return {
        ...state,
        isFetchingList: true,
        data: [],
        filters: {
          ...state.filters,
          ...action.payload,
        },
      };
    case types.GET_LIST_PROVIDER_SUCCESS:
      return {
        ...state,
        isFetchingList: false,
        data: action.payload,
      };
    case types.GET_LIST_PROVIDER_FAILURE:
      return {
        ...state,
        isFetchingList: false,
        data: [],
      };

      case types.GET_PROVIDERS_REQUEST:
        return {
          ...state,
          searchResult: [],
          isFetchingSearchResult: true,
        }
      case types.GET_PROVIDERS_SUCCESS:
        return {
          ...state,
          searchResult: action.payload,
          isFetchingSearchResult: false,
        }
      case types.GET_PROVIDERS_FAILURE:
        return {
          ...state,
          searchResult: [],
          isFetchingSearchResult: false,
        }


    // Initial state
    default:
      return state;
  }
};

export default providersReducer;
