import React, { Fragment } from 'react';
import { bindActionCreators, Dispatch } from 'redux';
import { connect, RootStateOrAny } from 'react-redux';

import { Button, Select, Typography } from 'antd';

import { editPgoDetails } from 'store/pgoDetails/actions';
import FloatLabel from 'components/Common/FloatLabel';
import { PGO_ROUTING_OPTIONS } from 'constants/mockData/pgoDetails';

const { Option } = Select;
const { Text } = Typography;

interface PgoDetailsProps {
  data: any;
  editPgoDetails: Function;
  handleGoToNextStep: Function;
}

const RoutingsPage = ({ data, editPgoDetails, handleGoToNextStep }: PgoDetailsProps) => {

  const handleChange = (key: string, val: any) => {
    editPgoDetails({ [key]: val });
  };

  const onNext = () => {

  };

  return (
    <Fragment>
      <Text>Routing</Text>

      <div className="row">
        <div className="col-md-12">
          <FloatLabel label="Route To" value={data.routing_routeTo}>
            <Select value={data.routing_routeTo}
                    onChange={(value) => handleChange('routing_routeTo', value)}
                    className="w-100">
              {PGO_ROUTING_OPTIONS.map(i => (<Option key={i.key} value={i.key}>{i.label}</Option>))}
            </Select>
          </FloatLabel>
        </div>
        <div className="col-md-12">
          <FloatLabel label="Follow Up" value={data.routing_followUp}>
            <Select value={data.routing_followUp}
                    onChange={(value) => handleChange('routing_followUp', value)}
                    className="w-100">
              {PGO_ROUTING_OPTIONS.map(i => (<Option key={i.key} value={i.key}>{i.label}</Option>))}
            </Select>
          </FloatLabel>
        </div>
      </div>

      <div className="d-flex flex-column w-100 align-items-end">
        <Text>Non-Complete</Text>
        <Text>Urgent-24~48 Hours</Text>
        <Text>Routine 3~5 Business Days</Text><br />
      </div>

      <div className="row">
        <div className="col d-flex">
          <Button className="ml-auto waves-effect waves-light"
                  onClick={() => handleGoToNextStep('8')}>Previous</Button>
          <Button type="primary" className="ml-3 waves-effect waves-light" onClick={onNext}>Finish</Button>
        </div>
      </div>
    </Fragment>
  );
};

const mapStateToProps = (store: RootStateOrAny) => ({
  data: store.rootReducer.pgoDetails.data,
});

const mapDispatchToProps = (dispatch: Dispatch) => bindActionCreators(
  {
    editPgoDetails,
  },
  dispatch,
);

export default connect(mapStateToProps, mapDispatchToProps)(RoutingsPage);
