import types from './types';

const INITIAL_DATA = {
  "CATEGORY_ID": 0,
  "NAME": "",
  "CATEGORYS": [],
  "TAT": 0
};

const INITIAL_STATE = {
  isFetchingListCategories: false,

  data: {...INITIAL_DATA},
  items: [],
};

const categoriesReducer = (state = INITIAL_STATE, action: any) => {
  switch (action.type) {
    // Get list
    case types.GET_LIST_CATEGORY_REQUEST:
      return {
        ...state,
        isFetchingListCategories: true,
      };
    case types.GET_LIST_CATEGORY_SUCCESS:
      return {
        ...state,
        isFetchingListCategories: false,
        items: action.payload,
      };
    case types.GET_LIST_CATEGORY_FAILURE:
      return {
        ...state,
        isFetchingListCategories: false,
        items: [],
      };

    default:
      return state;
  }
};

export default categoriesReducer;
