import React, { useEffect, Fragment } from 'react';
import { bindActionCreators, Dispatch } from 'redux';
import { connect, RootStateOrAny } from 'react-redux';

import { Button, Upload } from 'antd';
import { InboxOutlined } from '@ant-design/icons';

import { getPgoAttachments, editPgoAttachments, putPgoAttachments } from 'store/pgoDetails/sections/attachments/actions';

const { Dragger } = Upload;

interface PgoDetailsProps {
  edited: boolean;
  data: any;
  editPgoAttachments: Function;
  getPgoAttachments: Function;
  putPgoAttachments: Function;
  handleGoToNextStep: Function;
  masterId: any;
}

const AttachmentsPage = ({ edited, data, getPgoAttachments, editPgoAttachments, putPgoAttachments, handleGoToNextStep, masterId }: PgoDetailsProps) => {

  useEffect(() => {
    getPgoAttachments(masterId);
  }, [getPgoAttachments, masterId]);

  const handleChangeAttachments = (key: string, val: any) => {
    editPgoAttachments({ [key]: val });
  };

  return (
    <Fragment>
      <Dragger>
        <p className="ant-upload-drag-icon">
          <InboxOutlined />
        </p>
        <p className="ant-upload-text">Click or drag file to this area to upload</p>
      </Dragger>

      <div className="row">
        <div className="col d-flex">
          <Button className="ml-auto waves-effect waves-light" onClick={() => handleGoToNextStep('7')}>Previous</Button>
          <Button type="primary" className="ml-3 waves-effect waves-light"
                  onClick={() => handleGoToNextStep('9')}>Next</Button>
        </div>
      </div>
    </Fragment>
  );
};

const mapStateToProps = (store: RootStateOrAny) => ({
  edited: store.rootReducer.pgoAttachments.edited,
  data: store.rootReducer.pgoAttachments.data,
});

const mapDispatchToProps = (dispatch: Dispatch) => bindActionCreators(
  {
    getPgoAttachments,
    editPgoAttachments,
    putPgoAttachments
  },
  dispatch,
);

export default connect(mapStateToProps, mapDispatchToProps)(AttachmentsPage);
