import React, { useState, KeyboardEvent } from 'react';
import { bindActionCreators, Dispatch } from 'redux';
import { connect, RootStateOrAny } from 'react-redux';

import { RouteComponentProps } from 'react-router';
import { Link } from 'react-router-dom';

import { Card, Form, Input, Button, Typography } from 'antd';
import { UserOutlined, LockOutlined } from '@ant-design/icons';

import { login } from 'store/auth/actions';

import './style.scss';
import notifier from 'utils/notifier';

const { Title } = Typography;

type LoginPageProps = RouteComponentProps<any> & {
  login: Function,
  isPostingLogin: boolean,
}

const LoginPage = ({ history, login, isPostingLogin }: LoginPageProps) => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');

  const handleLogin = () => {
    if (username !== '' && password !== '') {
      let formData = {
        username,
        password,
      };

      login(formData).then(() => {
        history.push('/dashboard');
      }).catch((error: Error) => {
        notifier.error(error.message || 'Username or password is incorrect.');
      });
    } else {
      notifier.error('Please enter username and password.');
    }
  };

  const handlePasswordInputKeypress = (e: KeyboardEvent<HTMLInputElement>) => {
    if (e.nativeEvent.key === 'Enter') {
      handleLogin();
    }
  };

  return (
    <div className="page login-page">
      <Card className="login-form">
        <Title level={4}>User login</Title>
        <div className="login-form-body">
          <Form.Item>
            <Input
              prefix={<UserOutlined className="site-form-item-icon" />}
              placeholder="Username"
              value={username}
              onChange={(e) => setUsername(e.currentTarget.value)}
            />
          </Form.Item>

          <Form.Item>
            <Input
              prefix={<LockOutlined className="site-form-item-icon" />}
              type="password"
              placeholder="Password"
              value={password}
              onChange={(e) => setPassword(e.currentTarget.value)}
              onKeyPress={handlePasswordInputKeypress}
            />
          </Form.Item>

          <div className="d-flex align-items-center">
            <Link className="login-form-forgot mr-auto" to="/forgot-password">
              Forgot password
            </Link>

            <Button type="primary" className="login-form-button waves-effect waves-light" onClick={handleLogin} loading={isPostingLogin}>
              Log in
            </Button>
          </div>
        </div>
      </Card>
    </div>
  );
};

const mapStateToProps = (store: RootStateOrAny) => ({
  isPostingLogin: store.rootReducer.auth.isPostingLogin,
});

const mapDispatchToProps = (dispatch: Dispatch) => bindActionCreators(
  {
    login,
  },
  dispatch,
);

export default connect(mapStateToProps, mapDispatchToProps)(LoginPage);
