const INITIAL_STATE = {
    isFetchingListCCSTickets: false,
    isFetchingCCSTickets: false,
    isPostingCCSTickets: false,
    isPuttingTickets: false,

    data: {...INITIAL_DATA},
    items: [],
    edited: false,
};

const ccsTicketsReducer = (state = INITIAL_STATE, action: any) => {
    switch (action.type) {
        // Edit
        case types.CCS_TICKET_EDIT:
            return {
                ...state,
                data: { ...state.data, ...action.payload },
                edited: true,
            };
        
        // Init
        case types.CCS_TICKET_INIT:
            return {
                ...state,
                data: { ...INITIAL_DATA },
                edited: true,
            };
        
        // Get list
        case types.GET_LIST_CCS_TICKET_REQUEST:
            return {
                ...state,
                isFetchingListCCSTickets: true,
            };       
        case types.GET_LIST_CCS_TICKET_SUCCESS:
            return {
                ...state,
                isFetchingListCCSTickets: false,
                items: action.payload,
            };
        case types.GET_LIST_CCS_TICKET_FAILURE:
            return {
                ...state,
                isFetchingCCSListTickets: false,
                items: [],
            };
        
        // Get
        case types.GET_CCS_TICKET_REQUEST:
            return {
                ...state,
                isFetchingCCSTickets: true,
            };
        case types.GET_CCS_TICKET_SUCCESS:
            return {
                ...state,
                isFetchingCCSTickets: false,
                data: action.payload.item,
                edited: false,
            };
        case types.GET_CCS_TICKET_FAILURE:
            return {
                ...state,
                isFetchingCCSTickets: false,
                data: { ...INITIAL_DATA },
            };
        
        // Post
        case types.POST_CCS_TICKET_REQUEST:
            return {
                ...state,
                isPostingCCSTickets: true,
            };
        case types.POST_CCS_TICKET_SUCCESS:
            return {
                ...state,
                isPostingCCSTickets: false,
                edited: false,
            };
        case types.POST_CCS_TICKET_FAILURE:
            return {
                ...state,
                isPostingCCSTickets: false,
            };
        
        // Put
        case types.PUT_CCS_TICKET_REQUEST:
            return {
                ...state,
                isPuttingCCSTickets: true,
            };
        case types.PUT_CCS_TICKET_SUCCESS:
            return {
                ...state,
                isPuttingCCSTickets: false,
                edited: false,
            };
        case types.PUT_CCS_TICKET_FAILURE:
            return {
                ...state,
                isPuttingCCSTickets: false,
            };
        
        default:
            return state;
    }
};

export default ccsTicketsReducer;