import React, { useEffect, useState } from 'react';
import { bindActionCreators, Dispatch } from 'redux';
import { connect, RootStateOrAny } from 'react-redux';
import { Input, Select } from 'antd';

import { getListCompanies } from 'store/companies/actions';

import './index.scss';

import FloatLabel from 'components/Common/FloatLabel';
import { NOTIMP } from 'node:dns';

import api from 'constants/api';
//import ProviderSearch from 'components/Provider/ProviderSearch'
import { editProviders, getProviders } from 'store/providers/actions'

import { SearchOutlined } from '@ant-design/icons';

interface ProviderSearchProps {
  getProviders: Function;
  searchResult: any;
}

const { Option } = Select;

const ProviderSearchView = ({ getProviders, searchResult }: ProviderSearchProps) => {
  const [companyId, setCompanyId] = useState<string>('');
  const [providerId, setProviderId] = useState<string>('');
  const [providername, setProviderName] = useState<string>('');
  const [vendor, setVendor] = useState<string>('');
  const [npi, setNpi] = useState<string>('');
  const [licenseno, setLicenseNo] = useState<string>('');
  //const [showResult, setShowResult] = useState(false);
  //const [searchResult, setSearchResult] = useState([]);

  /*const handleChangeProviders = (key: string, val: any) => {
    editProviders({ [key]: val });
  };

  const handleProviderSelect = (data: any) => {
    setNpi(data?.npi || '');
    setProviderName(`${data?.firstName} ${data?.lastName}` || '');
    setProviderId(data?.providerRefId || '');
    setVendor(data?.vendorName || '');
    setLicenseNo(data?.licenseNo || '');

    //setShowResult(false);
  };*/

  const onSearch = () => {
    let form_data = {
      providerRefId: providerId,
      providerName: providername,
      companyId: companyId,
      vendorName: vendor,
      licenseNo: licenseno,
      npi: npi
    };

    let pagination = {
      current: 0,
      pageSize: 5,
    }

    getProviders({
      ...form_data,
      pageSize: pagination.pageSize,
      pageIndex: pagination.current
    })
      .then((searchResult : any) => {
        searchResult = searchResult;
      })
      .catch((err : Error) => {
        console.log(err);
        searchResult = [];
      });
    //getListProviders(form_data);

  };

  return (
    <div className="provider-search-view row flex-lg-wrap justify-content-start mb-3 ml-0">
      <div className="col-lg-6 col-md-4">
        <FloatLabel label="Company Id" value={companyId}>
          <Input
            value={companyId}
            onChange={e => setCompanyId(e.target.value)}
            className="w-100"
          >
          </Input>
        </FloatLabel>
      </div>

      <div className="col-lg-4 col-md-4">
        <FloatLabel label="NPI" value={npi}>
          <Input
            value={npi}
            onChange={e => setNpi(e.target.value)}
          />
        </FloatLabel>
      </div>

      <div className="col-lg-6 col-md-4">
        <FloatLabel label="Provider ID" value={providerId}>
          <Input
            value={providerId}
            onChange={e => setProviderId(e.target.value)}
          />
        </FloatLabel>
      </div>

      <div className="col-lg-6 col-md-4">
        <FloatLabel label="Provider Name" value={providername}>
          <Input
            value={providername}
            onChange={e => setProviderName(e.target.value)}
          />
        </FloatLabel>
      </div>

      <div className="col-lg-6 col-md-4">
        <FloatLabel label="Vendor" value={vendor}>
          <Input
            value={vendor}
            onChange={e => setVendor(e.target.value)}
          />
        </FloatLabel>
      </div>

      <div className="col-lg-4 col-md-4">
        <FloatLabel label="License No." value={licenseno}>
          <Input
            value={licenseno}
            onChange={e => setLicenseNo(e.target.value)}
          />
        </FloatLabel>
      </div>

      <div className="col-lg-4 col-md-8">
        <div className="btn-wrapper">
          <button className="provider-search-btn" onClick={onSearch}>SEARCH</button>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = (store: RootStateOrAny) => ({
  //companies: store.rootReducer.companies.data,
  searchResult: store.rootReducer.providers.searchResult
});

const mapDispatchToProps = (dispatch: Dispatch) => bindActionCreators(
  {
    getProviders,
  },
  dispatch,
);

export default connect(mapStateToProps, mapDispatchToProps)(ProviderSearchView);
