import React, { useEffect, Fragment } from 'react';
import { bindActionCreators, Dispatch } from 'redux';
import { connect, RootStateOrAny } from 'react-redux';
import moment from 'moment';

import { DatePicker, Input, Button } from 'antd';

import FloatLabel from 'components/Common/FloatLabel';
import {
  getPgoCareManagements,
  editPgoCareManagements,
  putPgoCareManagements
} from 'store/pgoDetails/sections/careManagements/actions';
import notifier from 'utils/notifier';

interface PgoDetailsProps {
  edited: boolean;
  data: any;
  editPgoCareManagements: Function;
  getPgoCareManagements: Function;
  putPgoCareManagements: Function;
  handleGoToNextStep: Function;
  masterId: any;
}

const CareManagementsPage = ({ edited, data, getPgoCareManagements, editPgoCareManagements, putPgoCareManagements, handleGoToNextStep, masterId }: PgoDetailsProps) => {

  useEffect(() => {
    getPgoCareManagements(masterId);
  }, [getPgoCareManagements, masterId]);

  const handleChangeCareManagements = (key: string, val: any) => {
    editPgoCareManagements({ [key]: val });
  };

  const onNext = () => {
    if (data.body !== '' && data.subject !== '' && data.user !== '') {
      putPgoCareManagements(data);
      handleGoToNextStep('6');
    } else {
      notifier.error('Please fill all input fields to continue !');
    }
  };

  return (
    <Fragment>
      <div className="row">
        <div className="col-md-8">
          <FloatLabel label="Subject" value={data.subject}>
            <Input value={data.subject} onChange={e => handleChangeCareManagements('subject', e.target.value)} />
          </FloatLabel>
        </div>
        <div className="col-md-8">
          <FloatLabel label="User" value={data.user}>
            <Input value={data.user} onChange={e => handleChangeCareManagements('user', e.target.value)} />
          </FloatLabel>
        </div>

        <div className="col-md-8">
          <FloatLabel label="Date/Time" value={data.createdDateUtc}>
            <DatePicker
              format="MM/DD/YYYY" suffixIcon={<i className="far fa-calendar-alt" />} placeholder=""
              value={data.createdDateUtc && moment(data.createdDateUtc)}
              onChange={(date, dateString) => handleChangeCareManagements('createdDateUtc', dateString)}
            />
          </FloatLabel>
        </div>

        <div className="col-md-24">
          <FloatLabel label="Notes" value={data.body}>
            <Input.TextArea value={data.body} rows={3} onChange={e => handleChangeCareManagements('body', e.target.value)} />
          </FloatLabel>
        </div>
      </div>

      <div className="row">
        <div className="col d-flex">
          <Button className="ml-auto waves-effect waves-light" onClick={() => handleGoToNextStep('4')}>Previous</Button>
          <Button type="primary" className="ml-3 waves-effect waves-light" onClick={onNext}>
            Next
          </Button>
        </div>
      </div>
    </Fragment>
  );
};

const mapStateToProps = (store: RootStateOrAny) => ({
  edited: store.rootReducer.pgoCareManagements.edited,
  data: store.rootReducer.pgoCareManagements.data,
});

const mapDispatchToProps = (dispatch: Dispatch) => bindActionCreators(
  {
    getPgoCareManagements,
    editPgoCareManagements,
    putPgoCareManagements
  },
  dispatch,
);

export default connect(mapStateToProps, mapDispatchToProps)(CareManagementsPage);
