import React, { useEffect, useState } from 'react';
import { bindActionCreators, Dispatch } from 'redux';
import { connect, RootStateOrAny } from 'react-redux';
import { DatePicker, Input, Select } from 'antd';

import { getListCompanies } from 'store/companies/actions';

import './index.scss';

import FloatLabel from 'components/Common/FloatLabel';
import { NOTIMP } from 'node:dns';

import api from 'constants/api';
//import AuthSearch from 'components/Member/AuthSearch'
import { getMembers } from 'store/members/actions'
import moment, { Moment } from 'moment';

import { SearchOutlined } from '@ant-design/icons';

interface MemberSearchProps {
  getMembers: Function;
  searchResult: any;
}

const { Option } = Select;

const MemberSearchView = ({ getMembers, searchResult }: MemberSearchProps) => {
  const [companyId, setCompanyId] = useState<string>('');
  const [memberId, setMemberId] = useState<string>('');
  const [membername, setMemberName] = useState<string>('');
  const [hpcode, setHpCode] = useState<string>('');
  //const [birthdate, setBirthDate] = useState<Moment | null>();
  const [birthdate, setBirthDate] = useState<string>('');
  //const [showResult, setShowResult] = useState(false);
  //const [searchResult, setSearchResult] = useState([]);

  /*const handleChangeMembers = (key: string, val: any) => {
    editMembers({ [key]: val });
  };

  const handleMemberSelect = (data: any) => {
    setNpi(data?.npi || '');
    setMemberName(`${data?.firstName} ${data?.lastName}` || '');
    setMemberId(data?.MemberRefId || '');
    setVendor(data?.vendorName || '');
    setLicenseNo(data?.licenseNo || '');

    //setShowResult(false);
  };*/

  const onSearch = () => {
    let form_data = {
      memberRefId: memberId,
      memberName: membername,
      companyId: companyId,
      hpCode: hpcode,
      birth: birthdate == '' ? null : birthdate
    };

    let pagination = {
      current: 0,
      pageSize: 5,
    }

    getMembers({
      ...form_data,
      pageSize: pagination.pageSize,
      pageIndex: pagination.current
    })
      .then((searchResult : any) => {
        searchResult = searchResult;
      })
      .catch((err : Error) => {
        console.log(err);
        searchResult = [];
      });
  };

  return (
    <div className="member-search-view row flex-lg-wrap justify-content-start mb-3 ml-0">
      <div className="col-lg-6 col-md-4">
        <FloatLabel label="Company Id" value={companyId}>
          <Input
            value={companyId}
            onChange={e => setCompanyId(e.target.value)}
            className="w-100"
          >
          </Input>
        </FloatLabel>
      </div>

      <div className="col-lg-4 col-md-4">
        <FloatLabel label="Member Name" value={membername}>
          <Input
            value={membername}
            onChange={e => setMemberName(e.target.value)}
          />
        </FloatLabel>
      </div>

      <div className="col-lg-6 col-md-4">
        <FloatLabel label="Member ID" value={memberId}>
          <Input
            value={memberId}
            onChange={e => setMemberId(e.target.value)}
          />
        </FloatLabel>
      </div>

      <div className="col-lg-6 col-md-4">
        <FloatLabel label="HP Code" value={hpcode}>
          <Input
            value={hpcode}
            onChange={e => setHpCode(e.target.value)}
          />
        </FloatLabel>
      </div>

      <div className="col-lg-6 col-md-4">
        <FloatLabel label="Date of Birth (YYYY-MM-DD)" value={birthdate}>
          <Input
            value={birthdate}
            onChange={e => setBirthDate(e.target.value)}
          />
        </FloatLabel>
      </div>

      <div className="col-lg-4 col-md-8">
        <div className="btn-wrapper">
          <button className="member-search-btn" onClick={onSearch}>SEARCH</button>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = (store: RootStateOrAny) => ({
  searchResult: store.rootReducer.members.searchResult
});

const mapDispatchToProps = (dispatch: Dispatch) => bindActionCreators(
  {
    getMembers,
  },
  dispatch,
);

export default connect(mapStateToProps, mapDispatchToProps)(MemberSearchView);
