import types from './types';

const INITIAL_RECEIVING_PROVIDER_GROUP_DATA = {
  id: '',
  deleted: '',
  createdDateUtc: '',
  modifiedDateUtc: '',
  pgoMasterId: '',
  npi: '',
  providerName: '',
  providerId: '',
  vendorEffectiveDate: '',
  vendorName: '',
  vendorId: '',
  licenseNumber: '',
  specialty: '',
  providerClass: '',
  details: ''
};

const INITIAL_STATE = {
  isFetchingListReceivingProviderGroups: false,
  isFetchingReceivingProviderGroups: false,
  isPostingReceivingProviderGroups: false,
  isPuttingReceivingProviderGroups: false,

  data: {...INITIAL_RECEIVING_PROVIDER_GROUP_DATA},
  items: [{...INITIAL_RECEIVING_PROVIDER_GROUP_DATA}],

  edited: false,
};

const pgoReceivingProviderGroupsReducer = (state = INITIAL_STATE, action: any) => {
  switch (action.type) {
    // Init
    case types.PGO_RECEIVING_PROVIDER_GROUP_INIT:
      return {
        ...state,
        data: { ...INITIAL_RECEIVING_PROVIDER_GROUP_DATA },
        edited: false,
      };

    // Edit
    case types.PGO_RECEIVING_PROVIDER_GROUP_EDIT:
      return {
        ...state,
        data: { ...state.data, ...action.payload },
        edited: true,
      };

    // Get list
    case types.GET_LIST_PGO_RECEIVING_PROVIDER_GROUP_REQUEST:
      return {
        ...state,
        isFetchingListReceivingProviderGroups: true,
      };
    case types.GET_LIST_PGO_RECEIVING_PROVIDER_GROUP_SUCCESS:
      return {
        ...state,
        isFetchingListReceivingProviderGroups: false,
        items: action.payload,
      };
    case types.GET_LIST_PGO_RECEIVING_PROVIDER_GROUP_FAILURE:
      return {
        ...state,
        isFetchingListReceivingProviderGroups: false,
        items: [],
      };

    // Get
    case types.GET_PGO_RECEIVING_PROVIDER_GROUP_REQUEST:
      return {
        ...state,
        isFetchingReceivingProviderGroups: true,
      };
    case types.GET_PGO_RECEIVING_PROVIDER_GROUP_SUCCESS:
      return {
        ...state,
        isFetchingReceivingProviderGroups: false,
        data: action.payload,
        edited: false,
      };
    case types.GET_PGO_RECEIVING_PROVIDER_GROUP_FAILURE:
      return {
        ...state,
        isFetchingReceivingProviderGroups: false,
        data: { ...INITIAL_RECEIVING_PROVIDER_GROUP_DATA },
      };

    // Post
    case types.POST_PGO_RECEIVING_PROVIDER_GROUP_REQUEST:
      return {
        ...state,
        isPostingReceivingProviderGroups: true,
      };
    case types.POST_PGO_RECEIVING_PROVIDER_GROUP_SUCCESS:
      return {
        ...state,
        isPostingReceivingProviderGroups: false,
        edited: false,
      };
    case types.POST_PGO_RECEIVING_PROVIDER_GROUP_FAILURE:
      return {
        ...state,
        isPostingReceivingProviderGroups: false,
      };

    // Put
    case types.PUT_PGO_RECEIVING_PROVIDER_GROUP_REQUEST:
      return {
        ...state,
        isPuttingReceivingProviderGroups: true,
      };
    case types.PUT_PGO_RECEIVING_PROVIDER_GROUP_SUCCESS:
      return {
        ...state,
        isPuttingReceivingProviderGroups: false,
        edited: false,
      };
    case types.PUT_PGO_RECEIVING_PROVIDER_GROUP_FAILURE:
      return {
        ...state,
        isPuttingReceivingProviderGroups: false,
      };

    default:
      return state;
  }
};

export default pgoReceivingProviderGroupsReducer;
