import types from './types';
import constants from 'constants/index';

const { DEFAULT_PAGINATION_PER_PAGE } = constants;

const INITIAL_STATE = {
  searchResult: [],
  isFetchingSearchResult: false,

};

const membersReducer = (state = INITIAL_STATE, action: any) => {
  switch (action.type) {
    // Login
    case types.GET_MEMBERS_REQUEST:
        return {
            ...state,
            searchResult: [],
            isFetchingSearchResult: true,
        }
    case types.GET_MEMBERS_SUCCESS:
        return {
            ...state,
            searchResult: action.payload,
            isFetchingSearchResult: false,
        }
    case types.GET_MEMBERS_FAILURE:
        return {
            ...state,
            searchResult: [],
            isFetchingSearchResult: false,
        }

    // Initial state
    default:
      return state;
  }
};

export default membersReducer;
