import React, { useState } from 'react';

import './index.scss';
import { Form } from 'antd';

interface FloatLabelProps {
  hasFormItem?: boolean;
  hasIcon?: boolean;
  children: any;
  label: string;
  value: any;
  validateStatus?: any;
  help?: any;
}

const FloatLabel = ({
  hasIcon,
  children,
  label,
  value,
  hasFormItem = true,
  validateStatus = '',
  help = '',
}: FloatLabelProps) => {
  const [focus, setFocus] = useState(false);

  const labelClass =
    focus || (`${value}`.length !== 0) ? `label label-float` : `label${hasIcon ? ' label--icon' : ''}`;

  if (hasFormItem) {
    return (
      <div
        className="float-label"
        onBlur={() => setFocus(false)}
        onFocus={() => setFocus(true)}
      >
        <Form.Item validateStatus={validateStatus} help={help}>
          {children}
        </Form.Item>
        <label className={labelClass}>{label}</label>
      </div>
    );
  }

  return (
    <div
      className="float-label"
      onBlur={() => setFocus(false)}
      onFocus={() => setFocus(true)}
    >
      {children}
      <label className={labelClass}>{label}</label>
    </div>
  );
};

export default FloatLabel;
