import React, { Fragment } from 'react';
import { bindActionCreators, Dispatch } from 'redux';
import { connect, RootStateOrAny } from 'react-redux';
import moment from 'moment';

import { DatePicker, Input, Button, Typography } from 'antd';

import FloatLabel from 'components/Common/FloatLabel';
import { editPgoEligibility } from 'store/pgoDetails/sections/eligibility/actions';
import notifier from 'utils/notifier';

const { Text } = Typography;

interface PgoDetailsProps {
  data: any;
  editPgoEligibility: Function;
  handleGoToNextStep: Function;
}

const EligibilityPage = ({ data, editPgoEligibility, handleGoToNextStep }: PgoDetailsProps) => {

  const handleChangeEligibility = (key: string, val: any) => {
    editPgoEligibility({ [key]: val });
  };

  const onNext = () => {
    if (data.body !== '' && data.subject !== '' && data.user !== '') {
      handleGoToNextStep('5');
    } else {
      notifier.error('Please fill all input fields to continue !');
    }
  };

  return (
    <Fragment>
      <Text>Checking of Membership for Termed Providers</Text>
      <div className="row">
        <div className="col-md-8">
          <FloatLabel label="Subject" value={data.subject}>
            <Input value={data.subject} onChange={e => handleChangeEligibility('subject', e.target.value)} />
          </FloatLabel>
        </div>
        <div className="col-md-8">
          <FloatLabel label="User" value={data.user}>
            <Input value={data.user} onChange={e => handleChangeEligibility('user', e.target.value)} />
          </FloatLabel>
        </div>

        <div className="col-md-8">
          <FloatLabel label="Date/Time" value={data.createdDateUtc}>
            <DatePicker
              format="MM/DD/YYYY" suffixIcon={<i className="far fa-calendar-alt" />} placeholder=""
              value={data.createdDateUtc && moment(data.createdDateUtc)}
              onChange={(date, dateString) => handleChangeEligibility('createdDateUtc', dateString)}
            />
          </FloatLabel>
        </div>

        <div className="col-md-24">
          <FloatLabel label="Notes" value={data.body}>
            <Input.TextArea value={data.body} rows={3} onChange={e => handleChangeEligibility('body', e.target.value)} />
          </FloatLabel>
        </div>
      </div>

      <div className="row">
        <div className="col d-flex">
          <Button className="ml-auto waves-effect waves-light" onClick={() => handleGoToNextStep('3')}>Previous</Button>
          <Button type="primary" className="ml-3 waves-effect waves-light" onClick={onNext}>
            Next
          </Button>
        </div>
      </div>
    </Fragment>
  );
};

const mapStateToProps = (store: RootStateOrAny) => ({
  data: store.rootReducer.pgoEligibility.data,
});

const mapDispatchToProps = (dispatch: Dispatch) => bindActionCreators(
  {
    editPgoEligibility,
  },
  dispatch,
);

export default connect(mapStateToProps, mapDispatchToProps)(EligibilityPage);
