import React, { Fragment } from 'react';
import { bindActionCreators, Dispatch } from 'redux';
import { connect, RootStateOrAny } from 'react-redux';
import moment from 'moment';

import { DatePicker, Input, Button } from 'antd';

import FloatLabel from 'components/Common/FloatLabel';
import { editPgoDataManagements } from 'store/pgoDetails/sections/dataManagements/actions';
import notifier from 'utils/notifier';

interface PgoDetailsProps {
  data: any;
  editPgoDataManagements: Function;
  handleGoToNextStep: Function;
}

const DataManagementsPage = ({ data, editPgoDataManagements, handleGoToNextStep }: PgoDetailsProps) => {

  const handleChangeDataManagements = (key: string, val: any) => {
    editPgoDataManagements({ [key]: val });
  };

  const onNext = () => {
    if (data.body !== '' && data.subject !== '' && data.user !== '') {
      handleGoToNextStep('7');
    } else {
      notifier.error('Please fill all input fields to continue !');
    }
  };

  return (
    <Fragment>
      <div className="row">
        <div className="col-md-8">
          <FloatLabel label="Subject" value={data.subject}>
            <Input value={data.subject}
                   onChange={e => handleChangeDataManagements('subject', e.target.value)} />
          </FloatLabel>
        </div>
        <div className="col-md-8">
          <FloatLabel label="User" value={data.user}>
            <Input value={data.user}
                   onChange={e => handleChangeDataManagements('user', e.target.value)} />
          </FloatLabel>
        </div>

        <div className="col-md-8">
          <FloatLabel label="Date/Time" value={data.createdDateUtc}>
            <DatePicker
              format="MM/DD/YYYY" suffixIcon={<i className="far fa-calendar-alt" />} placeholder=""
              value={data.createdDateUtc && moment(data.createdDateUtc)}
              onChange={(date, dateString) => handleChangeDataManagements('createdDateUtc', dateString)}
            />
          </FloatLabel>
        </div>

        <div className="col-md-24">
          <FloatLabel label="Notes" value={data.body}>
            <Input.TextArea value={data.body} rows={3}
                            onChange={e => handleChangeDataManagements('body', e.target.value)} />
          </FloatLabel>
        </div>
      </div>

      <div className="row">
        <div className="col d-flex">
          <Button className="ml-auto waves-effect waves-light" onClick={() => handleGoToNextStep('5')}>Previous</Button>
          <Button type="primary" className="ml-3 waves-effect waves-light" onClick={onNext}>Next</Button>
        </div>
      </div>
    </Fragment>
  );
};

const mapStateToProps = (store: RootStateOrAny) => ({
  data: store.rootReducer.pgoDataManagements.data,
});

const mapDispatchToProps = (dispatch: Dispatch) => bindActionCreators(
  {
    editPgoDataManagements,
  },
  dispatch,
);

export default connect(mapStateToProps, mapDispatchToProps)(DataManagementsPage);
