import React, { useState } from 'react';
import { bindActionCreators, Dispatch } from 'redux';
import { connect, RootStateOrAny } from 'react-redux';
import { useHistory } from "react-router-dom";

import { Button, Typography } from 'antd';

import './index.scss';

const { Title } = Typography;

interface AuthNewPageProps {
  edited: boolean;
}

const AuthNewPage = (
  {

  }: AuthNewPageProps) => {
  const history = useHistory();
  const [drafts, setDrafts] = useState(JSON.parse(localStorage.getItem('authDrafts') || '[]'));

  const onSubmit = (draft: any) => {
    // postAuth(draft);

    onRemove(draft);
  };

  const onRemove = (draft: any) => {
    let authDrafts = JSON.parse(localStorage.getItem('authDrafts') || '[]');
    authDrafts = authDrafts.filter((item: any) => (item.id !== draft.id));
    localStorage.setItem('authDrafts', JSON.stringify(authDrafts));
    setDrafts(authDrafts);
  };

  return (
    <div className="page auth-new-page">
      <div className="header">
        <Title level={4}>Auth Create Page</Title>

        <Button type="primary" className="ml-3 waves-effect waves-light" onClick={() => history.push('/authorization/create')}>
          NEW
        </Button>
      </div>

      <div className="content">
        <table className="w-100">
          <thead className="tbl-header">
            <tr>
              <th className="span-1">No</th>
              <th className="span-2">Content</th>
              <th className="span-3">Action</th>
            </tr>
          </thead>
          <tbody className="tbl-body">
          {drafts.map((draft: any, index: number) => (
            <tr key={index}>
              <td className="span-1">{index + 1}</td>
              <td className="span-2">{draft.id} ...content</td>
              <td className="span-3">
                <i className="fa fa-edit mr-3 icon" onClick={() => history.push(`/authorization/drafts/${draft.id}`)}/>
                <Button className="submit-btn" onClick={() => onSubmit(draft)}>Submit</Button>
                <i className="fa fa-trash-alt ml-3 icon" onClick={() => onRemove(draft)}/>
              </td>
            </tr>
          ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

const mapStateToProps = (store: RootStateOrAny) => ({
});

const mapDispatchToProps = (dispatch: Dispatch) => bindActionCreators(
  {

  },
  dispatch,
);

export default connect(mapStateToProps, mapDispatchToProps)(AuthNewPage);
