import types from './types';

const INITIAL_CARE_MANAGEMENT_DATA = {
  id: '',
  deleted: '',
  createdDateUtc: '',
  modifiedDateUtc: '',
  pgoMasterId: '',
  subject: '',
  user: '',
  body: ''
};

const INITIAL_STATE = {
  isFetchingListCareManagements: false,
  isFetchingCareManagements: false,
  isPostingCareManagements: false,
  isPuttingCareManagements: false,

  data: {...INITIAL_CARE_MANAGEMENT_DATA},
  items: [],

  edited: false,
};

const pgoCareManagementsReducer = (state = INITIAL_STATE, action: any) => {
  switch (action.type) {
    // Init
    case types.PGO_CARE_MANAGEMENT_INIT:
      return {
        ...state,
        data: { ...INITIAL_CARE_MANAGEMENT_DATA },
        edited: false,
      };

    // Edit
    case types.PGO_CARE_MANAGEMENT_EDIT:
      return {
        ...state,
        data: { ...state.data, ...action.payload },
        edited: true,
      };

    // Get list
    case types.GET_LIST_PGO_CARE_MANAGEMENT_REQUEST:
      return {
        ...state,
        isFetchingListCareManagements: true,
      };
    case types.GET_LIST_PGO_CARE_MANAGEMENT_SUCCESS:
      return {
        ...state,
        isFetchingListCareManagements: false,
        items: action.payload,
      };
    case types.GET_LIST_PGO_CARE_MANAGEMENT_FAILURE:
      return {
        ...state,
        isFetchingListCareManagements: false,
        items: [],
      };

    // Get
    case types.GET_PGO_CARE_MANAGEMENT_REQUEST:
      return {
        ...state,
        isFetchingCareManagements: true,
      };
    case types.GET_PGO_CARE_MANAGEMENT_SUCCESS:
      return {
        ...state,
        isFetchingCareManagements: false,
        data: action.payload,
        edited: false,
      };
    case types.GET_PGO_CARE_MANAGEMENT_FAILURE:
      return {
        ...state,
        isFetchingCareManagements: false,
        data: { ...INITIAL_CARE_MANAGEMENT_DATA },
      };

    // Post
    case types.POST_PGO_CARE_MANAGEMENT_REQUEST:
      return {
        ...state,
        isPostingCareManagements: true,
      };
    case types.POST_PGO_CARE_MANAGEMENT_SUCCESS:
      return {
        ...state,
        isPostingCareManagements: false,
        edited: false,
      };
    case types.POST_PGO_CARE_MANAGEMENT_FAILURE:
      return {
        ...state,
        isPostingCareManagements: false,
      };

    // Put
    case types.PUT_PGO_CARE_MANAGEMENT_REQUEST:
      return {
        ...state,
        isPuttingCareManagements: true,
      };
    case types.PUT_PGO_CARE_MANAGEMENT_SUCCESS:
      return {
        ...state,
        isPuttingCareManagements: false,
        edited: false,
      };
    case types.PUT_PGO_CARE_MANAGEMENT_FAILURE:
      return {
        ...state,
        isPuttingCareManagements: false,
      };

    default:
      return state;
  }
};

export default pgoCareManagementsReducer;
