import types from './types';
import { ThunkDispatch } from 'redux-thunk';
import api from 'constants/api';
import _get from 'lodash/get';

export const initPgoEligibility = () => {
  return {
    type: types.PGO_ELIGIBILITY_INIT,
  };
};

export const editPgoEligibility = (data: any) => {
  return {
    type: types.PGO_ELIGIBILITY_EDIT,
    payload: data,
  };
};

export const getListPgoEligibility = () => (dispatch: ThunkDispatch<any, any, any>, getState: any) => {
  if (getState().rootReducer.pgoEligibility.isFetchingListEligibility) { return Promise.reject(); }

  dispatch({ type: types.GET_LIST_PGO_ELIGIBILITY_REQUEST });

  return api.pgoDetails.eligibility.getList()
    .then((res: any) => {

      dispatch({
        type: types.GET_LIST_PGO_ELIGIBILITY_SUCCESS,
        payload: res,
      });

      return res;
    })
    .catch((err: Error) => {
      const msg = _get(err, 'response.data.msg', 'Get List failed!');
      const errors = _get(err, 'response.data.errors', {});
      const payload = { ...err, msg, errors };

      dispatch({
        type: types.GET_LIST_PGO_ELIGIBILITY_FAILURE,
        payload,
      });

      return Promise.reject(payload);
    });
};

