import types from './types';

const INITIAL_ATTACHMENT_DATA = {
  id: '',
  deleted: '',
  createdDateUtc: '',
  modifiedDateUtc: '',
  pgoMasterId: '',
  region: '',
  bucket: '',
  fileKey: ''
};

const INITIAL_STATE = {
  isFetchingListAttachments: false,
  isFetchingAttachments: false,
  isPostingAttachments: false,
  isPuttingAttachments: false,

  data: {...INITIAL_ATTACHMENT_DATA},
  items: [],

  edited: false,
};

const pgoAttachmentReducer = (state = INITIAL_STATE, action: any) => {
  switch (action.type) {
    // Init
    case types.PGO_ATTACHMENT_INIT:
      return {
        ...state,
        data: { ...INITIAL_ATTACHMENT_DATA },
        edited: false,
      };

    // Edit
    case types.PGO_ATTACHMENT_EDIT:
      return {
        ...state,
        data: { ...state.data, ...action.payload },
        edited: true,
      };

    // Get list
    case types.GET_LIST_PGO_ATTACHMENT_REQUEST:
      return {
        ...state,
        isFetchingListAttachments: true,
      };
    case types.GET_LIST_PGO_ATTACHMENT_SUCCESS:
      return {
        ...state,
        isFetchingListAttachments: false,
        items: action.payload,
      };
    case types.GET_LIST_PGO_ATTACHMENT_FAILURE:
      return {
        ...state,
        isFetchingListAttachments: false,
        items: [],
      };

    // Get
    case types.GET_PGO_ATTACHMENT_REQUEST:
      return {
        ...state,
        isFetchingAttachments: true,
      };
    case types.GET_PGO_ATTACHMENT_SUCCESS:
      return {
        ...state,
        isFetchingAttachments: false,
        data: action.payload,
        edited: false,
      };
    case types.GET_PGO_ATTACHMENT_FAILURE:
      return {
        ...state,
        isFetchingAttachments: false,
        data: { ...INITIAL_ATTACHMENT_DATA },
      };

    // Post
    case types.POST_PGO_ATTACHMENT_REQUEST:
      return {
        ...state,
        isPostingAttachments: true,
      };
    case types.POST_PGO_ATTACHMENT_SUCCESS:
      return {
        ...state,
        isPostingAttachments: false,
        edited: false,
      };
    case types.POST_PGO_ATTACHMENT_FAILURE:
      return {
        ...state,
        isPostingAttachments: false,
      };

    // Put
    case types.PUT_PGO_ATTACHMENT_REQUEST:
      return {
        ...state,
        isPuttingAttachments: true,
      };
    case types.PUT_PGO_ATTACHMENT_SUCCESS:
      return {
        ...state,
        isPuttingAttachments: false,
        edited: false,
      };
    case types.PUT_PGO_ATTACHMENT_FAILURE:
      return {
        ...state,
        isPuttingAttachments: false,
      };

    default:
      return state;
  }
};

export default pgoAttachmentReducer;
