import types from './types';

const INITIAL_DATA = {
  'USER_ID': null,
  'EZCAP_USER_ID': null,
  'USERNAME': '',
  'EMAIL': '',
  'ROLES': [],
  'DEPARTMENTS': [],
  'BUCKETS': [],
  'FIRST_NAME': '',
  'LAST_NAME': '',
  'IS_ACTIVE': false,
  'DEPT_ID': null,
  'LAST_ONLINE': '',
  'DEFAULT_BUCKET': 0,
  'COLOR': '',
  'EMAIL_OPT_OUT': false,
};

const INITIAL_STATE = {
  isPostingLogin: false,
  isCheckingMe: false,
  isFetchingMe: false,

  isStoredTokenVerified: false,

  user: { ...INITIAL_DATA },
  is_admin: false,
  is_manager: false,

  access_token: '',
  refresh_token: '',

  loggedIn: false,
};

const authReducer = (state = INITIAL_STATE, action: any) => {
  switch (action.type) {
    // Login
    case types.LOGIN_REQUEST:
      return {
        ...state,
        isPostingLogin: true,
      };
    case types.LOGIN_SUCCESS:
      return {
        ...state,
        isPostingLogin: false,
        access_token: action.payload.access_token,
        refresh_token: action.payload.refresh_token,
        loggedIn: true,
      };
    case types.LOGIN_FAILURE:
      return {
        ...state,
        isPostingLogin: false,
        access_token: '',
        refresh_token: '',
        loggedIn: false,
      };

    // Update token in store
    case types.UPDATE_TOKEN_IN_STORE:
      return {
        ...state,
        access_token: action.payload.access_token,
        refresh_token: action.payload.refresh_token,
      };

    // Check me
    case types.CHECK_ME_REQUEST:
      return {
        ...state,
        isCheckingMe: true,
      };
    case types.CHECK_ME_SUCCESS:
      return {
        ...state,
        isCheckingMe: false,
        user: action.payload.user,
        is_admin: action.payload.is_admin,
        is_manager: action.payload.is_manager,
        loggedIn: true,
        isStoredTokenVerified: true,
      };
    case types.CHECK_ME_FAILURE:
      return {
        ...state,
        isCheckingMe: false,
        user: { ...INITIAL_DATA },
        loggedIn: false,
        isStoredTokenVerified: true,
      };

    // Get me
    case types.GET_ME_REQUEST:
      return {
        ...state,
        isFetchingMe: true,
      };
    case types.GET_ME_SUCCESS:
      return {
        ...state,
        isFetchingMe: false,
        user: action.payload.user,
        is_admin: action.payload.is_admin,
        is_manager: action.payload.is_manager,
        loggedIn: true,
      };
    case types.GET_ME_FAILURE:
      return {
        ...state,
        isFetchingMe: false,
        user: { ...INITIAL_DATA },
        loggedIn: false,
      };

    // Logout
    case types.LOGOUT_SUCCESS:
      return {
        ...state,
        user: { ...INITIAL_DATA },
        access_token: '',
        refresh_token: '',
        loggedIn: false,
      };

    // Initial state
    default:
      return state;
  }
};

export default authReducer;
