import types from './types';
import { ThunkDispatch } from 'redux-thunk';
import api from 'constants/api';
import _get from 'lodash/get';

export const initPgoGridOperations = () => {
  return {
    type: types.PGO_GRID_OPERATION_INIT,
  };
};

export const editPgoGridOperations = (data: any) => {
  return {
    type: types.PGO_GRID_OPERATION_EDIT,
    payload: data,
  };
};

export const getListPgoGridOperations = () => (dispatch: ThunkDispatch<any, any, any>, getState: any) => {
  if (getState().rootReducer.pgoGridOperations.isFetchingListGridOperations) { return Promise.reject(); }

  dispatch({ type: types.GET_LIST_PGO_GRID_OPERATION_REQUEST });

  return api.pgoDetails.gridOperations.getList()
    .then((res: any) => {

      dispatch({
        type: types.GET_LIST_PGO_GRID_OPERATION_SUCCESS,
        payload: res,
      });

      return res;
    })
    .catch((err: Error) => {
      const msg = _get(err, 'response.data.msg', 'Get List failed!');
      const errors = _get(err, 'response.data.errors', {});
      const payload = { ...err, msg, errors };

      dispatch({
        type: types.GET_LIST_PGO_GRID_OPERATION_FAILURE,
        payload,
      });

      return Promise.reject(payload);
    });
};

