import React, { PureComponent } from 'react';
import { bindActionCreators, Dispatch } from 'redux';
import { connect, RootStateOrAny } from 'react-redux';

import {
  Layout,
  Menu,
} from 'antd';

import {
  DashboardOutlined,
  BlockOutlined,
  EditOutlined,
  SearchOutlined,
  FolderOutlined,
  UsergroupAddOutlined,
  ToolOutlined,
  SettingOutlined,
  CloudOutlined,
  LogoutOutlined,
  MenuUnfoldOutlined,
  MenuFoldOutlined,
} from '@ant-design/icons';

import './GeneralLayout.scss';

const { Header, Content, Footer, Sider } = Layout;
const { SubMenu } = Menu;

interface GeneralLayoutProps {
  children: any,
  isAuthenticated: boolean,
  user: any,
  location?: any,
  history?: any,
  user_role?: string,
}

class GeneralLayout extends PureComponent<GeneralLayoutProps> {
  state = {
    collapsed: false,
  };

  onCollapse = (collapsed: boolean) => {
    this.setState({ collapsed });
  };


  toggleCollapse = () => {
    this.setState({
      collapsed: !this.state.collapsed,
    });
  };

  gotoUrl = (url: string) => {
    this.props.history.push(url);
  };

  render() {
    const { collapsed } = this.state;
    const { children, location } = this.props;

    const openKeys = [];
    const separatedKeys = location.pathname.split('/');
    if (separatedKeys.length > 2) {
      openKeys.push(separatedKeys[1]);
    }

    return (
      <Layout className="general-layout">
        <Sider trigger={null} collapsible collapsed={collapsed}>
          <div className="logo">
            MSQ
          </div>

          <Menu theme="dark" mode="inline" defaultSelectedKeys={[location.pathname]} defaultOpenKeys={openKeys}>
            <Menu.Item key="/dashboard" icon={<DashboardOutlined />} onClick={() => this.gotoUrl('/dashboard')}>
              Dashboard
            </Menu.Item>

            <SubMenu key="member-tickets" icon={<UsergroupAddOutlined />} title="Member Tickets">
              <Menu.Item key="/member-tickets/create" icon={<EditOutlined />} onClick={() => this.gotoUrl('/member-tickets/create')}>
                Create
              </Menu.Item>
              <Menu.Item key="/member-tickets/search" icon={<SearchOutlined />} onClick={() => this.gotoUrl('/member-tickets/search')}>
                Search
              </Menu.Item>
            </SubMenu>

            <SubMenu key="ccstickets" icon={<UsergroupAddOutlined />} title="CCS Tickets">
              <Menu.Item key="/ccstickets/create" icon={<EditOutlined />} onClick={() => this.gotoUrl('/ccstickets/create')}>
                Create
              </Menu.Item>
              <Menu.Item key="/ccstickets/search" icon={<SearchOutlined />} onClick={() => this.gotoUrl('/ccstickets/search')}>
                Search
              </Menu.Item>
            </SubMenu>

            <SubMenu key="pgo-details" icon={<UsergroupAddOutlined />} title="PGO Details">
              <Menu.Item key="/pgo-details/new" icon={<EditOutlined />} onClick={() => this.gotoUrl('/pgo-details/new')}>
                Create
              </Menu.Item>
              <Menu.Item key="/pgo-details/search" icon={<SearchOutlined />} onClick={() => this.gotoUrl('/pgo-details/search')}>
                Search
              </Menu.Item>
            </SubMenu>

            <Menu.Item key="/pgo-details" icon={<DashboardOutlined />} onClick={() => this.gotoUrl('/pgo-details')}>
              PGO Details
            </Menu.Item>

            {/*<Menu.Item key="/umhs" icon={<DashboardOutlined />} onClick={() => this.gotoUrl('/umhs')}>*/}
            {/*  UMHS*/}
            {/*</Menu.Item>*/}

            <SubMenu key="authorization" icon={<BlockOutlined />} title="Authorization">
              <Menu.Item key="/authorization/new" icon={<EditOutlined />}
                         onClick={() => this.gotoUrl('/authorization/new')}>
                Create
              </Menu.Item>
              <Menu.Item key="/authorization/search" icon={<SearchOutlined />}
                         onClick={() => this.gotoUrl('/authorization/search')}>
                Search
              </Menu.Item>
            </SubMenu>

            {/*<SubMenu key="case" icon={<FolderOutlined />} title="Case">*/}
            {/*  <Menu.Item key="/case/create" icon={<EditOutlined />} onClick={() => this.gotoUrl('/case/create')}>*/}
            {/*    Create*/}
            {/*  </Menu.Item>*/}
            {/*  <Menu.Item key="/case/search" icon={<SearchOutlined />} onClick={() => this.gotoUrl('/case/search')}>*/}
            {/*    Search*/}
            {/*  </Menu.Item>*/}
            {/*</SubMenu>*/}

            <Menu.Item key="/member" icon={<UsergroupAddOutlined />} onClick={() => this.gotoUrl('/member')}>
              Member
            </Menu.Item>

            {/*<Menu.Item key="/attachmentviewertest" icon={<UsergroupAddOutlined />} onClick={() => this.gotoUrl('/attachmentviewertest')}>
              Attachment Viewer
            </Menu.Item>*/}

            <Menu.Item key="/provider" icon={<CloudOutlined />} onClick={() => this.gotoUrl('/provider')}>
              Provider
            </Menu.Item>

            <Menu.Item key="/support" icon={<ToolOutlined />} onClick={() => this.gotoUrl('/support')}>
              Support
            </Menu.Item>

            <Menu.Item key="/profile" icon={<SettingOutlined />} onClick={() => this.gotoUrl('/profile')}>
              My Profile
            </Menu.Item>

            <Menu.Item key="/logout" icon={<LogoutOutlined />} onClick={() => this.gotoUrl('/logout')}>
              Logout
            </Menu.Item>
          </Menu>
        </Sider>

        <Layout className="site-layout">
          <Header className="site-layout-background" style={{ padding: 0 }}>
            {React.createElement(this.state.collapsed ? MenuUnfoldOutlined : MenuFoldOutlined, {
              className: 'trigger',
              onClick: this.toggleCollapse,
            })}
          </Header>

          <Content className="site-layout-content">
            {/*<Breadcrumb style={{ margin: '16px 0' }}>*/}
            {/*  <Breadcrumb.Item>MSQ</Breadcrumb.Item>*/}
            {/*</Breadcrumb>*/}

            <div className="site-layout-background" style={{ padding: 24, minHeight: 360 }}>
              {children}
            </div>
          </Content>

          <Footer className="site-layout-footer">Multi Services Queue, NMM</Footer>

        </Layout>
      </Layout>
    );

  }
}

const mapStateToProps = (store: RootStateOrAny) => ({});

const mapDispatchToProps = (dispatch: Dispatch) => bindActionCreators(
  {
  },
  dispatch,
);

export default connect(mapStateToProps, mapDispatchToProps)(GeneralLayout);
