import * as React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import { Provider } from 'react-redux';
import $ from 'jquery';

// Import sentry error tracking
import * as Sentry from "@sentry/react";
import { Integrations } from "@sentry/tracing";

// Import store
import store from 'store/index';
import { loginWithStoredToken } from 'store/auth/actions';


// Import components
import App from 'containers/App';
import ScrollToTop from 'components/Common/ScrollToTop';
import notifier from 'utils/notifier';

// Import root styles
import 'assets/styles/index.scss';

// Make jQuery and notifier global
declare global {
  interface Window {
    jQuery: any,
    $: any,
    jquery: any,
    notifier: any,
    store: any,
  }
}

window.jQuery = window.jquery = window.$ = $;
window.notifier = notifier;

// Verify Stored access_token at app startup
store.dispatch<any>(loginWithStoredToken());

// Debug store values
if (process.env.NODE_ENV === 'development') {
  window.store = store;
} else {
  // Initialize sentry error tracking
  Sentry.init({
    dsn: "https://74009db5f863474f9da4cefab794e794@o550693.ingest.sentry.io/5674207",
    integrations: [new Integrations.BrowserTracing()],

    // Set tracesSampleRate to 1.0 to capture 100%
    // of transactions for performance monitoring.
    // We recommend adjusting this value in production
    tracesSampleRate: 0.8,
  });
}

// import reportWebVitals from './reportWebVitals';


ReactDOM.render(
  <Provider store={store}>
    <Router>
      <ScrollToTop>

        <Route component={App} />

      </ScrollToTop>
    </Router>
  </Provider>,
  document.getElementById('root'),
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
// reportWebVitals();
