import React, { useEffect } from 'react';
import { bindActionCreators, Dispatch } from 'redux';
import { connect, RootStateOrAny } from 'react-redux';
import { Typography } from 'antd';

import { getMySelf } from 'store/auth/actions';
import { getListDepartments } from 'store/departments/actions';

import { USER_ROLES } from 'constants/mockData/roleList';

import LoadingIndicator from 'components/Common/LoadingIndicator';

import './index.scss';

interface Department {
  ID: number;
  NAME: String;
}

interface ProfileProps {
  departments: any;
  user: any;
  getMySelf: Function;
  getListDepartments: Function;
  isFetchingMe: boolean;
  isFetchingList: boolean;
}

const { Title } = Typography;

const Profile = ({ user, departments, getMySelf, getListDepartments, isFetchingMe, isFetchingList }: ProfileProps) => {
  useEffect(() => {
    getMySelf();
    getListDepartments();
  }, [getMySelf, getListDepartments]);

  return (
    <div className="page profile-page">
      <LoadingIndicator isLoading={isFetchingMe || isFetchingList} fullScreen />

      <Title level={4}>USER PROFILE</Title>

      <div className="content">
        <div className="profile-item">
          <span className="item-label">Name: </span>
          <div className="item-text">{user?.FIRST_NAME + ' ' + user?.LAST_NAME}</div>
        </div>

        <div className="profile-item">
          <span className="item-label">Username: </span>
          <div className="item-text">{user?.USERNAME}</div>
        </div>

        <div className="profile-item">
          <span className="item-label">Departments: </span>
          <div className="item-content">
            {departments?.filter((dept: Department) => (user?.DEPARTMENTS.includes(dept.ID)))?.map((user_dept: Department) => (
              <span key={user_dept.ID} className="item-text">{user_dept.NAME}</span>
            ))}
          </div>
        </div>

        <div className="profile-item">
          <span className="item-label">Roles: </span>
          <div className="item-content">
            {USER_ROLES.filter((role) => (user?.ROLES.includes(role.ID)))?.map((user_role) => (
                <span key={user_role.ID} className="item-text">{user_role.RoleName}</span>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = (store: RootStateOrAny) => ({
  user: store.rootReducer.auth.user,
  isFetchingMe: store.rootReducer.auth.isFetchingMe,
  departments: store.rootReducer.departments.data,
  isFetchingList: store.rootReducer.departments.isFetchingList,
});

const mapDispatchToProps = (dispatch: Dispatch) => bindActionCreators(
  {
    getMySelf,
    getListDepartments,
  },
  dispatch,
);

export default connect(mapStateToProps, mapDispatchToProps)(Profile);
