import types from './types';
import { ThunkDispatch } from 'redux-thunk';
import api from 'constants/api';
import _get from 'lodash/get';

export const initPgoCareManagements = () => {
  return {
    type: types.PGO_CARE_MANAGEMENT_INIT,
  };
};

export const editPgoCareManagements = (data: any) => {
  return {
    type: types.PGO_CARE_MANAGEMENT_EDIT,
    payload: data,
  };
};

export const getListPgoCareManagements = () => (dispatch: ThunkDispatch<any, any, any>, getState: any) => {
  if (getState().rootReducer.pgoCareManagements.isFetchingListCareManagements) { return Promise.reject(); }

  dispatch({ type: types.GET_LIST_PGO_CARE_MANAGEMENT_REQUEST });

  return api.pgoDetails.careManagements.getList()
    .then((res: any) => {

      dispatch({
        type: types.GET_LIST_PGO_CARE_MANAGEMENT_SUCCESS,
        payload: res,
      });

      return res;
    })
    .catch((err: Error) => {
      const msg = _get(err, 'response.data.msg', 'Get List failed!');
      const errors = _get(err, 'response.data.errors', {});
      const payload = { ...err, msg, errors };

      dispatch({
        type: types.GET_LIST_PGO_CARE_MANAGEMENT_FAILURE,
        payload,
      });

      return Promise.reject(payload);
    });
};

