import types from './types';
import { ThunkDispatch } from 'redux-thunk';
import api from 'constants/api';
import _get from 'lodash/get';

export const editTickets = (data: any) => {
  return {
    type: types.TICKET_EDIT,
    payload: data,
  };
};

export const initTickets = () => {
  return {
    type: types.TICKET_INIT
  };
};

export const getListTickets = () => (dispatch: ThunkDispatch<any, any, any>, getState: any) => {
  if (getState().rootReducer.memberTickets.isFetchingListTickets) { return Promise.reject(); }

  dispatch({ type: types.GET_LIST_TICKET_REQUEST });

  return api.memberTickets.getList()
    .then((res: any) => {

      dispatch({
        type: types.GET_LIST_TICKET_SUCCESS,
        payload: res,
      });

      return res;
    })
    .catch((err: Error) => {
      const msg = _get(err, 'response.data.msg', 'Get List failed!');
      const errors = _get(err, 'response.data.errors', {});
      const payload = { ...err, msg, errors };

      dispatch({
        type: types.GET_LIST_TICKET_FAILURE,
        payload,
      });

      return Promise.reject(payload);
    });
};

