import React, { useEffect, Fragment, useState } from 'react';
import { bindActionCreators, Dispatch } from 'redux';
import { connect, RootStateOrAny } from 'react-redux';
import { Link, useHistory } from 'react-router-dom';

import { Button, Checkbox, Input, Select, Card, TimePicker, Form, Typography, DatePicker, Upload , Radio} from 'antd';
import { InboxOutlined } from '@ant-design/icons';
import { SearchOutlined } from '@ant-design/icons';
import { initCCSTickets, getCCSTickets, editCCSTickets, postCCSTickets , getTicketsNo} from 'store/ccsTickets/actions';

import { getListIpas } from 'store/ipas/actions';
import { getListCustomerCategories } from 'store/customerCategories/actions';
// import { getListQueues } from 'store/queues/actions';
import { getListCallCategories } from 'store/callCategories/actions';
import { getListHPCodes } from 'store/hpCodes/actions';


import FloatLabel from 'components/Common/FloatLabel';
import ProviderSearch from 'components/Provider/ProviderSearch'
import MemberSearch from 'components/Member/MemberSearch'
import RichTextEditor from './RichTextEditor';

import './index.scss';

import moment from "moment";
import { fromPairs } from 'lodash';

const { Option } = Select;
const { Title } = Typography;
const { Dragger } = Upload;


interface CCSTicketsCreateProps {
    edited: boolean;
    data: any;
    initCCSTickets: Function;
    getCCSTickets: Function;
    editCCSTickets: Function;
    postCCSTickets: Function;
    getTicketsNo: Function;
    ticket_ref_no: any;
    getListCustomerCategories: Function;
    customerCategoryItems: any;
    getListCallCategories: Function;
    callCategoryItems: any;
    getListHPCodes: Function;
    hpCodeItems: any;
    getListIpas: Function;
    ipaItems: any
    
    
}
const CCSTicketsCreatePage = (
    {
        edited, data, initCCSTickets, getCCSTickets, editCCSTickets, postCCSTickets, 
        getTicketsNo, ticket_ref_no,
        getListCustomerCategories, customerCategoryItems,
        getListCallCategories, callCategoryItems,
        getListHPCodes, hpCodeItems,
        getListIpas, ipaItems
    }: CCSTicketsCreateProps) => {
  const [isProviderSearchOpen, setIsProviderSearchOpen] = useState(false);
  const [isMemberSearchOpen, setIsMemberSearchOpen] = useState(false);

  const history = useHistory();
  

  useEffect(() => {
    initCCSTickets();
    getTicketsNo()
        .then((res: any) => {
            editCCSTickets({TICKET_REF_NO: res});
        })
    getListCustomerCategories();
    getListCallCategories();
    getListHPCodes();
    getListIpas();
  }, [initCCSTickets, getTicketsNo, getListCustomerCategories, getListCallCategories, getListHPCodes, getListIpas]);

// useEffect(() => {
// if (data.HEALTH_PLAN === true) {
//     editCCSTickets({ 'HEALTH_PLAN': 'SOMEVALUE' });
// }
// }, [data.HEALTH_PLAN]);

  const handleChangeCCSTicketsCreate = (key: string, val: any) => {
    editCCSTickets({ [key]: val });
  };

  const handleChangeRichEditorValue = (content: any, delta: any, source: any, editor: any ) => {
    console.log('RichEditor:::::::content::::::', content, delta, source, editor);
  };

  const handleSelectProvider = (data: any) => {
    handleChangeCCSTicketsCreate('PROVIDER_FIRSTNAME', data?.firstName || '');
    handleChangeCCSTicketsCreate('PROVIDER_LASTNAME', data?.lastName || '');
    handleChangeCCSTicketsCreate('PROVIDER_ID', data?.providerRefId || '');
    // handleChangeCCSTicketsCreate('vendorEffectDate', data?.vendorEffectDate || '');
    // handleChangeCCSTicketsCreate('providerAccount', data?.subAccount || '');
    handleChangeCCSTicketsCreate('PROVIDER_TAX_ID', data?.providerTaxId || '');
    handleChangeCCSTicketsCreate('PROVIDER_NPI_1', data?.providerNpi1 || '');
    handleChangeCCSTicketsCreate('PROVIDER_NPI_2', data?.providerNpi2 || '');
    handleChangeCCSTicketsCreate('PROV_MD_LICENSE', data?.providerMdLicense || '');

    setIsProviderSearchOpen(false);
  };

  const handleSelectMember = (data: any) => {
    handleChangeCCSTicketsCreate('MBR_ID', data?.memberRefId || '');
    handleChangeCCSTicketsCreate('MBR_FIRSTNAME', data?.firstName || '');
    handleChangeCCSTicketsCreate('MBR_LASTNAME', data?.lastName || '');
    handleChangeCCSTicketsCreate('memberStatus', true);
    handleChangeCCSTicketsCreate('MBR_PHONE', data?.phone || '');
    handleChangeCCSTicketsCreate('MBR_EXT', data?.altPhone || '');
    handleChangeCCSTicketsCreate('MBR_DOB', moment(data?.birth,'YYYY-MM-DD').format('MM/DD/YYYY') || '');
    handleChangeCCSTicketsCreate('MBR_HPCODE', data?.hpcode || '');
    handleChangeCCSTicketsCreate('opt', data?.opt || '');

    setIsMemberSearchOpen(false);
  };



  const onSubmit = () => {

    let item: any = {
        ticketid: data.TICKET_REF_NO,
        calltime: data.CALL_TIME,
        itemid: null,
        itemtype: data.ITEM_TYPE,
        itemtitle: data.ITEM_TITLE,
        userid: data.USER?.USER_ID,
        status: data.STATUS,
        callerfirstname: data.CALLER_FIRSTNAME,
        callerlastname: data.CALLER_LASTNAME,
        callbacknbr: data.CALLBACK_NBR,
        callbackext: data.CALLBACK_EXT,
        callerfax: data.CALLER_FAX,
        calleremail: data.CALLER_EMAIL,
        ipa: data.IPA,
        healthplan: data.HEALTH_PLAN,
        hpcode: data.HPCODE,
        customercategory: data.CUSTOMER_CATEGORY,
        mbrfirstname: data.MBR_FIRSTNAME,
        mbrlastname: data.MBR_LASTNAME,
        mbrid: data.MBR_ID,
        mbrdob: data.MBR_DOB,
        providerfirstname: data.PROVIDER_FIRSTNAME,
        providerlastname: data.PROVIDER_LASTNAME,
        providertaxid: data.PROVIDER_TAX_ID,
        providernpi1: data.PROVIDER_NPI_1,
        providernpi2: data.PROVIDER_NPI_2,
        providermdlicense: data.PROV_MD_LICENSE,
        callcategory: data.CALL_CATEGORY,
        claimno: data.CLAIMNO,
        chechno: data.CHECKNO,
        categorydos: data.CATEGORY_DOS,
        proccode1: data.PROC_CODE_1,
        proccode2: data.PROC_CODE_2,
        proccode3: data.PROC_CODE_3,
        proccode4: data.PROC_CODE_4,
        proccode5: data.PROC_CODE_5,
        routeto: data.ROUTE_TO,
        dateexpcomplete: data.DATE_EXPECTED_COMPLETE,
        callbackdate: data.CALLBACK_DATE,
        closedby: data.CLOSED_BY,
        notes: data.NOTES,
        callercode: data.CALLER_CODE,
        itemcontent: data.ITEM_CONTENT,
        priority: data.PRIORITY,
        urgent: data.URGENT
    };
    
    const itemJson = JSON.stringify(item);
    
    const params = new URLSearchParams();
    params.append('item', itemJson);
    params.append('parent', data.parent);

    postCCSTickets(params)
      .then((res: any) => {
        history.push(`/ccstickets/${res.ITEM_ID}`);
    });
};

const hasHpCodeOptions = [
    {label: 'Yes', value: true },
    {label: 'No', value: false }
];

//   const customerCategoryOptions = [
//     {label: 'Member', value: 0 },
//     {label: 'Provider', value: 1},
//     {label: '3rd Party', value: 2}
//   ];

  const routeToOptions = [
      {label: 'Claims Audit - Routine', value: 0 },
      {label: 'Claims Production - Routine ', value: 1 }
  ];

  return (
    <div className="page ticket-create-page">
        <ProviderSearch
        isModal
        isModalVisible={isProviderSearchOpen}
        onCloseModal={() => setIsProviderSearchOpen(false)}
        onProviderSelect={handleSelectProvider}
      />
      <MemberSearch
        isModal
        isModalVisible={isMemberSearchOpen}
        onCloseModal={() => setIsMemberSearchOpen(false)}
        onMemberSelect={handleSelectMember}
      />

        <Title level={4}>CCS Item</Title>
        <div className="row ccs-ticket-header">
            <div className="col-md-12">
                <FloatLabel label="Ticket Ref #" value={data.TICKET_REF_NO}>
                    <Input
                        value={data.TICKET_REF_NO}
                        onChange={e => handleChangeCCSTicketsCreate('TICKET_REF_NO', e.target.value)}>
                    </Input>
                </FloatLabel>
            </div>
        </div>

        <div className="row ticket-body">
            <Card size="small" title="Caller Information" className="ticket-card w-100">
                <div className="row mt-3">
                    <div className="col-md-12">
                        <FloatLabel label="First" value={data.CALLER_FIRSTNAME}>
                            <Input value={data.CALLER_FIRSTNAME} onChange={e => handleChangeCCSTicketsCreate('CALLER_FIRSTNAME', e.target.value)} />
                        </FloatLabel>
                    </div>

                    <div className="col-md-12">
                        <FloatLabel label="Last" value={data.CALLER_LASTNAME}>
                            <Input value={data.CALLER_LASTNAME} onChange={e => handleChangeCCSTicketsCreate('CALLER_LASTNAME', e.target.value)} />
                        </FloatLabel>
                    </div>

                    <div className="col-md-12">
                        <FloatLabel label="Callback Phone" value={data.CALLBACK_NBR}>
                            <Input value={data.CALLBACK_NBR} onChange={e => handleChangeCCSTicketsCreate('CALLBACK_NBR', e.target.value)} />
                        </FloatLabel>
                    </div>

                    <div className="col-md-12">
                        <FloatLabel label="Callback Extension" value={data.CALLBACK_EXT}>
                            <Input value={data.CALLBACK_EXT} onChange={e => handleChangeCCSTicketsCreate('CALLBACK_EXT', e.target.value)} />
                        </FloatLabel>
                    </div>

                    <div className="col-md-12">
                        <FloatLabel label="Caller's Fax" value={data.CALLER_FAX}>
                            <Input value={data.CALLER_FAX} onChange={e => handleChangeCCSTicketsCreate('CALLER_FAX', e.target.value)} />
                        </FloatLabel>
                    </div>

                    <div className="col-md-12">
                        <FloatLabel label="Caller's Email" value={data.CALLER_EMAIL}>
                            <Input value={data.CALLER_EMAIL} onChange={e => handleChangeCCSTicketsCreate('CALLER_EMAIL', e.target.value)} />
                        </FloatLabel>
                    </div>
                </div>
            </Card>

            <Card size="small" className="ticket-card w-100">
                <div className="row mt-3">
                    <div className="col-md-12">
                        <FloatLabel label="IPA" value={data.IPA}>
                            <Select
                            value={data.IPA}
                            onChange={(value) => handleChangeCCSTicketsCreate('IPA', value)}
                            className="w-100"
                            >
                            {ipaItems?.map((ipa: any) => (
                                    <Option key={ipa.code} value={ipa.code}>{ipa.code + ' | ' + ipa.description}</Option>
                            ))}
                            </Select>
                        </FloatLabel>
                    </div>
                    
                    <div className="col-md-12">
                        <FloatLabel label="Has Health Plan" value={data.HEALTH_PLAN}>
                            <Radio.Group
                            options={hasHpCodeOptions}
                            onChange={(e) => {
                                console.log(e);
                                handleChangeCCSTicketsCreate('HEALTH_PLAN', e.target.value)}
                            }
                            value={data.HEALTH_PLAN}
                            buttonStyle="solid"
                            optionType="button"
                            />
                        </FloatLabel>
                    </div>
                    {data.HEALTH_PLAN === true && (
                        <div className="col-md-12">
                            <FloatLabel label="Health Plan" value={data.HPCODE}>
                                <Select
                                value={data.HPCODE}
                                onChange={(value) => handleChangeCCSTicketsCreate('HPCODE', value)}
                                className="w-100"
                                >
                                {hpCodeItems?.map((hpCode: any) => (
                                        <Option key={hpCode.code} value={hpCode.code}>{hpCode.code + ' | ' + hpCode.description}</Option>
                                ))}
                                </Select>
                            </FloatLabel>
                        </div>   
                    )}                
                </div>
            </Card>

            <Card size="small" title="Customer Category" className="ticket-card w-100">
                <div className="col-md-12">
                    <FloatLabel label="Select a category" value={data.CUSTOMER_CATEGORY}>
                        <Select
                        value={data.CUSTOMER_CATEGORY}
                        onChange={(value) => handleChangeCCSTicketsCreate('CUSTOMER_CATEGORY', value)}
                        className="w-100"
                        >
                        {customerCategoryItems?.map((customerCategory: any) => (
                                <Option key={customerCategory.code} value={customerCategory.code}>{customerCategory.description}</Option>
                        ))}
                        </Select>
                    </FloatLabel>
                </div>
                {(data.CUSTOMER_CATEGORY === 'Member' || data.CUSTOMER_CATEGORY === '3rd Party') && (
                    <Card
                    size="small" title="Member Validation"
                    extra={
                        <Fragment>
                        <Link to="#" onClick={() => setIsMemberSearchOpen(true)}><SearchOutlined/>SEARCH</Link>
                        <a href="#" className="ml-3">SHOW</a>
                        </Fragment>
                    }
                    className="ticket-card w-100"
                    >
                        <div className="row">
                            <div className="col-md-12">
                                <FloatLabel label="" value={data.MBR_FIRSTNAME}>
                                    <Input value={data.MBR_FIRSTNAME} placeholder="First Name" disabled/>
                                </FloatLabel>
                            </div>
    
                            <div className="col-md-12">
                                <FloatLabel label="" value={data.MBR_LASTNAME}>
                                    <Input value={data.MBR_LASTNAME} placeholder="Last Name" disabled/>
                                </FloatLabel>
                            </div>

                            <div className="col-md-12">
                                <FloatLabel label="" value={data.MBR_ID}>
                                    <Input value={data.MBR_ID} placeholder="Member ID" disabled />
                                </FloatLabel>
                            </div>
    
                            <div className="col-md-12">
                                <FloatLabel label="" value={data.MBR_DOB}>
                                    <DatePicker
                                    format="MM/DD/YYYY" suffixIcon={<i className="far fa-calendar-alt" />} placeholder="Date of Birth"
                                    value={data.MBR_DOB && moment(data.MBR_DOB, 'MM/DD/YYYY')}
                                    disabled
                                    />
                                </FloatLabel>
                            </div>
                        </div>
                    </Card>
                )} 
                
                {(data.CUSTOMER_CATEGORY === 'Provider' || data.CUSTOMER_CATEGORY === '3rd Party') && (
                    <Card
                size="small" title="Provider Validation"
                extra={
                    <Fragment>
                    <Link to="#" onClick={() => setIsProviderSearchOpen(true)}><SearchOutlined/>SEARCH</Link>
                    <a href="#" className="ml-3">SHOW</a>
                    </Fragment>
                }
                className="ticket-card w-100"
                >
                    <div className="row">
                        <div className="col-md-12">
                            <FloatLabel label="" value={data.PROVIDER_ID}>
                                <Input value={data.PROVIDER_ID} placeholder="Provider ID" disabled />
                            </FloatLabel>
                        </div>

                        <div className="col-md-12">
                            <FloatLabel label="" value={data.PROVIDER_FIRSTNAME}>
                                <Input value={data.PROVIDER_FIRSTNAME} placeholder="First Name" disabled />
                            </FloatLabel>
                        </div>

                        <div className="col-md-12">
                            <FloatLabel label="" value={data.PROVIDER_LASTNAME}>
                                <Input value={data.PROVIDER_LASTNAME} placeholder="Last Name" disabled />
                            </FloatLabel>
                        </div>

                        <div className="col-md-12">
                            <FloatLabel label="" value={data.PROVIDER_TAX_ID}>
                                <Input value={data.PROVIDER_TAX_ID} placeholder="Tax ID" disabled />
                            </FloatLabel>
                        </div>

                        <div className="col-md-12">
                            <FloatLabel label="NPI 1" value={data.PROVIDER_NPI_1}>
                                <Input value={data.PROVIDER_NPI_1} onChange={e => handleChangeCCSTicketsCreate('PROVIDER_NPI_1', e.target.value)} />
                            </FloatLabel>
                        </div>

                        <div className="col-md-12">
                            <FloatLabel label="NPI 2" value={data.PROVIDER_NPI_2}>
                                <Input value={data.PROVIDER_NPI_2} onChange={e => handleChangeCCSTicketsCreate('PROVIDER_NPI_2', e.target.value)} />
                            </FloatLabel>
                        </div>

                        <div className="col-md-12">
                            <FloatLabel label="Provider MD License #" value={data.PROV_MD_LICENSE}>
                                <Input value={data.PROV_MD_LICENSE} onChange={e => handleChangeCCSTicketsCreate('PROV_MD_LICENSE', e.target.value)} />
                            </FloatLabel>
                        </div>
                    </div>
                </Card>
                )}
                
            </Card>

            <Card size="small" title="Call Category" className="ticket-card w-100">
                <div className="col-md-12">
                    <FloatLabel label="Select a category" value={data.CALL_CATEGORY}>
                        <Select
                        value={data.CALL_CATEGORY}
                        onChange={(value) => handleChangeCCSTicketsCreate('CALL_CATEGORY', value)}
                        className="w-100"
                        >
                        {callCategoryItems?.map((callCategory: any) => (
                                <Option key={callCategory.code} value={callCategory.code}>{callCategory.code + ' | ' + callCategory.description}</Option>
                        ))}
                        </Select>
                    </FloatLabel>
                </div>
                {(data.CALL_CATEGORY === 'CO' || data.CALL_CATEGORY === 'CU' 
                || data.CALL_CATEGORY === 'CA' || data.CALL_CATEGORY === 'CDDOFR'
                || data.CALL_CATEGORY === 'CDDUPS' || data.CALL_CATEGORY === 'I'
                || data.CALL_CATEGORY === 'A') && (
                    <div className="row">
                        <div className="col-md-12">
                            <FloatLabel label="Claim Number" value={data.CLAIMNO}>
                                <Input value={data.CLAIMNO} onChange={e => handleChangeCCSTicketsCreate('CLAIMNO', e.target.value)} />
                            </FloatLabel>
                        </div>
                    </div>
                )}
                {(data.CALL_CATEGORY === 'I' || data.CALL_CATEGORY === 'NIS') && (
                    <div className="row">
                        <div className="col-md-12">
                            <FloatLabel label="First Name" value={data.MBR_FIRSTNAME}>
                                <Input value={data.MBR_FIRSTNAME} onChange={e => handleChangeCCSTicketsCreate('MBR_FIRSTNAME', e.target.value)} />
                            </FloatLabel>
                        </div>
                        <div className="col-md-12">
                            <FloatLabel label="Last Name" value={data.MBR_LASTNAME}>
                                <Input value={data.MBR_LASTNAME} onChange={e => handleChangeCCSTicketsCreate('MBR_LASTNAME', e.target.value)} />
                            </FloatLabel>
                        </div>
                        <div className="col-md-12">
                            <FloatLabel label="Member ID" value={data.MBR_ID}>
                                <Input value={data.MBR_ID} onChange={e => handleChangeCCSTicketsCreate('MBR_ID', e.target.value)} />
                            </FloatLabel>
                        </div>
                        <div className="col-md-12">
                            <FloatLabel label="Date of Birth" value={data.MBR_DOB}>
                                <DatePicker
                                format="MM/DD/YYYY" suffixIcon={<i className="far fa-calendar-alt" />} placeholder=""
                                value={data.MBR_DOB && moment(data.MBR_DOB, 'MM/DD/YYYY')}
                                onChange={(date, dateString) => handleChangeCCSTicketsCreate('MBR_DOB', dateString)}
                                />
                            </FloatLabel>
                        </div>
                        <div className="col-md-12">
                            <FloatLabel label="Date of Service" value={data.CATEGORY_DOS}>
                                <DatePicker
                                format="MM/DD/YYYY" suffixIcon={<i className="far fa-calendar-alt" />} placeholder=""
                                value={data.CATEGORY_DOS && moment(data.CATEGORY_DOS, 'MM/DD/YYYY')}
                                onChange={(date, dateString) => handleChangeCCSTicketsCreate('CATEGORY_DOS', dateString)}
                                />
                            </FloatLabel>
                        </div>

                        <div className="col-md-12">
                            <FloatLabel label="ProcCode 1" value={data.PROC_CODE_1}>
                                <Input value={data.PROC_CODE_1} onChange={e => handleChangeCCSTicketsCreate('PROC_CODE_1', e.target.value)} />
                            </FloatLabel>
                        </div>

                        <div className="col-md-12">
                            <FloatLabel label="ProcCode 2" value={data.PROC_CODE_2}>
                                <Input value={data.PROC_CODE_2} onChange={e => handleChangeCCSTicketsCreate('PROC_CODE_2', e.target.value)} />
                            </FloatLabel>
                        </div>

                        <div className="col-md-12">
                            <FloatLabel label="ProcCode 3" value={data.PROC_CODE_3}>
                                <Input value={data.PROC_CODE_3} onChange={e => handleChangeCCSTicketsCreate('PROC_CODE_3', e.target.value)} />
                            </FloatLabel>
                        </div>

                        <div className="col-md-12">
                            <FloatLabel label="ProcCode 4" value={data.PROC_CODE_4}>
                                <Input value={data.PROC_CODE_4} onChange={e => handleChangeCCSTicketsCreate('PROC_CODE_4', e.target.value)} />
                            </FloatLabel>
                        </div>

                        <div className="col-md-12">
                            <FloatLabel label="ProcCode 5" value={data.PROC_CODE_5}>
                                <Input value={data.PROC_CODE_5} onChange={e => handleChangeCCSTicketsCreate('PROC_CODE_5', e.target.value)} />
                            </FloatLabel>
                        </div>
                    </div>  
                )}
                {(data.CALL_CATEGORY == 'EOB') && (
                    <div className="row">
                        <div className="col-md-12">
                            <FloatLabel label="Check Number" value={data.CHECKNO}>
                                <Input value={data.CHECKNO} onChange={e => handleChangeCCSTicketsCreate('CHECKNO', e.target.value)} />
                            </FloatLabel>
                        </div>
                    </div>
                )}
            </Card>

            <Card size="small" title="Routing" className="ticket-card w-100">
                <div className="col-md-12">
                    <FloatLabel label="Route To" value={data.ROUTE_TO}>
                        <Select
                            value={data.ROUTE_TO}
                            onChange={(value) => handleChangeCCSTicketsCreate('ROUTE_TO', value)}
                            className="w-100"
                            >
                            {routeToOptions.map(i => (
                                <Option value={i.value}>{i.label}</Option>
                            ))}
                        </Select>
                    </FloatLabel>
                </div>
            </Card>

            <Card size="small" title="Follow Up" className="ticket-card w-100">
                <div className="row">
                    <div className="col-md-12">
                        <FloatLabel label="Expected Time of Completion" value={data.DATE_EXPECTED_COMPLETE}>
                            <DatePicker
                            format="MM/DD/YYYY" suffixIcon={<i className="far fa-calendar-alt" />} placeholder=""
                            value={data.DATE_EXPECTED_COMPLETE && moment(data.DATE_EXPECTED_COMPLETE, 'MM/DD/YYYY')}
                            onChange={(date, dateString) => handleChangeCCSTicketsCreate('DATE_EXPECTED_COMPLETE', dateString)}
                            />
                        </FloatLabel>
                    </div>
                    <div className="col-md-12">
                        <FloatLabel label="Callback Date" value={data.CALLBACK_DATE}>
                            <DatePicker
                            format="MM/DD/YYYY" suffixIcon={<i className="far fa-calendar-alt" />} placeholder=""
                            value={data.CALLBACK_DATE && moment(data.CALLBACK_DATE, 'MM/DD/YYYY')}
                            onChange={(date, dateString) => handleChangeCCSTicketsCreate('CALLBACK_DATE', dateString)}
                            />
                        </FloatLabel>
                    </div>

                </div>
            </Card>
            <Card size="small" title="Notes" className="ticket-card w-100">
                <RichTextEditor
                    content={data.NOTES || ''}
                    handleContentChange={handleChangeRichEditorValue}
                    placeholder="insert text here..."
                />
            </Card>
        </div>

        <div className="row ticket-footer">
            <div className="col-24 d-flex justify-content-center">
                <Button type="primary" className="ml-3 waves-effect waves-light" onClick={onSubmit}>
                    SUBMIT
                </Button>
            </div>
        </div>
    </div>

  );
};



const mapStateToProps = (store: RootStateOrAny) => ({
    edited: store.rootReducer.ccsTickets.edited,
    data: store.rootReducer.ccsTickets.data,
    ticket_ref_no: store.rootReducer.ccsTickets.ticket_ref_no,
    customerCategoryItems: store.rootReducer.customerCategories.items,
    callCategoryItems: store.rootReducer.callCategories.items,
    hpCodeItems: store.rootReducer.hpCodes.items,
    ipaItems: store.rootReducer.ipas.items
});
const mapDispatchToProps = (dispatch: Dispatch) => bindActionCreators(
    {
      initCCSTickets,
      getCCSTickets,
      editCCSTickets,
      postCCSTickets,
      getTicketsNo,
      getListCustomerCategories,
      getListCallCategories,
      getListHPCodes,
      getListIpas,
      //getTicketsNo,
      //getListQueues,

    },
    dispatch,
);

export default connect(mapStateToProps, mapDispatchToProps)(CCSTicketsCreatePage);