import types from './types';

const INITIAL_DATA_MANAGEMENT_DATA = {
  id: '',
  deleted: '',
  createdDateUtc: '',
  modifiedDateUtc: '',
  pgoMasterId: '',
  subject: '',
  user: '',
  body: ''
};

const INITIAL_STATE = {
  isFetchingListDataManagements: false,
  isFetchingDataManagements: false,
  isPostingDataManagements: false,
  isPuttingDataManagements: false,

  data: {...INITIAL_DATA_MANAGEMENT_DATA},
  items: [],

  edited: false,
};

const pgoDataManagementsReducer = (state = INITIAL_STATE, action: any) => {
  switch (action.type) {
    // Init
    case types.PGO_DATA_MANAGEMENT_INIT:
      return {
        ...state,
        data: { ...INITIAL_DATA_MANAGEMENT_DATA },
        edited: false,
      };

    // Edit
    case types.PGO_DATA_MANAGEMENT_EDIT:
      return {
        ...state,
        data: { ...state.data, ...action.payload },
        edited: true,
      };

    // Get list
    case types.GET_LIST_PGO_DATA_MANAGEMENT_REQUEST:
      return {
        ...state,
        isFetchingListDataManagements: true,
      };
    case types.GET_LIST_PGO_DATA_MANAGEMENT_SUCCESS:
      return {
        ...state,
        isFetchingListDataManagements: false,
        items: action.payload,
      };
    case types.GET_LIST_PGO_DATA_MANAGEMENT_FAILURE:
      return {
        ...state,
        isFetchingListDataManagements: false,
        items: [],
      };

    // Get
    case types.GET_PGO_DATA_MANAGEMENT_REQUEST:
      return {
        ...state,
        isFetchingDataManagements: true,
      };
    case types.GET_PGO_DATA_MANAGEMENT_SUCCESS:
      return {
        ...state,
        isFetchingDataManagements: false,
        data: action.payload,
        edited: false,
      };
    case types.GET_PGO_DATA_MANAGEMENT_FAILURE:
      return {
        ...state,
        isFetchingDataManagements: false,
        data: { ...INITIAL_DATA_MANAGEMENT_DATA },
      };

    // Post
    case types.POST_PGO_DATA_MANAGEMENT_REQUEST:
      return {
        ...state,
        isPostingDataManagements: true,
      };
    case types.POST_PGO_DATA_MANAGEMENT_SUCCESS:
      return {
        ...state,
        isPostingDataManagements: false,
        edited: false,
      };
    case types.POST_PGO_DATA_MANAGEMENT_FAILURE:
      return {
        ...state,
        isPostingDataManagements: false,
      };

    // Put
    case types.PUT_PGO_DATA_MANAGEMENT_REQUEST:
      return {
        ...state,
        isPuttingDataManagements: true,
      };
    case types.PUT_PGO_DATA_MANAGEMENT_SUCCESS:
      return {
        ...state,
        isPuttingDataManagements: false,
        edited: false,
      };
    case types.PUT_PGO_DATA_MANAGEMENT_FAILURE:
      return {
        ...state,
        isPuttingDataManagements: false,
      };

    default:
      return state;
  }
};

export default pgoDataManagementsReducer;
