import React, { useState } from 'react';
import { bindActionCreators, Dispatch } from 'redux';
import { connect, RootStateOrAny } from 'react-redux';
import { useHistory } from "react-router-dom";

import { Button, Typography } from 'antd';

import './index.scss';
import { postPgoCareManagements } from 'store/pgoDetails/sections/careManagements/actions';
import { postPgoMasters } from 'store/pgoDetails/sections/masters/actions';
import { postPgoAttachments } from 'store/pgoDetails/sections/attachments/actions';
import { postPgoDataManagements } from 'store/pgoDetails/sections/dataManagements/actions';
import { postPgoEligibility } from 'store/pgoDetails/sections/eligibility/actions';
import { postPgoGridOperations } from 'store/pgoDetails/sections/gridOperations/actions';
import { postPgoTermingProviderGroups } from 'store/pgoDetails/sections/termingProviderGroups/actions';
import { postPgoReceivingProviderGroups } from 'store/pgoDetails/sections/receivingProviderGroups/actions';

const { Title } = Typography;

interface PgoNewPageProps {
  edited: boolean;
  postPgoCareManagements: Function;
  postPgoMasters: Function;
  postPgoAttachments: Function;
  postPgoDataManagements: Function;
  postPgoEligibility: Function;
  postPgoGridOperations: Function;
  postPgoTermingProviderGroups: Function;
  postPgoReceivingProviderGroups: Function;
}

const PgoNewPage = (
  {
    postPgoCareManagements,
    postPgoMasters,
    postPgoAttachments,
    postPgoDataManagements,
    postPgoEligibility,
    postPgoGridOperations,
    postPgoTermingProviderGroups,
    postPgoReceivingProviderGroups,
  }: PgoNewPageProps) => {
  const history = useHistory();
  const [drafts, setDrafts] = useState(JSON.parse(localStorage.getItem('pgoDrafts') || '[]'));

  const onSubmit = (draft: any) => {
    postPgoMasters(draft.mastersData);
    postPgoCareManagements(draft.careManagementsData);
    postPgoAttachments(draft.attachmentsData);
    postPgoDataManagements(draft.dataManagementsData);
    postPgoEligibility(draft.eligibilityData);
    postPgoGridOperations(draft.gridOperationsData);
    postPgoTermingProviderGroups(draft.termingProviderData);
    postPgoReceivingProviderGroups(draft.receivingProviderData);

    onRemove(draft);
  };

  const onRemove = (draft: any) => {
    let pgoDrafts = JSON.parse(localStorage.getItem('pgoDrafts') || '[]');
    pgoDrafts = pgoDrafts.filter((item: any) => (item.id !== draft.id));
    localStorage.setItem('pgoDrafts', JSON.stringify(pgoDrafts));
    setDrafts(pgoDrafts);
  };

  return (
    <div className="page pgo-new-page">
      <div className="header">
        <Title level={4}>Pgo Create Page</Title>

        <Button type="primary" className="ml-3 waves-effect waves-light" onClick={() => history.push('/pgo-details/create')}>
          NEW
        </Button>
      </div>

      <div className="content">
        <table className="w-100">
          <thead className="tbl-header">
            <tr>
              <th className="span-1">No</th>
              <th className="span-2">Content</th>
              <th className="span-3">Action</th>
            </tr>
          </thead>
          <tbody className="tbl-body">
          {drafts.map((draft: any, index: number) => (
            <tr key={index}>
              <td className="span-1">{index + 1}</td>
              <td className="span-2">{draft.id} ...content</td>
              <td className="span-3">
                <i className="fa fa-edit mr-3 icon" onClick={() => history.push(`/pgo-details/drafts/${draft.id}`)}/>
                <Button className="submit-btn" onClick={() => onSubmit(draft)}>Submit</Button>
                <i className="fa fa-trash-alt ml-3 icon" onClick={() => onRemove(draft)}/>
              </td>
            </tr>
          ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

const mapStateToProps = (store: RootStateOrAny) => ({
});

const mapDispatchToProps = (dispatch: Dispatch) => bindActionCreators(
  {
    postPgoCareManagements,
    postPgoMasters,
    postPgoAttachments,
    postPgoDataManagements,
    postPgoEligibility,
    postPgoGridOperations,
    postPgoTermingProviderGroups,
    postPgoReceivingProviderGroups,
  },
  dispatch,
);

export default connect(mapStateToProps, mapDispatchToProps)(PgoNewPage);
