import axios, { AxiosPromise } from 'axios';
import _get from 'lodash/get';

import constants from './index';
import { FAKE_API } from './fakeApi';

import { setApiAuthenticationHeader } from 'utils/setApiAuthenticationHeader';
import store from 'store';
import authTypes from 'store/auth/types';

const { API_SERVER, USE_FAKE_API, UMS_SERVER, API_SERVER_NEW, UMS_CLIENT_ID, UMS_CLIENT_SECRET } = constants;

// Configure axios instances
axios.defaults.baseURL = API_SERVER;
axios.defaults.headers.common['X-Requested-With'] = 'XMLHttpRequest';
axios.defaults.headers.post['Content-Type'] = 'application/json';
axios.defaults.headers.common['Accept'] = 'application/json';

const axiosErrorHandler = (error: any) => {
  if (
    error.config
    && error.response
    && error.response.status === 401
  ) {
    // access token expired, get new one
    const refresh_token = store.getState().rootReducer.auth.refresh_token;

    if (refresh_token) {
      return api.auth.getNewToken(refresh_token)
        .then(response => {
          const access_tokenNew = response?.access_token;
          const refresh_tokenNew = response?.refresh_token;

          if (access_tokenNew && refresh_tokenNew) {
            error.config.headers.common = {
              ...error.config.headers.common,
              authorization: `Bearer ${access_tokenNew}`,
            };
            error.config.headers.authorization = `Bearer ${access_tokenNew}`;
            setApiAuthenticationHeader(access_tokenNew);
            localStorage.access_token = access_tokenNew;
            localStorage.refresh_token = refresh_tokenNew;

            store.dispatch({
              type: authTypes.LOGIN_SUCCESS,
              payload: {
                access_token: access_tokenNew,
                refresh_token: refresh_tokenNew,
              },
            });

            return axiosInstance.request(error.config);
          } else {
            return Promise.reject(Error('Can not get access token'));
          }
        });
    }
  }

  return Promise.reject(error);
};

//====================================================================================
// Create main axios instance
const axiosInstance = axios.create();

// axios interceptors to handle token expiration
axiosInstance.interceptors.response.use(
  response => response,
  axiosErrorHandler,
);

//====================================================================================
// Create new API axios instance
const newAxiosInstanceConfig = {
  baseURL: API_SERVER_NEW,
  headers: {
    'Content-Type': 'application/json',
    'Accept': 'application/json',
    'X-Requested-With': 'XMLHttpRequest'
  }
};
const newAxiosInstance = axios.create(newAxiosInstanceConfig);

newAxiosInstance.interceptors.response.use(
  response => response,
  axiosErrorHandler,
);

//====================================================================================
//Create UMS axios instance, UMS server accepts form-urlencoded only, so we need to create separate instance here
const umsConfig = {
  baseURL: UMS_SERVER,
  headers: {
    'Content-Type': 'application/x-www-form-urlencoded',
    'Accept': 'application/json, text/plain, */*',
    'X-Requested-With': 'XMLHttpRequest',
  },
};
const umsAxios = axios.create(umsConfig);

//====================================================================================

// Axios wrapper to handle error
const axiosWrapper = (apiCall: AxiosPromise) => apiCall.then(res => res.data).catch(err => Promise.reject(err));

