import React from 'react';
import { bindActionCreators, Dispatch } from 'redux';
import { connect, RootStateOrAny } from 'react-redux';
import { Typography, Table} from 'antd';
import { useHistory } from 'react-router-dom';

import LoadingIndicator from 'components/Common/LoadingIndicator';
import AuthSearch from './AuthSearch';

import './index.scss';

interface AuthPageProps {
  searchResult: any;
  isFetchingList: boolean;
}

const { Title } = Typography;

const columns = [
  {
    title: 'Company Id',
    key: 'companyId',
    dataIndex: 'companyId',
  },
  {
    title: 'First Name',
    key: 'firstName',
    dataIndex: 'firstName',
  },
  {
    title: 'Last Name',
    key: 'lastName',
    dataIndex: 'lastName',
  },
  {
    title: 'Auth Id',
    key: 'authRefId',
    dataIndex: 'authRefId',
  },
  {
    title: 'Hp Code',
    key: 'hpCode',
    dataIndex: 'hpCode',
  },
  {
    title: 'Date of Birth',
    key: 'birth',
    dataIndex: 'birth',
  },
];

const AuthSearchPage = ({searchResult, isFetchingList}: AuthPageProps) => {
  const history = useHistory();

  return (
    <div className="page auth-search-page">
      <LoadingIndicator isLoading={isFetchingList} fullScreen />

      <div className="header-title">
        <Title level={4}>Search Auth</Title>
      </div>

      <div className="search-section">
        <AuthSearch/>
      </div>

      <div className="content">
        <Table
          columns={columns}
          pagination={{ position: ['bottomCenter'] }}
          dataSource={searchResult}
          //onChange={onChange}
          onRow={(record) => ({
            onClick: () => {
              history.push(`/authorization/${record.id}/edit`);
            },
          })}
        />
      </div>
    </div>
  );
};

const mapStateToProps = (store: RootStateOrAny) => ({
  searchResult: store.rootReducer.authorization.searchResult,
  isFetchingList: store.rootReducer.authorization.isFetchingList,
});

const mapDispatchToProps = (dispatch: Dispatch) => bindActionCreators(
  {
  },
  dispatch,
);

export default connect(mapStateToProps, mapDispatchToProps)(AuthSearchPage);
