import React, { Fragment, useState } from 'react';
import { bindActionCreators, Dispatch } from 'redux';
import { connect, RootStateOrAny } from 'react-redux';
import moment from 'moment';

import { DatePicker, Input, Button, Select } from 'antd';

import FloatLabel from 'components/Common/FloatLabel';
import { editPgoReceivingProviderGroups } from 'store/pgoDetails/sections/receivingProviderGroups/actions';

import ProviderSearch from 'components/Provider/ProviderSearch';

import { SearchOutlined } from '@ant-design/icons';
import notifier from 'utils/notifier';
import { useGetVendorsOfProviders } from '../../../getVendorsOfProviderHook';

const { Option } = Select;

interface PgoDetailsProps {
  data: any;
  editPgoReceivingProviderGroups: Function;
  handleGoToNextStep: Function;
}

const ReceivingProviderGroupsPage = ({ data, editPgoReceivingProviderGroups, handleGoToNextStep }: PgoDetailsProps) => {
  const [isProviderSearchOpen, setIsProviderSearchOpen] = useState(false);
  const [selectedProviderGuid, setSelectedProviderGuid] = useState(null);

  const { isFetchingVendors, vendorDetails } = useGetVendorsOfProviders(selectedProviderGuid);

  const handleChangeReceivingProviderGroups = (key: string, val: any) => {
    editPgoReceivingProviderGroups({ [key]: val });
  };

  const onNext = () => {
    if (data.details !== '' && data.licenseNumber !== '' && data.npi !== '' && data.providerClass !== '' && data.providerName !== '' && data.specialty !== '' && data.vendorName !== '') {
      handleGoToNextStep('4');
    } else {
      notifier.error('Please fill all input fields to continue !');
    }
  };

  const handleSelectProvider = (data: any) => {
    editPgoReceivingProviderGroups({
      'npi': data?.npi || '',
      'providerName': `${data?.firstName} ${data?.lastName}` || '',
      'providerId': data?.providerRefId || '',
      'vendorEffectDate': data?.vendorEffectDate || '',
      'vendorName': data?.vendorName || '',
      'vendorId': data?.vendorId || '',
      'licenseNumber': data?.licenseNo || '',
      'specialty': data?.specialty || '',
      'providerClass': data?.providerClass || '',
    });

    setSelectedProviderGuid(data?.id);

    setIsProviderSearchOpen(false);
  };

  return (
    <Fragment>
      {/*{items.map((data: any, index: number) => (*/}
      <div className="row receiving-provider-group">
        <ProviderSearch
          isModal
          isModalVisible={isProviderSearchOpen}
          onCloseModal={() => setIsProviderSearchOpen(false)}
          onProviderSelect={handleSelectProvider}
        />

        <div className="col-md-8">
          <FloatLabel label="NPI" value={data.npi} hasIcon>
            <Input
              prefix={<SearchOutlined onClick={() => { setIsProviderSearchOpen(true); }} />}
              value={data.npi} onChange={e => handleChangeReceivingProviderGroups('npi', e.target.value)}
            />
          </FloatLabel>
        </div>

        <div className="col-md-8">
          <FloatLabel label="Provider Name" value={data.providerName} hasIcon>
            <Input
              prefix={<SearchOutlined onClick={() => { setIsProviderSearchOpen(true); }} />}
              value={data.providerName}
              onChange={e => handleChangeReceivingProviderGroups('providerName', e.target.value)}
            />
          </FloatLabel>
        </div>

        <div className="col-md-8">
          <FloatLabel label="Provider ID" value={data.providerId} hasIcon>
            <Input
              prefix={<SearchOutlined onClick={() => { setIsProviderSearchOpen(true); }} />}
              value={data.providerId} onChange={e => handleChangeReceivingProviderGroups('providerId', e.target.value)}
            />
          </FloatLabel>
        </div>

        <div className="col-md-8">
          <FloatLabel label="Vendor Effect Date" value={data.vendorEffectDate}>
            <DatePicker
              format="MM/DD/YYYY" suffixIcon={<i className="far fa-calendar-alt" />} placeholder=""
              value={data.vendorEffectDate && moment(data.vendorEffectDate)}
              onChange={(date, dateString) => handleChangeReceivingProviderGroups('vendorEffectDate', dateString)}
            />
          </FloatLabel>
        </div>

        <div className="col-md-8">
          <FloatLabel label="Vendor ID" value={data.vendorId} hasIcon>
            <Select
              value={data.vendorId} style={{ width: '100%' }}
              onChange={v => handleChangeReceivingProviderGroups('vendorId', v)}
              loading={isFetchingVendors}
            >
              {!!vendorDetails?.length && vendorDetails.map((item: any, index: number) => (
                <Option value={item.id} key={`${item.id}_${index}`}>{item.id}</Option>
              ))}
            </Select>
          </FloatLabel>
        </div>

        <div className="col-md-8">
          <FloatLabel label="Vendor Name" value={data.vendorName} hasIcon>
            <Select
              value={data.vendorName} style={{ width: '100%' }}
              onChange={(v) => handleChangeReceivingProviderGroups('vendorName', v)}
              loading={isFetchingVendors}
            >
              {!!vendorDetails?.length && vendorDetails.map((item: any, index: number) => (
                <Option value={item.name} key={`${item.id}_${index}`}>{item.name}</Option>
              ))}
            </Select>
          </FloatLabel>
        </div>

        <div className="col-md-8">
          <FloatLabel label="License #" value={data.licenseNo} hasIcon>
            <Input
              prefix={<SearchOutlined onClick={() => { setIsProviderSearchOpen(true); }} />}
              value={data.licenseNo} onChange={e => handleChangeReceivingProviderGroups('licenseNo', e.target.value)}
            />
          </FloatLabel>
        </div>

        <div className="col-md-8">
          <FloatLabel label="Speciality" value={data.specialty}>
            <Input value={data.specialty}
                   onChange={e => handleChangeReceivingProviderGroups('specialty', e.target.value)} />
          </FloatLabel>
        </div>

        <div className="col-md-8">
          <FloatLabel label="Provider Class" value={data.providerClass}>
            <Input value={data.providerClass}
                   onChange={e => handleChangeReceivingProviderGroups('providerClass', e.target.value)} />
          </FloatLabel>
        </div>

        <div className="col-md-24">
          <FloatLabel label="Details" value={data.details}>
            <Input.TextArea value={data.details} rows={2}
                            onChange={e => handleChangeReceivingProviderGroups('details', e.target.value)} />
          </FloatLabel>
        </div>

        <div className="col d-flex">
          <Button className="ml-auto waves-effect waves-light" onClick={() => {}}>
            Remove
          </Button>
        </div>
      </div>
      {/*))}*/}

      <div className="row">
        <div className="col d-flex">
          <Button className="ml-3 waves-effect waves-light">New</Button>

          <Button className="ml-auto waves-effect waves-light" onClick={() => handleGoToNextStep('2')}>
            Previous
          </Button>
          <Button type="primary" className="ml-3 waves-effect waves-light" onClick={onNext}>
            Next
          </Button>
        </div>
      </div>
    </Fragment>
  );
};

const mapStateToProps = (store: RootStateOrAny) => ({
  data: store.rootReducer.pgoReceivingProviderGroups.data,
});

const mapDispatchToProps = (dispatch: Dispatch) => bindActionCreators(
  {
    editPgoReceivingProviderGroups,
  },
  dispatch,
);

export default connect(mapStateToProps, mapDispatchToProps)(ReceivingProviderGroupsPage);
