import types from './types';

const INITIAL_ELIGIBILITY_DATA = {
  id: '',
  deleted: '',
  createdDateUtc: '',
  modifiedDateUtc: '',
  pgoMasterId: '',
  subject: '',
  user: '',
  body: ''
};

const INITIAL_STATE = {
  isFetchingListEligibility: false,
  isFetchingEligibility: false,
  isPostingEligibility: false,
  isPuttingEligibility: false,

  data: {...INITIAL_ELIGIBILITY_DATA},
  items: [],

  edited: false,
};

const pgoEligibilityReducer = (state = INITIAL_STATE, action: any) => {
  switch (action.type) {
    // Init
    case types.PGO_ELIGIBILITY_INIT:
      return {
        ...state,
        data: { ...INITIAL_ELIGIBILITY_DATA },
        edited: false,
      };

    // Edit
    case types.PGO_ELIGIBILITY_EDIT:
      return {
        ...state,
        data: { ...state.data, ...action.payload },
        edited: true,
      };

    // Get list
    case types.GET_LIST_PGO_ELIGIBILITY_REQUEST:
      return {
        ...state,
        isFetchingListEligibility: true,
      };
    case types.GET_LIST_PGO_ELIGIBILITY_SUCCESS:
      return {
        ...state,
        isFetchingListEligibility: false,
        items: action.payload,
      };
    case types.GET_LIST_PGO_ELIGIBILITY_FAILURE:
      return {
        ...state,
        isFetchingListEligibility: false,
        items: [],
      };

    // Get
    case types.GET_PGO_ELIGIBILITY_REQUEST:
      return {
        ...state,
        isFetchingEligibility: true,
      };
    case types.GET_PGO_ELIGIBILITY_SUCCESS:
      return {
        ...state,
        isFetchingEligibility: false,
        data: action.payload,
        edited: false,
      };
    case types.GET_PGO_ELIGIBILITY_FAILURE:
      return {
        ...state,
        isFetchingEligibility: false,
        data: { ...INITIAL_ELIGIBILITY_DATA },
      };

    // Post
    case types.POST_PGO_ELIGIBILITY_REQUEST:
      return {
        ...state,
        isPostingEligibility: true,
      };
    case types.POST_PGO_ELIGIBILITY_SUCCESS:
      return {
        ...state,
        isPostingEligibility: false,
        edited: false,
      };
    case types.POST_PGO_ELIGIBILITY_FAILURE:
      return {
        ...state,
        isPostingEligibility: false,
      };

    // Put
    case types.PUT_PGO_ELIGIBILITY_REQUEST:
      return {
        ...state,
        isPuttingEligibility: true,
      };
    case types.PUT_PGO_ELIGIBILITY_SUCCESS:
      return {
        ...state,
        isPuttingEligibility: false,
        edited: false,
      };
    case types.PUT_PGO_ELIGIBILITY_FAILURE:
      return {
        ...state,
        isPuttingEligibility: false,
      };

    default:
      return state;
  }
};

export default pgoEligibilityReducer;
