import types from './types';
import api, { axiosInstance } from 'constants/api';
import _get from 'lodash/get';
import constants from 'constants/index';

import setApiAuthHeader, { setApiAuthenticationHeader } from 'utils/setApiAuthenticationHeader';
import notifier from 'utils/notifier';
import { ThunkDispatch } from 'redux-thunk';

const { UMS_CLIENT_ID, UMS_CLIENT_SECRET, UMS_SERVER } = constants;

export const login = (credentials: any) => (dispatch: ThunkDispatch<any, any, any>, getState: () => any) => {
  if (getState().rootReducer.auth.isPostingLogin) { return Promise.reject(); }

  dispatch({ type: types.LOGIN_REQUEST });

  const params = new URLSearchParams();
  params.append('grant_type', 'password');
  params.append('username', credentials.username);
  params.append('password', credentials.password);
  params.append('scope', 'api1 offline_access');
  params.append('client_id', UMS_CLIENT_ID);
  params.append('client_secret', UMS_CLIENT_SECRET);

  return api.auth.login(params)
    .then((res: any) => {
      const msg = _get(res, 'msg', 'Login success!');
      let payload = { ...res, msg };

      if (!payload.access_token || !payload.refresh_token) {
        return Promise.reject(payload);
      } else {
        localStorage.access_token = payload.access_token;
        localStorage.refresh_token = payload.refresh_token;
        setApiAuthHeader(payload.access_token);

        dispatch({
          type: types.LOGIN_SUCCESS,
          payload,
        });

        return payload;
      }
    })
    .catch((err: Error) => {
      const msg = _get(err, 'response.data.msg', 'Login failed!');
      const errors = _get(err, 'response.data.errors', {});
      const payload = { ...err, msg, errors };

      dispatch({
        type: types.LOGIN_FAILURE,
        payload,
      });

      return Promise.reject(payload);
    });
};

export const loginWithStoredToken = () => (dispatch: ThunkDispatch<any, any, any>) => {
  if (localStorage.access_token && localStorage.refresh_token) {
    setApiAuthHeader(localStorage.access_token);

    dispatch({
      type: types.UPDATE_TOKEN_IN_STORE,
      payload: {
        access_token: localStorage.access_token,
        refresh_token: localStorage.refresh_token,
      },
    });

    dispatch(checkMySelf());
  } else {
    localStorage.clear();
    dispatch({ type: types.CHECK_ME_FAILURE });
  }
};

export const checkMySelf = () => (dispatch: ThunkDispatch<any, any, any>, getState: any) => {
  if (getState().rootReducer.auth.isCheckingMe) { return Promise.reject(); }

  dispatch({ type: types.CHECK_ME_REQUEST });

  return api.auth.check()
    .then((res: any) => {
      if (res?.status === 'unauthenticated') {
        const refresh_token = getState().rootReducer.auth.refresh_token;

        if (refresh_token) {
          return api.auth.getNewToken(refresh_token)
            .then(response => {
              const access_tokenNew = response?.access_token;
              const refresh_tokenNew = response?.refresh_token;

              if (access_tokenNew && refresh_tokenNew) {
                setApiAuthenticationHeader(access_tokenNew);
                localStorage.access_token = access_tokenNew;
                localStorage.refresh_token = refresh_tokenNew;

                dispatch({
                  type: types.LOGIN_SUCCESS,
                  payload: {
                    access_token: access_tokenNew,
                    refresh_token: refresh_tokenNew,
                  },
                });

                window.location.reload();

                return Promise.resolve();
              } else {
                return Promise.reject(Error('Can not get access token'));
              }
            });
        }

        return Promise.reject(Error('Unauthenticated'));
      }

      const admin = res.user.ROLES.includes(2);
      const manager = res.user.ROLES.includes(4);
      const payload = { ...res, is_admin: admin, is_manager: manager };

      dispatch({
        type: types.CHECK_ME_SUCCESS,
        payload,
      });

      return res;
    })
    .catch((err: Error) => {
      const msg = _get(err, 'response.data.msg', 'Check failed!');
      const errors = _get(err, 'response.data.errors', {});
      const payload = { ...err, msg, errors };

      dispatch({
        type: types.CHECK_ME_FAILURE,
        payload,
      });

      return Promise.reject(payload);
    });
};

export const getMySelf = () => (dispatch: ThunkDispatch<any, any, any>, getState: any) => {
  if (getState().rootReducer.auth.isFetchingMe) { return Promise.reject(); }

  dispatch({ type: types.GET_ME_REQUEST });

  return api.auth.getMySelf()
    .then((res: any) => {
      const admin = res.user.ROLES.includes(2);
      const manager = res.user.ROLES.includes(4);
      const payload = { ...res, is_admin: admin, is_manager: manager };

      dispatch({
        type: types.GET_ME_SUCCESS,
        payload,
      });

      return res;
    })
    .catch((err: Error) => {
      const msg = _get(err, 'response.data.msg', 'Get failed!');
      const errors = _get(err, 'response.data.errors', {});
      const payload = { ...err, msg, errors };

      dispatch({
        type: types.GET_ME_FAILURE,
        payload,
      });

      return Promise.reject(payload);
    });
};

export const logout = () => (dispatch: ThunkDispatch<any, any, any>) => {
  localStorage.clear();
  setApiAuthHeader();

  dispatch({
    type: types.LOGOUT_SUCCESS,
    payload: {
      msg: 'Logout success!',
    },
  });

  notifier.success('You have been logged out.');

  window.location.href = '/';
};
