import React, { PureComponent } from 'react';
import { Link } from 'react-router-dom';
import {
  Layout,
  Menu,
} from 'antd';

import './GuestLayout.scss';
import imgLogo from 'assets/images/logo.png';

const { Header, Content, Footer } = Layout;

interface GuestLayoutProp {
  location?: any,
  history?: any,
}

class GuestLayout extends PureComponent<GuestLayoutProp> {
  gotoUrl = (url: string) => {
    this.props.history.push(url);
  };

  render() {
    const { children } = this.props;

    return (
      <Layout className="guest-layout">
        <Header className="header">
          <div className="logo">
            <Link to="/">
              <img src={imgLogo} alt="NMM" />
            </Link>
          </div>

          <Menu theme="dark" mode="horizontal" selectedKeys={[]}>
            <Menu.Item key="1" onClick={() => this.gotoUrl('/login')}>Login</Menu.Item>
          </Menu>
        </Header>

        <Content className="site-layout-content">
          {children}
        </Content>

        <Footer className="site-layout-footer">Multi Services Queue, NMM</Footer>
      </Layout>
    );
  }
}

export default GuestLayout;
