import types from './types';

const INITIAL_DATA = {
  "CALLER_CODE": "",
  "CC_ID": 0
};

const INITIAL_STATE = {
  isFetchingListCallerCodes: false,

  data: {...INITIAL_DATA},
  items: [],
};

const callerCodesReducer = (state = INITIAL_STATE, action: any) => {
  switch (action.type) {
    // Get list
    case types.GET_LIST_CALLER_CODE_REQUEST:
      return {
        ...state,
        isFetchingListCallerCodes: true,
      };
    case types.GET_LIST_CALLER_CODE_SUCCESS:
      return {
        ...state,
        isFetchingListCallerCodes: false,
        items: action.payload,
      };
    case types.GET_LIST_CALLER_CODE_FAILURE:
      return {
        ...state,
        isFetchingListCallerCodes: false,
        items: [],
      };

    default:
      return state;
  }
};

export default callerCodesReducer;
