import React, { Fragment } from 'react';
import { bindActionCreators, Dispatch } from 'redux';
import { connect, RootStateOrAny } from 'react-redux';
import moment from 'moment';

import { DatePicker, Input, Button } from 'antd';

import FloatLabel from 'components/Common/FloatLabel';
import { editPgoGridOperations } from 'store/pgoDetails/sections/gridOperations/actions';
import notifier from 'utils/notifier';

interface PgoDetailsProps {
  data: any;
  editPgoGridOperations: Function;
  handleGoToNextStep: Function;
}

const GridOperationsPage = ({ data, editPgoGridOperations, handleGoToNextStep }: PgoDetailsProps) => {

  const handleChangeGridOperations = (key: string, val: any) => {
    editPgoGridOperations({ [key]: val });
  };

  const onNext = () => {
    if (data.body !== '' && data.subject !== '' && data.user !== '') {
      handleGoToNextStep('8');
    } else {
      notifier.error('Please fill all input fields to continue !');
    }
  };

  return (
    <Fragment>
      <div className="row">
        <div className="col-md-8">
          <FloatLabel label="Subject" value={data.subject}>
            <Input value={data.subject} onChange={e => handleChangeGridOperations('subject', e.target.value)} />
          </FloatLabel>
        </div>
        <div className="col-md-8">
          <FloatLabel label="User" value={data.user}>
            <Input value={data.user} onChange={e => handleChangeGridOperations('user', e.target.value)} />
          </FloatLabel>
        </div>

        <div className="col-md-8">
          <FloatLabel label="Date/Time" value={data.createdDateUtc}>
            <DatePicker
              format="MM/DD/YYYY" suffixIcon={<i className="far fa-calendar-alt" />} placeholder=""
              value={data.createdDateUtc && moment(data.createdDateUtc)}
              onChange={(date, dateString) => handleChangeGridOperations('createdDateUtc', dateString)}
            />
          </FloatLabel>
        </div>

        <div className="col-md-24">
          <FloatLabel label="Notes" value={data.body}>
            <Input.TextArea value={data.body} rows={3} onChange={e => handleChangeGridOperations('body', e.target.value)} />
          </FloatLabel>
        </div>
      </div>

      <div className="row">
        <div className="col d-flex">
          <Button className="ml-auto waves-effect waves-light" onClick={() => handleGoToNextStep('6')}>Previous</Button>
          <Button type="primary" className="ml-3 waves-effect waves-light" onClick={onNext}>
            Next
          </Button>
        </div>
      </div>
    </Fragment>
  );
};

const mapStateToProps = (store: RootStateOrAny) => ({
  data: store.rootReducer.pgoGridOperations.data,
});

const mapDispatchToProps = (dispatch: Dispatch) => bindActionCreators(
  {
    editPgoGridOperations,
  },
  dispatch,
);

export default connect(mapStateToProps, mapDispatchToProps)(GridOperationsPage);
