import { axiosInstance, newAxiosInstance } from 'constants/api';

const setApiAuthenticationHeader = (access_token: string | null = null) => {
  if (access_token) {
    axiosInstance.defaults.headers.common.authorization = `Bearer ${access_token}`;
    newAxiosInstance.defaults.headers.common.authorization = `Bearer ${access_token}`;
  } else {
    delete axiosInstance.defaults.headers.common.authorization;
    delete newAxiosInstance.defaults.headers.common.authorization;
  }
};

export { setApiAuthenticationHeader };

export default setApiAuthenticationHeader;
